import { useState } from "react";
import dateToString from "../../../Utils/DateUtils";
/**
 * @author Arthur Rimaudière
 * @function ResultatAnalyse
 * @description Affichage de la boite d'un résultat json du GITLAB
 * @returns Component
 * @version 1.0.0
 * @example <ResultatAnalyse result={object} index={15} forceOpen={false} forcedName="Ce composant aura ce nom et pas un autre" />
 * @param {Object} resultat - Objet contenant: la date, le resultat et le json de la réponse d'un push GITLAB
 * @param {Number} index - l'index du résultat dans le tableau de résultats
 * @param {Boolean} forceOpen - Permet de forcer le composant à être rendu comme "ouvert" au premier render de react
 * @param {String} forcedName - Permet de forcer le changement de nom d'un résultat json.
 */
export default function ResultatAnalyse({resultat, index, forceOpen, forcedName}){
    let {result, date, content} = resultat;
    const [openState, setOpenState] = useState(false);
    
    if(forceOpen && forceOpen === true && openState === false){
        setOpenState(true);
    }

    content = convertStringToCode(content);

    let nameClose = (result.charAt(0).toUpperCase() + result.slice(1))  + " - " + dateToString(new Date(date));
    let nameOpen = (result.charAt(0).toUpperCase() + result.slice(1))  + " - " + dateToString(new Date(date));
    if(forcedName && forcedName !== ""){
        nameClose = forcedName;
        nameOpen = forcedName;
    }

    return openState === true ? (
        <>
            <div style={{
                color: 'black',
                backgroundColor: 'white',
                borderBottom: '1px solid black',
                padding: '10px 20px 10px 20px',
                fontSize: window.innerWidth < 1200 ? '15px' : '20px',
                width: window.innerWidth < 1400 ? (window.innerWidth-50) + 'px' : '1400px',
                marginLeft: 'auto',
                marginRight: 'auto',
                overflow: '',
            }}>
                <p onClick={() => {
                    if(forceOpen && forceOpen === true){ return; }
                    setOpenState(false);
                }} style={{cursor: ((forceOpen && forceOpen === true) ? 'auto' : 'pointer'), fontSize: window.innerWidth < 800 ? '15px' : '20px'}}>
                    <b>⌄ Analyse n°{index+1}</b> - {nameOpen}
                </p>
                <div style={{width: '94%', 
                    marginLeft: '3%',
                    backgroundColor: 'rgb(67, 69, 73)',
                    color: 'white',
                    borderRadius: '5px',
                    fontSize: window.innerWidth < 800 ? '12px' : '20px'
                }}>
                    <pre style={{padding: '20px 20px 20px 20px', overflowX: 'scroll'}}>
                        <code style={{ whiteSpace: ''}}>
                            {content}
                        </code>
                    </pre>
                </div>
            </div>
        </>
    ) : (
        <div style={{
            color: 'black',
            backgroundColor: 'white',
            borderBottom: '1px solid black',
            padding: '10px 20px 10px 20px',
            fontSize: window.innerWidth < 800 ? '15px' : '20px',
            cursor: 'pointer', 
            width: '1400px',
            marginLeft: 'auto',
            marginRight: 'auto'
        }} onClick={() => {
            setOpenState(true);
            if(forceOpen && forceOpen === true){ return; }
        }}>
            <b>› Analyse n°{index+1}</b> - {nameClose}
        </div>
    )
}

let convertStringToCode = (phrase) => {
    phrase = phrase.replaceAll(",", ",\n");
    phrase = phrase.replaceAll("{", "\n{\n");
    phrase = phrase.replaceAll("}", "\n}");
    let phraseFinale = "";
    let nbtab = 0;
    for(let i = 0; i < phrase.length; i++) {
        let char = phrase.charAt(i);
        phraseFinale += char;
        if(char === "{"){
            nbtab++;
        } 
        if(char === "}"){
            nbtab--;
        }
        if(char === "\n"){
            phraseFinale += getTab(nbtab);
        }

    }
    let finall = "";
    for(let i = 0; i < phraseFinale.length - 1; i++) {
        let char = phraseFinale.charAt(i);
        let char2 = phraseFinale.charAt(i + 1);
        if(!(char === "\t" && char2 === "}")){
            finall += char;
        }
        if(i > 0 && char === "," && phraseFinale.charAt(i-1) === "}"){
            finall += "\n";
        }
    }
    return finall + phraseFinale.charAt(phraseFinale.length-1);
}

function getTab(i){
    let tab = "";
    for(let j = 0; j < i; j++){
        tab += "\t";
    }
    return tab;
}