import ListEventBox from "../../components/ListEventBox";
import PageEspacePerso from "../PageEspacePersoType";

import bannerPau from "../../assets/photoFont1.jpg";
import SearchBar from "../../components/SearchBar";
import ButtonGreen from "../../components/ButtonGreen";
import { useState } from "react";
import { useAuthContext } from "../../Contexts/AuthContext";
import { Navigate } from "react-router-dom";
import { visitUrl } from "../../Contexts/ReturnManager";

export default function EventsHome(){

    const [redirection, setRedirection] = useState("");
    const [filtre, changeFiltre] = useState("");
    const [update, setUpdate] = useState(0);
    
    let {user} = useAuthContext();
    let userStorage = localStorage.getItem('user');
    if(userStorage){ 
        user = JSON.parse(userStorage); 
    }
    let hasPermission = false;
    if(user){
        if(user.role === "administrateur"){
            hasPermission = true;
        }
    }
    let doesNotHavePermission = !hasPermission;
    visitUrl(window.location.pathname);
    return (
        <>
            {
                doesNotHavePermission && (
                <Navigate to="/" replace/>
                )
            }
            {
                hasPermission && (redirection !== "") && (
                    <Navigate to={redirection} replace/>
                )
            }
            <PageEspacePerso loading={false}>

                <img src={bannerPau} style={{width: '100%', marginTop: '-250px'}} alt=""/>
                <h1 style={{fontFamily: 'Light', color: 'white', position: 'absolute',top: '80px', fontSize: '55px', textAlign: 'center', width: '96%', textShadow: '0 1px 0 black, 0 -1px 0 black'}}>Gestion des évènements</h1>
                <ul style={{display: 'flex', flexDirection: 'row', listStyleType: 'none', marginLeft: '8%'}}>
                    <li>
                        <SearchBar changeFiltre={changeFiltre} message="Rechercher un événement"/>
                    </li>
                    <li style={{marginLeft: '20px'}}>
                        <ButtonGreen toRun={(e) => {
                            e.preventDefault();
                            setRedirection("/events/create");
                        }}>
                            CRÉER
                        </ButtonGreen>
                    </li>
                </ul>
                <ListEventBox filtre={filtre} pageAdmin={true} update={update} setUpdate={setUpdate}/>
            </PageEspacePerso>
        </>
    )
}