import PageEspacePerso from "../PageEspacePersoType";

import imagePyrenees from "../../assets/pyrenees.jpg";
import "./Dashboard.css";
import DashboardBox from "../../components/DashboardBox";
import { useAuthContext } from "../../Contexts/AuthContext";
import '../../style/App.css';
import '../../fonts/Inter-Light.ttf'
import { useState } from "react";
import { Navigate } from "react-router-dom";
import { visitUrl } from "../../Contexts/ReturnManager";

export default function Dashboard(){

    const {user} = useAuthContext();
    const [redirection, setRedirection] = useState("");

    visitUrl(window.location.pathname);

    return (
        <>
            {
                user && (user.role === "gestionnaireExterne" || user.role === "gestionnaireIA") && (
                    <Navigate to="/projects" replace={true}/>
                )
            }
            {
                redirection !== "" && (
                    <Navigate to={redirection} replace />
                )
            }
            <PageEspacePerso>
                <img src={imagePyrenees} alt="FontPicturePyrennees" style={{width: '100%'}}/>
                
                <ul className="DashboardListeChoix">
                    {
                        user && user.role === "administrateur" && (
                            <>
                                <li className="largeurDashboardItem" onClick={(e) => {
                                    e.preventDefault();
                                    setRedirection("/events");
                                }}>
                                    <DashboardBox img={require("../../assets/un-evenement.png")} alt="image" title="GESTION DES ÉVÈNEMENTS" lore="Pour créer/ modifier/ supprimer les évènements "/>
                                </li>
                                <li className="largeurDashboardItem" onClick={(e) => {
                                        e.preventDefault();
                                        setRedirection("/projects");
                                    }}>
                                    <DashboardBox img={require("../../assets/coordinateur.png")} alt="image" title="GESTION DES PROJETS" lore="Pour créer/ modifier/ supprimer les projets "/>
                                </li>
                                <li className="largeurDashboardItem" onClick={(e) => {
                                        e.preventDefault();
                                        setRedirection("/users/config");
                                }}>
                                    <DashboardBox img={require("../../assets/employe.png")} alt="image" title="GESTION DES UTILISATEURS" lore="Pour créer/ modifier/ supprimer des utilisateurs" />
                                </li>
                            </>
                        )
                    }
                    {
                        user && user.role === "etudiant" && (
                            <>
                                <li className="largeurDashboardItem" onClick={(e) => {
                                    e.preventDefault();
                                    setRedirection("/myevents");
                                }}>
                                    <DashboardBox img={require("../../assets/un-evenement.png")} alt="image" title="MES ÉVÈNEMENTS" lore="Pour consulter la liste des évènements auxquels vous êtes inscrits. "/>
                                </li>
                                <li className="largeurDashboardItem" onClick={(e) => {
                                        e.preventDefault();
                                        setRedirection("/myteams");
                                    }}>
                                    <DashboardBox img={require("../../assets/partners.png")} alt="image" title="MES ÉQUIPES" lore="Pour voir la liste des équipes dans lesquelles vous êtes"/>
                                </li>
                            </>
                        )
                    }
                </ul>
                
            </PageEspacePerso>
        </>
    )
}