
import { useState } from "react";
import "./header.css";

import '../../style/App.css';
import '../../fonts/Inter-Light.ttf'
import { useAuthContext } from "../../Contexts/AuthContext";

import '../../style/App.css';
import '../../fonts/Inter-Light.ttf'

import profilPicture from "../../assets/profil0.png"
import homePicture from "../../assets/home.png"
import logoutPicture from "../../assets/logout.png";
import eventsPicture from "../../assets/myevents.png";
import teamPicture from "../../assets/team.png";
import dashboardPicture from "../../assets/tableau-de-bord.png";
import projectPicture from "../../assets/lancement-du-projet.png";
import teamIcone from "../../assets/teamIcone.png";
import {Navigate, useLocation} from "react-router-dom";


/**
 * @name HeaderDashboard
 * @description The header dashboard is what you see when you're in your environment
 * @returns The header component for dashboard
 */
function HeaderDashboard({bypassConnexion}) {

    const [open, setOpen] = useState(false);
    const [redirection, setRedirection] = useState("");

    const location = useLocation();
    if(redirection !== "" && location.pathname === redirection){
        setRedirection("");
    }

    let {user, logout, updateTimeConnexion} = useAuthContext();
    if(user){
        updateTimeConnexion();
    }
    let userStorage = localStorage.getItem('user');
    if(userStorage){ 
        user = JSON.parse(userStorage); 
    }

    let navclass = (open) ? "navPerso2" : "navPerso";

    return (
        <>
            {
                redirection && redirection !== "" && (
                    <Navigate to={redirection} replace />
                )
            }
            <nav 
                onMouseLeave={(e) => {
                    setOpen(false)
                    e.stopPropagation();}
                } 
                onMouseEnter={(e) => {
                    setOpen(window.innerWidth > 800)
                    e.stopPropagation();}
                } 
            className={navclass}>
                <ul className="navPersoListe" >
                    {
                        open && (
                            <>
                                <li>
                                    <ul style={{display: 'flex', flexDirection: 'row', listStyleType: 'none', padding: '0', position: 'static', top: '0px', height: '120px'}}>
                                        <li style={{width: '80px', position: 'relative', top: '-25px', borderBottom: '2px solid #17c2b1'}}>
                                            <span style={{height: '60px', width: '60px', marginLeft: '9px', marginTop: '20px', borderRadius: '50%', backgroundColor: 'rgb(212, 217, 217)', border: '2px solid black', display: 'inline-block', cursor: 'pointer'}}>
                                            <img src={profilPicture} alt="profil" style={{marginTop: '8px', marginLeft: '8px', width: '44px', height: '44px'}}/>
                                    </span>  
                                        </li>
                                        <li style={{width: `calc(100% - 80px)`, position: 'relative', top: '-25px', marginBottom: '0px', paddingBottom: '0px', marginTop: '0px', paddingTop: '0px', borderBottom: '2px solid #17c2b1', fontFamily: 'Light', color: 'white', paddingLeft: '20px'}}>
                                            <p style={{fontSize: '20px'}}><b>{user.prenom} {user.nom}</b></p>
                                            <p>{user.role[0].toUpperCase() + user.role.slice(1)}</p>
                                        </li>
                                    </ul>
                                </li>
                                <li style={{paddingBottom: '10px', cursor: 'pointer', display: 'flex', flexDirection: 'row', color: 'white', fontFamily: 'Light', fontSize: '20px'}} onClick={(e) => {
                                    e.preventDefault();
                                    setRedirection("/");
                                }}>
                                    <img src={homePicture} alt="home" style={{marginTop: '25px', marginLeft: '25px', width: '30px', height: '30px'}}/>
                                    <div style={{paddingLeft: '15px', marginTop: '28px'}}>Accueil</div>
                                </li>
                                {
                                    (user.role === "administrateur") && (
                                        <li style={{paddingBottom: '10px', cursor: 'pointer', display: 'flex', flexDirection: 'row', color: 'white', fontFamily: 'Light', fontSize: '20px'}} onClick={(e) => {
                                            e.preventDefault();
                                            setRedirection("/dashboard");
                                        }}>
                                            <img src={dashboardPicture} alt="home" style={{marginTop: '25px', marginLeft: '25px', width: '30px', height: '30px'}}/>
                                            <div style={{paddingLeft: '15px', marginTop: '28px', width: '100%', overflow: 'hidden'}}>Tableau de bord</div>
                                        </li>
                                    )
                                }
                                {
                                    (user.role === "administrateur") && (
                                        <>
                                            <li style={{paddingBottom: '10px', cursor: 'pointer', display: 'flex', flexDirection: 'row', color: 'white', fontFamily: 'Light', fontSize: '20px'}} onClick={(e) => {
                                                e.preventDefault();
                                                setRedirection("/events");
                                            }}>
                                                <img src={eventsPicture} alt="home" style={{marginTop: '25px', marginLeft: '25px', width: '30px', height: '30px'}}/>
                                                <div style={{paddingLeft: '15px', marginTop: '28px'}}>Gérer les évènements</div>
                                            </li>
                                            <li style={{paddingBottom: '10px', cursor: 'pointer', display: 'flex', flexDirection: 'row', color: 'white', fontFamily: 'Light', fontSize: '20px'}} onClick={(e) => {
                                                e.preventDefault();
                                                setRedirection("/projects");
                                            }}>
                                                <img src={projectPicture} alt="équipes" style={{marginTop: '25px', marginLeft: '25px', width: '30px', height: '30px'}}/>
                                                <div style={{paddingLeft: '15px', marginTop: '28px'}}>Gérer les projets</div>
                                            </li>
                                            <li style={{paddingBottom: '10px', cursor: 'pointer', display: 'flex', flexDirection: 'row', color: 'white', fontFamily: 'Light', fontSize: '20px'}} onClick={(e) => {
                                                e.preventDefault();
                                                setRedirection("/users/config");
                                            }}>
                                                <img src={teamPicture} alt="équipes" style={{marginTop: '25px', marginLeft: '25px', width: '30px', height: '30px'}}/>
                                                <div style={{paddingLeft: '15px', marginTop: '28px'}}>Gérer les utilisateurs</div>
                                            </li>
                                            
                                        </>
                                    )
                                }
                                {
                                    (user.role === "gestionnaireExterne" || user.role === "gestionnaireIA") && (
                                        <>
                                            <li style={{paddingBottom: '10px', cursor: 'pointer', display: 'flex', flexDirection: 'row', color: 'white', fontFamily: 'Light', fontSize: '20px'}} onClick={(e) => {
                                                e.preventDefault();
                                                setRedirection("/projects");
                                            }}>
                                                <img src={projectPicture} alt="équipes" style={{marginTop: '25px', marginLeft: '25px', width: '30px', height: '30px'}}/>
                                                <div style={{paddingLeft: '15px', marginTop: '28px'}}>Gérer les projets</div>
                                            </li>
                                            
                                        </>
                                    )
                                }
                                {
                                    (user.role === "etudiant") && (
                                        <>
                                            {/*<li style={{paddingBottom: '10px', cursor: 'pointer', display: 'flex', flexDirection: 'row', color: 'white', fontFamily: 'Light', fontSize: '20px'}} onClick={(e) => {
                                                e.preventDefault();
                                                setRedirection("/myevents");
                                            }}>
                                                <img src={eventsPicture} alt="home" style={{marginTop: '25px', marginLeft: '25px', width: '30px', height: '30px'}}/>
                                                <div style={{paddingLeft: '15px', marginTop: '28px'}}>Mes évènements</div>
                                        </li>*/}
                                            <li style={{paddingBottom: '10px', cursor: 'pointer', display: 'flex', flexDirection: 'row', color: 'white', fontFamily: 'Light', fontSize: '20px'}} onClick={(e) => {
                                                e.preventDefault();
                                                setRedirection("/myteams");
                                            }}>
                                                <img src={teamIcone} alt="équipes" style={{marginTop: '25px', marginLeft: '25px', width: '30px', height: '30px'}}/>
                                                <div style={{paddingLeft: '15px', marginTop: '28px'}}>Mes équipes</div>
                                            </li>
                                            {/*<li style={{paddingBottom: '10px', cursor: 'pointer', display: 'flex', flexDirection: 'row', color: 'white', fontFamily: 'Light', fontSize: '20px'}}>
                                                <img src={messagePicture} alt="messagerie" style={{marginTop: '25px', marginLeft: '25px', width: '30px', height: '30px'}}/>
                                                <div style={{paddingLeft: '15px', marginTop: '28px'}}>Messagerie</div>
                                        </li>*/}
                                            
                                        </>
                                    )
                                }
                                {/*<li style={{paddingBottom: '10px', cursor: 'pointer', display: 'flex', flexDirection: 'row', color: 'white', fontFamily: 'Light', fontSize: '20px'}}>
                                    <img src={notifPicture} alt="home" style={{marginTop: '25px', marginLeft: '25px', width: '30px', height: '30px'}}/>
                                    <div style={{paddingLeft: '15px', marginTop: '28px'}}>Notifications</div>
                            </li>*/}
                                <li style={{paddingBottom: '10px', cursor: 'pointer', display: 'flex', flexDirection: 'row', color: 'white', fontFamily: 'Light', fontSize: '20px'}} onClick={(e) => {
                                    e.preventDefault();
                                    logout();
                                    setRedirection("/");
                                }}>
                                    <img src={logoutPicture} alt="home" style={{marginTop: '25px', marginLeft: '25px', width: '30px', height: '30px'}}/>
                                    <div style={{paddingLeft: '15px', marginTop: '28px'}}>Fermer la session</div>
                                </li>
                            </>
                        )
                    }
                    {
                        !open && (
                            <>
                                {
                                    window.innerWidth > 800 && (
                                        <li className="PictureLiStyleHeaderDash">
                                            <span style={{height: '60px', width: '60px', marginLeft: '9px', marginTop: '20px', borderRadius: '50%', backgroundColor: 'rgb(212, 217, 217)', border: '2px solid black', display: 'inline-block', cursor: 'pointer'}}>
                                                <img src={profilPicture} alt="profil" style={{marginTop: '8px', marginLeft: '8px', width: '44px', height: '44px'}} />
                                            </span>  
                                        </li>
                                    )
                                }
                                <li className="ItemLiStyleHeaderDash" onClick={(e) => {
                                    e.preventDefault();
                                    setRedirection("/");
                                }}>
                                    <img src={homePicture} alt="home" className="ImageNavBarDashboard"/>
                                </li>
                                {
                                    (user.role === "administrateur") &&
                                        <li className="ItemLiStyleHeaderDash" onClick={(e) => {
                                                        e.preventDefault();
                                                        setRedirection("/dashboard")
                                                    }}>
                                            <img src={dashboardPicture} alt="home" className="ImageNavBarDashboard"/>
                                        </li>
                                }
                                {
                                    (user.role === "administrateur") && (
                                        <>
                                            <li className="ItemLiStyleHeaderDash" onClick={(e) => {
                                                e.preventDefault();
                                                setRedirection("/events")
                                            }}>
                                                <img src={eventsPicture} alt="home" className="ImageNavBarDashboard"/>
                                            </li>
                                            <li className="ItemLiStyleHeaderDash" onClick={(e) => {
                                                e.preventDefault();
                                                setRedirection("/projects")
                                            }}>
                                                <img src={projectPicture} alt="home" className="ImageNavBarDashboard"/>
                                            </li>
                                            <li className="ItemLiStyleHeaderDash" onClick={(e) => {
                                                e.preventDefault();
                                                setRedirection("/users/config")
                                            }}>
                                                <img src={teamPicture} alt="home" className="ImageNavBarDashboard"/>
                                            </li>
                                        </>
                                    )
                                }
                                {
                                    (user.role === "gestionnaireExterne" || user.role === "gestionnaireIA") && (
                                        <>
                                            <li className="ItemLiStyleHeaderDash" onClick={(e) => {
                                                e.preventDefault();
                                                setRedirection("/projects")
                                            }}>
                                                <img src={projectPicture} alt="home" className="ImageNavBarDashboard"/>
                                            </li>
                                        </>
                                    )
                                }
                                {
                                    (user.role === "etudiant") && (
                                        <>
                                            {/*<li className="ItemLiStyleHeaderDash" onClick={(e) => {
                                                e.preventDefault();
                                                setRedirection("/myevents")
                                            }}>
                                                <img src={eventsPicture} alt="home" className="ImageNavBarDashboard"/>
                                        </li>*/}
                                            <li className="ItemLiStyleHeaderDash" onClick={(e) => {
                                                e.preventDefault();
                                                setRedirection("/myteams")
                                            }}>
                                                <img src={teamIcone} alt="home" className="ImageNavBarDashboard"/>
                                            </li>
                                            {/*<li className="ItemLiStyleHeaderDash">
                                                <img src={messagePicture} alt="home" className="ImageNavBarDashboard"/>
                                        </li>*/}
                                        </>
                                    )
                                }
                                {/*<li className="ItemLiStyleHeaderDash">
                                    <img src={notifPicture} alt="home" className="ImageNavBarDashboard"/>
                                </li>*/}
                                <li className="ItemLiStyleHeaderDash" onClick={(e) => {
                                    e.preventDefault();
                                    logout();
                                }}>
                                    <img src={logoutPicture} alt="home" className="ImageNavBarDashboard"/>
                                </li>
                            </>
                        )
                    }
                </ul>
            </nav>
        </>
    )

}

export default HeaderDashboard;
