
export function getReturnUrl() {
    let url = localStorage.getItem("OldreturnURL");
    if(url){
        return url;
    } else {
        return "/";
    }
}

export function visitUrl(url) {
    let newOldReturn = localStorage.getItem("thisUrl");
    if(newOldReturn === url){ return; }
    localStorage.setItem("OldreturnURL", newOldReturn);
    localStorage.setItem("thisUrl", url);
}

// eslint-disable-next-line
function debugUrlMessage(){
    let oldUrl = localStorage.getItem("OldreturnURL");
    let url = localStorage.getItem("thisUrl");
    console.log("Changement d'URL: ");
    console.log("  - Ancien: " + oldUrl);
    console.log("  - Nouveau: " + url);
}