import dateToString from "../../Utils/DateUtils";

export default function Message(msg, update, setUpdate){
    let styleSended = {
        width: `85%`,
        marginLeft: '15%',
        backgroundColor: '#16b7a7',
        fontSize: '18px',
        color: 'white',
        borderTopLeftRadius: '35px',
        borderTopRightRadius: '35px',
        borderBottomLeftRadius: '35px',
    };
    let styleRetrieved = {
        width: `85%`,
        backgroundColor: '#00897B',
        fontSize: '18px',
        color: 'white',
        borderTopLeftRadius: '35px',
        borderTopRightRadius: '35px',
        borderBottomRightRadius: '35px',
    };
    let styleProjetAnnonce = {
        width: `85%`,
        backgroundColor: 'rgb(185, 195, 194)',
        marginLeft: '7%',
        fontSize: '15px',
        color: 'black',
    };
    let styleEventAnnonce = {
        width: `85%`,
        backgroundColor: 'rgb(185, 195, 194)',
        marginLeft: '7%',
        fontSize: '15px',
        color: 'black',
    };
    
    let {pseudoSender,roleSender,dateMessage, range, userIsSender,content} = msg.msg;

    let styleOfMessage = styleSended;
    let personne = "VOUS - " + dateToString(dateMessage);
    if(userIsSender === false){
        styleOfMessage = styleRetrieved;
        personne = pseudoSender + " - " + roleSender + " - " + dateToString(dateMessage);
    }
    if(range === "projet"){
        styleOfMessage = styleProjetAnnonce;
    }
    if(range === "event"){
        styleOfMessage = styleEventAnnonce;
    }


    return window.innerWidth > 800 ? (
        <>
            <div style={styleOfMessage}>
                <p style={{fontSize: '22px', textAlign: 'center', padding: '10px 0px 0px 0px'}}>
                    <b>{personne}</b>
                </p>
                {
                    range === "projet" && (
                        <p style={{textAlign: 'center', paddingBottom: '10px'}}>
                            Ce message a été envoyé à toutes les équipes du projet.
                        </p>
                    )
                }
                {
                    range === "event" && (
                        <p style={{textAlign: 'center', paddingBottom: '10px'}}>
                            Ce message a été envoyé à toutes les équipes de l'événement.
                        </p>
                    )
                }
                <p style={{fontSize: '22px', textAlign: 'justify', padding: '0px 20px 40px 20px'}}>
                    {content}
                </p>
            </div>
        </>
    ) : (
        <>
            <div style={styleOfMessage}>
                <p style={{fontSize: '15px', textAlign: 'center', padding: '10px 0px 0px 0px', cursor: 'pointer'}}>
                    <b>{personne}</b>
                </p>
                {
                    range === "projet" && (
                        <p style={{textAlign: 'center', paddingBottom: '10px'}}>
                            Ce message a été envoyé à toutes les équipes du projet.
                        </p>
                    )
                }
                {
                    range === "event" && (
                        <p style={{textAlign: 'center', paddingBottom: '10px'}}>
                            Ce message a été envoyé à toutes les équipes de l'événement.
                        </p>
                    )
                }
                <p style={{fontSize: '15px', textAlign: 'justify', padding: '0px 20px 40px 20px'}}>
                    {content}
                </p>
            </div>
        </>
    )
}