import { NavLink } from "react-router-dom";

import '../../style/App.css';
import '../../fonts/Inter-Light.ttf'

/**
 * @name MyNavButton
 * @param {children} child : The children of the component
 * @param {to} link : the link it leads to 
 * @returns the button component nav link (exemple: used to be clicked for connection)
 */
function MyNavButton2({ children, to }) {

    return (
        <div>
            <button style={{
                backgroundColor: '#01C2AF',
                height: '50px',
                width: '200px',
                border: '1px solid #01C2AF',
                borderRadius: '26px',
            }}>
                <NavLink style={{
                    textDecoration: "none",
                    fontSize: '18px',
                    color: "white"
                }} to={to} >
                    {children}
                </NavLink>
            </button>
        </div>
    );
}

export default MyNavButton2;
