// eslint-disable-next-line
export default function dateToString(date){
    let dateChoosen = new Date(date);
    let year = dateChoosen.getFullYear();
    let month = dateChoosen.getMonth()+1;
    let day = dateChoosen.getDate();
    let hour = dateChoosen.getHours();
    let minute = dateChoosen.getMinutes();
    if(year >= 0 && month >= 0 && day >= 0 && hour >= 0 && minute >= 0){
        return base10(day) + "/" +  base10(month) + "/" + year + " - " +  base10(hour) + ":" +  base10(minute);
    } else {
        return "";
    }
}

function base10(value){
    if(value < 10){
        return "0" + value;
    } else {
        return value;
    };
}