
export default function TeamProjectBox(projet){

    const {
        titre, 
        //image, 
        description,
        mots,
        lien_sujet, 
    } = projet.projet;

    const N = 700;
    const maxChar = (description.length > N) ? N : description.length;
    const printedDescription = description.slice(0, maxChar) + ((maxChar >= N) ? " [...]" : "")
    let spaces = []
    for(let i = printedDescription.length; i < 700; i += 120){
        spaces.push("");
    }

    return (
        <>
            <ul style={{listStyleType: 'none', display: 'flex', flexDirection: window.innerWidth < 800 ? 'column' : 'row', padding: '0px', width: '94%'}}>
                <li style={{width: window.innerWidth < 800 ? '100%' : '30%', overflow: 'hidden'}}>
                    <img src={require("../../assets/dataChallenge.jpg")} alt='ImgDuProjetChoisi' style={{
                        height: '100%',
                        maxWidth: '500px',
                    }}/>
                </li>
                <li style={{backgroundColor: 'white', width: window.innerWidth < 800 ? '100%' : '70%'}}>
                    <div style={{paddingTop: '10px', paddingLeft: window.innerWidth < 800 ? '0px' : '50px', fontFamily: 'Light', color: 'black', border: '0px solid white'}}>
                        <h3 style={{textAlign: window.innerWidth < 800 ? 'center' : 'none'}}> {titre} </h3>
                        <p style={{textAlign: 'justify', fontSize: window.innerWidth < 800 ? '17px' : '20px'}}> {printedDescription} </p>
                        {
                            window.innerWidth > 800 && spaces.map((espace) => {
                                return (
                                    <br/>
                                )
                            })
                        }
                        <ul style={{marginTop: '40px', listStyleType: 'none', padding: '0', display: 'flex', flexDirection: window.innerWidth < 800 ? 'column' : 'none'}}>
                            <li style={{width: window.innerWidth < 800 ? '100%' : '50%'}}>
                                <ul style={{listStyleType: 'none', padding: '0', display: 'flex', flexDirection: 'row'}}>
                                    {
                                        mots.map((mot) => {
                                            return (
                                                <li>
                                                    <button style={{fontFamily: 'Light', fontSize: '25px', marginLeft: '10px', marginRight: '10px',
                                                        backgroundColor: '#b9b1b1',
                                                        color: 'black',
                                                        border: 'none',
                                                        padding: '10px 15px 10px 15px'
                                                    }} disabled='disabled'>
                                                        {mot}
                                                    </button>
                                                </li>
                                            )
                                        })
                                    }
                                </ul>
                            </li>
                            
                            <li style={{marginTop: window.innerWidth < 800 ? '25px' : '0px'}}>
                                <button style={{
                                    fontFamily: 'Light',
                                    fontSize: '25px',
                                    padding: '10px 45px 10px 45px',
                                    border: '1px solid black',
                                    borderRadius: '25px',
                                    cursor: 'pointer'
                                }} onClick={(e) => {
                                    e.preventDefault();
                                    window.open(lien_sujet);
                                }}>
                                    Lien vers le sujet
                                </button>
                            </li>
                        </ul>
                    </div>
                </li>
            </ul>
        </>
    ) 
}