import Header from "../../components/Header";
import LoginMessagePart from "../../components/LoginMessagePart";

import '../../style/App.css';
import '../../fonts/Inter-Light.ttf'
import MyNavButton from "../../components/MyNavButtonClair";
import TextBox from "../../components/Champs/TextBox";
import MdpBox from "../../components/Champs/MdpBox";

import WhiteNavButton from "../../components/WhiteNavButton";
import SchoolSelector from "../../components/Champs/SchoolSelector";
import LevelSelector from "../../components/Champs/LevelSelector";

import "./Inscription.css"
import { useAuthContext } from "../../Contexts/AuthContext";
import { Navigate } from "react-router-dom";
import getApiUrl from "../../Constantes/apiurl";
import { useState } from "react";
import { visitUrl } from "../../Contexts/ReturnManager";
import { throwErrorOnFetch } from "../../Utils/Errors";

/**
 * @name InscriptionPage
 * @description Used to print the inscription page
 * @returns The Page InscriptionPage component
 */
function InscriptionPage(){

    visitUrl(window.location.pathname);

    const [nom, setNom] = useState("");
    const [prenom, setPrenom] = useState("");
    const [pseudo, setPseudo] = useState("");
    const [ville, setVille] = useState("");
    const [mail, setMail] = useState("");
    const [mdp, setMdp] = useState("");
    const [github, setGithub] = useState("");
    const [linkedin, setLinkedin] = useState("");

    const [erreur, setErreur] = useState(["", ""]);
    const {user, setUser , setIsLoggedIn } = useAuthContext();

    function containsUppercase(str) {
        return /[A-Z]/.test(str);
    }

    function containsLowercase(str) {
        return /[a-z]/.test(str);
    }

    function containsNumero(str) {
        return /[0-9]/.test(str);
    }

    function containsSpecialCase(str) {
        // eslint-disable-next-line
        let char = /[!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?]+/;
        return char.test(str);
    }

    function sizeIsOk(str){
        return (str.length >= 8);
    }

    const register = async (e) => {

        e.preventDefault();

        let email = mail;
        let erreur = false;

        if(!erreur && nom === ""){
            setErreur(["nom", "Le champ 'nom' est vide"]);
            erreur = true;
        }

        if(!erreur && prenom === ""){
            setErreur(["prenom", "Le champ 'prénom' est vide"]);
            erreur = true;
        }

        if(!erreur && pseudo === ""){
            setErreur(["pseudo", "Le champ 'pseudo' est vide"])
            erreur = true;
        }

        if(!erreur && email === ""){
            setErreur(["email", "Le champ 'email' est vide"])
            erreur = true;
        }

        if(!erreur && !(email.includes("@"))){
            setErreur(["email", "Adresse email invalide (elle ne contient pas de '@')"])
            erreur = true;
        } 
        if (!erreur && email.includes("@") && !((email.split("@")[1]).includes("."))){
            setErreur(["email", "Aucun domaine detecté dans votre mail."]);
            erreur = true;
        } 
        if (!erreur && email.endsWith(".")){
            setErreur(["email", "Un email ne se termine pas par un point."])
            erreur = true;
        } 

        if(!erreur && ville === ""){
            setErreur(["ville", "Le champ 'ville' est vide"]);
            erreur = true;
        }

        let ecole = document.getElementById('combo-box-school')['value'];

        if(!erreur && ecole === ""){
            setErreur(["ecole", "Le champ 'école d origine' est vide"]);
            erreur = true;
        }

        let codeEcole = "";
        let niveauEtude = document.getElementById('combo-box-level')['value'];

        if(!erreur && niveauEtude === ""){
            setErreur(["niveauEtude", "Le champ 'niveau d'étude' est vide"]);
            erreur = true;
        }

        let password = mdp;

        if(!erreur && password === ""){
            setErreur(["password", "Vous ne voudriez quand même pas que tout le monde puisse accéder à votre compte ? Choisissez un mot de passe"]);
            erreur = true;
        }

        if (!erreur && !sizeIsOk(password)){
            setErreur(["password", "Votre mot de passe est trop court (il faut au minimum 8 charactères)."]);
            erreur = true;
        }

        if (!erreur && !containsLowercase(password)){
            setErreur(["password", "Il faut au moins une lettre minuscule dans votre mot de passe"]);
            erreur = true;
        }

        if (!erreur && !containsUppercase(password)){
            setErreur(["password", "Il faut au moins une lettre majuscule dans votre mot de passe"]);
            erreur = true;
        }

        if (!erreur && !containsNumero(password)){
            setErreur(["password", "Il faut au moins un chiffre dans votre mot de passe"]);
            erreur = true;
        }

        if (!erreur && !containsSpecialCase(password)){
            setErreur(["password", "Il faut au moins un charactère spécial dans votre mot de passe"]);
            erreur = true;
        }
        if(!erreur){
            try {
                const response = await fetch(getApiUrl() + 'inscription', {
                    method: 'POST',
                    body: JSON.stringify({nom, prenom, pseudo, email, linkedin, github, ville, ecole, codeEcole, niveau_etude: niveauEtude, password}),
                    headers: {
                        'Content-Type': 'application/json'
                    }
                })

                const json = await response.json();
                if(response.ok) {
                    const userLogged = {token: json.token, role: json.role, prenom: json.prenom, nom: json.nom, pseudo: json.pseudo}
                    setErreur(["", ""]);
                    setUser(userLogged);
                    setIsLoggedIn(true);
                } else {
                    if(json.error){
                        if(json.error === "Le pseudo existe déjà."){
                            setErreur(['pseudo', json.error]);
                        } else {
                            setErreur(['SERVEUR_ERROR', json.error]);
                        }
                    } else if(json.errors && Array.isArray(json.errors) && json.errors.length > 0){
                        setErreur([json.errors[0].path, json.errors[0].msg]);
                    } else {
                        setErreur(['SERVEUR_ERROR', ""]);
                    }
                }
            } catch(e) {
                throwErrorOnFetch(e);
                setErreur(['SERVEUR_ERROR', 'La requete n\'a pas pu être envoyée !'])
            }
        }
    }

    return (
        <div style={{overflowX: 'hidden', width: `calc(100% + 16px)`, marginLeft: '-8px'}}>
            <div className="font">
                
                {user && (
                    <Navigate to="/dashboard" replace={true} />
                )}
                <Header/>
                <ul className="listeInscription">
                    <li className="InscriptionC1">
                        <div className="InscriptionC1Container">
                            <LoginMessagePart>
                                <img alt="logo-Asso-Ia-Pau" src={require("../../assets/iapau_logo.png")} className="InscriptionC1Picture"/>
                                <h1> <b>Bon retour parmis nous !</b> </h1>
                                <p style={{ fontSize: '20px' }}>
                                    Vous possédez déjà un compte ?
                                </p>
                                <MyNavButton to="/connexion">
                                    Connectez vous
                                </MyNavButton>
                            </LoginMessagePart>
                        </div>
                    </li>
                    <li className="InscriptionC2">
                        <div style={{
                            backgroundColor: 'white'
                        }}>
                            <img alt="banner" src={require("../../assets/photoFont1.jpg")} className="InscriptionC2Image"/>
                            <div style={{
                                paddingBottom: '10%',
                                textAlign: 'center',
                                width: '100%'
                            }}>
                               <h1 style={{
                                    color: '#00897B',
                                    fontSize: '45px',
                                    textAlign: 'center',
                                    paddingTop: '10px'
                               }}>
                                    Inscription
                               </h1>

                               <form style={{width: '100%'}}>
                                <ul className="InscriptionListeChamps">
                                    <li className="InscriptionListeChampsChamp">
                                        <ul style={{listStyleType: 'none', paddingLeft: '0', width: '90%', marginLeft: '5%', marginRight: '5%'}}>
                                            
                                            <li>
                                                <TextBox placeholder="Nom" setText={setNom} color={erreur[0] === "nom" ? "red" : "#707776"} id='text-box-nom'/>
                                            </li>
                                            <li style={{ paddingTop: '20px'}}>
                                                <TextBox placeholder="Prénom" setText={setPrenom} color={erreur[0] === "prenom" ? "red" : "#707776"} id='text-box-prenom'/>
                                            </li>
                                            <li style={{ paddingTop: '20px'}}>
                                                <TextBox placeholder="Pseudo" setText={setPseudo} color={erreur[0] === "pseudo" ? "red" : "#707776"} id='text-box-pseudo'/>
                                            </li>
                                            <li style={{ paddingTop: '20px'}}>
                                                <TextBox placeholder="E-mail" setText={setMail} color={erreur[0] === "email" ? "red" : "#707776"} id='text-box-mail'/>
                                            </li>
                                            <li style={{ paddingTop: '20px'}}>
                                                <TextBox placeholder="Ville" setText={setVille} color={erreur[0] === "ville" ? "red" : "#707776"} id='text-box-ville'/>
                                            </li>
                                        </ul>
                                    </li>
                                    <br/>
                                    <li className="InscriptionListeChampsChamp">
                                        <ul style={{listStyleType: 'none', paddingLeft: '0', width: '90%', marginLeft: '5%', marginRight: '5%'}}>
                                            <SchoolSelector color={erreur[0] === "ecole" ? "red" : "#707776"}/>
                                            <br/>
                                            <LevelSelector color={erreur[0] === "niveauEtude" ? "red" : "#707776"}/>
                                            <li style={{ paddingTop: '20px'}}>
                                                <TextBox placeholder="Lien linkedin (optionnel)" color={erreur[0] === "linkedin" ? "red" : "#707776"} setText={setLinkedin} id='text-box-linkedin'/>
                                            </li>
                                            <li style={{ paddingTop: '20px'}}>
                                                <TextBox placeholder="Lien Github (optionnel)" color={erreur[0] === "github" ? "red" : "#707776"} setText={setGithub} id='text-box-github'/>
                                            </li>
                                            <li style={{ paddingTop: '20px'}}>
                                                <MdpBox placeholder="Mot de passe" setMdp={setMdp} id='passwordbox' color={erreur[0] === "password" ? "red" : "#707776"} />
                                            </li>
                                        </ul>
                                    </li>

                                </ul>
                                <div style={{width: '50%', marginLeft: '25%', marginRight: '25%'}}>
                                    {
                                        erreur[0] !== "" && (
                                            <>
                                                <br/>
                                                <p style={{color: 'red', fontSize: '20px'}}>
                                                    <b>{erreur[1]}</b>
                                                </p>
                                            </>
                                        )
                                    }
                                    <WhiteNavButton onClick={register}>
                                        S'enregistrer
                                    </WhiteNavButton>
                                </div>
                               </form>
                            </div>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    )
}

export default InscriptionPage;