import { NavLink, Navigate, useLocation } from "react-router-dom";

import '../../style/App.css';
import '../../fonts/Inter-Light.ttf'
import { useState } from "react";

/**
 * @name MyNavButton
 * @param {children} child : The children of the component
 * @param {to} link : the link it leads to 
 * @returns the button component nav link (exemple: used to be clicked for connection)
 */
function MyNavButton({ children, to }) {

    const [needRedirect , setNeedRedirect] = useState(false);

    let location = useLocation().pathname;

    return (needRedirect === true && typeof to !== "undefined" && to !== "" && to !== location) ? (
        <Navigate to={to} replace={true} />
    ) : (
        <div>
            <button style={{
                backgroundColor: '#00897B',
                height: '50px',
                width: '200px',
                border: '1px solid #00897B',
                borderRadius: '25px',
                textDecoration: "none",
                fontSize: '18px',
                color: "white",
                cursor: 'pointer'
            }} onClick={(e) => {
                e.preventDefault();
                setNeedRedirect(true);
            }}>
                <NavLink style={{
                    textDecoration: "none",
                    fontSize: '18px',
                    color: "white"
                }} to={to} >
                    {children}
                </NavLink>
            </button>
        </div>
    );
}

export default MyNavButton;
