import React from 'react'
import ReactDOM from 'react-dom'
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom'
import Home from './pages/Home'
import Erreur404 from './pages/Erreur404'
import Connexion from './pages/Connexion'
import Inscription from './pages/Inscription'

import { AuthProvider } from './Contexts/AuthContext'
import DataChallengePage from './pages/DataChallenge'
import Dashboard from './pages/Dashboard'
import AdminUserConfig from './pages/AdminUserConfig'
import ProjectListe from './pages/Projects'
import ProjectCreate from './pages/Projects/Create'
import EventsHome from './pages/Events'
import EventCreationPage from './pages/Events/Create'
import ProjectEdit from './pages/Projects/Edit'
import EventModificationPage from './pages/Events/Edit'
import PageEquipe from './pages/Equipes/Voir'
import PageEquipeEdit from './pages/Equipes/Modifier'
import PageListeEquipe from './pages/Equipes/MyTeams'
import PageListeEquipesProjet from './pages/Projects/ListeEquipes'
import PageListeEquipesEvents from './pages/Events/ListeEquipes'

/**
 *  Route Manager
 */
ReactDOM.render(
  <React.StrictMode>
    <AuthProvider>
        <Router>
          <Routes>
              <Route path="/" element={ <Home/> }/>
              <Route path="/connexion" element={ <Connexion/> }/>
              <Route path="/inscription" element={ <Inscription/> }/>
              <Route path="/dashboard" element={ <Dashboard/> }/>
              <Route path="/users/config" element={ <AdminUserConfig/> }/>
              <Route path="/projects" element={ <ProjectListe /> }/>
              <Route path="/projects/create" element={ <ProjectCreate /> }/>
              <Route path="/projects/:id/edit" element={ <ProjectEdit/> }/>
              <Route path="/projects/:id/teams" element={ <PageListeEquipesProjet/> }/>
              <Route path="/events" element={ <EventsHome/> }/>
              <Route path="/events/create" element={ <EventCreationPage/> }/>
              <Route path="/events/:id" element={ <DataChallengePage /> }/>
              <Route path="/events/:id/edit" element={ <EventModificationPage/> }/>
              <Route path="/events/:id/teams" element={ <PageListeEquipesEvents/> }/>
              <Route path="/teams/:id" element={ <PageEquipe /> }/>
              <Route path="/teams/:id/edit" element={ <PageEquipeEdit /> }/>
              <Route path="/myteams" element={ <PageListeEquipe /> }/>

              <Route path="*" element={<Erreur404/>}/>
          </Routes>
      </Router>
    </AuthProvider>
  </React.StrictMode>,
  document.getElementById('root')
)