
import { Navigate } from "react-router-dom";
import bannerPau from "../../../assets/photoFont1.jpg";
import { useAuthContext } from "../../../Contexts/AuthContext";
import PageEspacePerso from "../../PageEspacePersoType";
import { useEffect, useState } from "react";
import WhiteNavButton from "../../../components/WhiteNavButton";
import TextBox from "../../../components/Champs/TextBox";
import ButtonGreen from "../../../components/ButtonGreen";
import getApiUrl from "../../../Constantes/apiurl";
import Swal from "sweetalert2";
import { throwErrorOnFetch } from "../../../Utils/Errors";

export default function ProjectCreate(){
    let {user} = useAuthContext();
    let userStorage = localStorage.getItem('user');
    if(userStorage){ 
        user = JSON.parse(userStorage); 
    }
    let doesNotHavePermission = !user || user.role !== "administrateur";
    let hasPermission = !doesNotHavePermission;

    const [retour, setRetour] = useState(false);

    function handleRetour(){
        setRetour(!retour);
    }
    const [nom, setNom] = useState("");
    const [lienSujet, setLienSujet] = useState("");
    const [description, setDescription] = useState("");
    // eslint-disable-next-line
    const [recompense, setRecompense] = useState(0);
    const [keyword, setKeyword] = useState([]);
    const [ressources, setRessources] = useState([]);
    const [gestionnaireExterne, setGestionnaireExterne] = useState([]);
    const [gestionnaireIA, setGestionnaireIA] = useState([]);
    const [propositionsExternes, setPropositionsExternes] = useState([]);
    const [propositionsInternes, setPropositionsInternes] = useState([]);
    const [image, setImage] = useState("");
    const [template, setTemplate] = useState("");

    const [possibleGestionnaireExterne, setPossibleGestionnaireExterne] = useState([]);
    const [possibleGestionnaireIA, setPossibleGestionnaireIA] = useState([]);


    useEffect(() => {
        const fetchDatasGestionnaires = async () => {
            try {
                const response = await fetch(getApiUrl() + "gestionnaire/", {
                    method: "GET",
                    headers: {
                        Authorization: "Bearer " + user.token,
                        "Content-Type": "application/json; charset=utf-8",
                    },
                });
                const json = await response.json();
                if(response.ok){
                    setPossibleGestionnaireExterne(json.gestionnairesExternes);
                    setPossibleGestionnaireIA(json.gestionnairesIa);
                }
            } catch(e) {
                throwErrorOnFetch(e);
            }
        }
        fetchDatasGestionnaires();
    // eslint-disable-next-line
    }, []);

    const ListeGestionnaireExternes = possibleGestionnaireExterne;

    let ListeGestionnairesInternes = possibleGestionnaireIA;

    function addGestionnaire(gest, setGest, gestionnaire){

        if(typeof gestionnaire === "undefined"){ return;}

        let dedans = false;
        for(let i = 0; i < gest.length; i++){
            dedans = (dedans === false) ? (gest[i].id === gestionnaire.id) : dedans;
        }
        if(!dedans){
            let datas = gest;
            datas.push(gestionnaire);
            setGest(datas);
            setUpdate(update + 1);
        }
    }

    function removeGestionnaire(gest, setGest, gestionnaire){
        let datas = [];
        for(let i = 0; i < gest.length; i++){
            if(gest[i].id !== gestionnaire.id){
                datas.push(gest[i]);
            }
        }
        setGest(datas);
        setUpdate(update + 1);
    }

    function deleteRessource(i){
        if(i < 0 || i >= ressources.length){ return; }
        let datas = ressources;
        let newDatas = [];
        for(let j = 0; j < datas.length; j++){
            if(i !== j){
                newDatas.push(datas[j]);
            }
        }
        setRessources(newDatas);
        setUpdate(update+1);
    }

    function addRessource(){
        let datas = ressources;
        datas.push({
            nom: '',
            type: 'lien',
            lien: '',
            description: '',
            consultation: true,
            publication: Date.now(),
            shown: false
        });
        setRessources(datas);
        setUpdate(update+1);
    }

    function handleView(i){

        if(i < 0 || i >= ressources.length){ return; }

        let datas = ressources;
        datas[i].shown = !datas[i].shown;
        setRessources(datas);
        setUpdate(update+1);

    }

    function base10value(val){
        return val < 10 ? "0" + val : val;
    }

    function editRessource(i, champ, value){

        if(i < 0 || i >= ressources.length){ return; }

        let datas = ressources;
        if(champ === "NOM"){
            datas[i].nom = value;
        } else if (champ === "TYPE"){
            datas[i].type = value;
        } else if (champ === "LIEN"){
            datas[i].lien = value;
        } else if (champ === "DESCRIPTION"){
            datas[i].description = value;
        } else if (champ === "CONSULTATION"){
            datas[i].consultation = !datas[i].consultation;
        } else if (champ === "PUBLICATION"){
            datas[i].publication = value;
        } 

        setRessources(datas);
        setUpdate(update+1);

    }

    function addKeyword(word){

        if(word === ""){ return; }

        let keywordTemp = keyword;

        if(!(keywordTemp.includes(word))){
            keywordTemp.push(word);
            setKeyword(keywordTemp);
        }
    }

    function removeKeyword(word){

        if(word === ""){ return; }

        if(keyword.includes(word)){
            let arrayWithoutWord = [];
            for(let i = 0; i < keyword.length; i++){
                if(!(word.toUpperCase() === keyword[i].toUpperCase())){
                    arrayWithoutWord.push(keyword[i]);
                }
            }
            setKeyword(arrayWithoutWord);
        }
    }

    const [update, setUpdate] = useState(0);

    return (
        <>
            {
                doesNotHavePermission && (
                <Navigate to="/" replace/>
                )
            }
            {
                hasPermission && retour && (
                    <Navigate to="/projects" replace/>
                )
            }
            {
                hasPermission && !retour && (
                    <>
                        <PageEspacePerso>
                            <img src={bannerPau} className="BannerAdminUser" alt=""/>
                            <h1 style={{fontFamily: 'Light', color: 'white', position: 'absolute',top: '80px', fontSize: '55px', textAlign: 'center', width: '96%', textShadow: '0 1px 0 black, 0 -1px 0 black'}}>
                                Création d'un projet
                            </h1>
                            <ul style={{listStyleType: 'none', padding: '0px', display: 'flex', flexDirection: 'row', marginLeft: '100px', marginRight: '100px'}}>
                                <li style={{width: '150px'}}>
                                    <div style={{width: '150px', position: 'fixed'}}>
                                        <WhiteNavButton onClick={(e) => {
                                            e.preventDefault();
                                            handleRetour();
                                        }}>
                                            Retour
                                        </WhiteNavButton>
                                    </div>
                                </li>
                                <li style={{width: `calc(100% - 300px)`}}>
                                    
                                    <div style={{fontFamily: 'Light', marginLeft: '15%', marginTop: '40px', textAlign: 'center', width: '70%'}}>
                                    <h2>
                                        Nom du projet
                                    </h2>
                                    <TextBox setText={setNom} id="project-create-nom"/>
                                    <br/><br/><br/>
                                    <h2>
                                        Description
                                    </h2>
                                    <textarea style={{width: '100%', fontFamily: 'Light', height: '350px', resize: 'none', border: `3px solid #707776`,
                                        borderRadius: '15px',
                                        fontSize: '19px', }} 
                                    onChange={(e) => {
                                        e.preventDefault();
                                        setDescription(e.target.value);
                                    }} defaultValue={description}/>
                                    <br/><br/><br/>
                                    {/*<h2>
                                        Récompense
                                    </h2>
                                    <ul style={{listStyleType: 'none', padding: '0', display: 'flex', flexDirection: 'row'}}>
                                        <li style={{width: '70%'}}>
                                            <input type="number" style={{width: '100%', fontFamily: 'Light', height: '50px', resize: 'none', border: `3px solid #707776`,
                                                borderRadius: '15px',
                                                fontSize: '20px', textAlign: 'right', paddingRight: '10px'}}/> 
                                        </li>
                                        <li style={{width: '30%', fontSize: '25px', paddingLeft: '30px', textAlign: 'left', marginTop: `calc(25px - 12px)`}} onChange={(e) => {
                                            e.preventDefault();
                                            setRecompense(e.target.value);
                                        }}>
                                            € (Devise)
                                        </li>
                                    </ul>
                                    
                                    <br/><br/><br/>*/}
                                    <h2>
                                        Lien vers le sujet
                                    </h2>
                                    <TextBox setText={setLienSujet} id="project-create-sujet"/>
                                    <br/><br/><br/>
                                    <h2>
                                        Lien vers template GITLAB
                                    </h2>
                                    <TextBox setText={setTemplate} id="projec-gitlabtemplate-sujet"/>
                                    <br/><br/><br/>
                                    <h2>
                                        Mots clefs
                                    </h2>
                                    <div>
                                        <ul style={{display: 'flex', flexDirection: 'row', listStyleType: 'none'}}>
                                            <li style={{width: '60%'}}>
                                                <TextBox id="project-create-keyword"/>
                                            </li>
                                            <li style={{width: '40%'}}>
                                                <ButtonGreen toRun={(e) => {
                                                    e.preventDefault();
                                                    let word = document.getElementById('project-create-keyword').value;
                                                    document.getElementById('project-create-keyword').value = "";
                                                    addKeyword(word);
                                                    setUpdate(update+1)
                                                }}>
                                                    Ajouter
                                                </ButtonGreen>
                                            </li>
                                        </ul>
                                    </div>
                                    <br/>
                                    <div style={{color: 'black'}}>
                                        <ul style={{display: 'flex', flexDirection: 'row', listStyleType: 'none', padding: '0', overflow: 'scroll', overflowY: 'hidden'}}>
                                        {
                                            keyword.map((value, index) => {
                                                return (
                                                    <li key={`keyword-${index}`} style={{backgroundColor: '#00897B', textAlign: 'left' ,marginLeft: '10px', marginRight: '10px', fontSize: '17px'}}>
                                                        <ul style={{display: 'flex', flexDirection: 'row', listStyleType: 'none', width: '100%', padding: '0px'}}>
                                                        <li style={{textAlign: 'center', width: '80%', fontSize: '18px', padding: '10px 10px 10px 10px', color: 'white'}}>
                                                                {value}
                                                        </li>
                                                        <li style={{width: '20%', padding: '10px 10px 10px 0px'}}>
                                                                <button style={{backgroundColor: '#00897B', border: 'none', color: 'black', cursor: 'pointer'}} onClick={(e) => {
                                                                    e.preventDefault();
                                                                    removeKeyword(value);
                                                                }}>
                                                                    X
                                                                </button>
                                                        </li>
                                                        </ul>
                                                    </li>
                                                )
                                            })
                                        }
                                        </ul>
                                    </div>
                                    <br/><br/><br/>
                                    <h2>
                                        Image
                                    </h2>
                                        <TextBox setText={setImage} id="event-create-picture" value={image}/>
                                        <br/><br/><br/>
                                    
                                    {
                                        (typeof user.role !== "undefined" && user.role === "administrateur") && (
                                            <>
                                            <br/><br/><br/>
                                            <h2>
                                                Gestionnaires Externes
                                            </h2>
                                            <div>
                                            <input type="text" id="gestexternes" style={{
                                                width: '100%',
                                                height: '60px',
                                                border: `3px solid #707776`,
                                                borderRadius: '15px',
                                                fontSize: '25px', 
                                                paddingLeft: '20px',
                                                boxSizing: 'border-box'
                                            }} onChange={(e) => {
                                                if(e.target.value === "" || (typeof ListeGestionnaireExternes === "undefined")){
                                                    setPropositionsExternes([]);
                                                    setUpdate(update+1);
                                                } else {
                                                    let propositions = [];
                                                    let value = e.target.value;
                                                    ListeGestionnaireExternes.forEach((gestionnaire) => {
                                                        let chaine = gestionnaire.Nom + " " + gestionnaire.Prenom + " " + gestionnaire.Mail + " " + gestionnaire.Entreprise + " " + gestionnaire.Metier;
                                                        if(chaine.toUpperCase().includes(value.toUpperCase())){
                                                            if(propositions.length < 5){
                                                                let obj = gestionnaireExterne.findIndex(element => element.id === gestionnaire.id)
                                                                if(obj === -1){
                                                                    propositions.push(gestionnaire);
                                                                }
                                                            }
                                                        }
                                                    })
                                                    setPropositionsExternes(propositions);
                                                    setUpdate(update + 1)
                                                }
                                            }} />
                                            <ul style={{ listStyleType: 'none', textAlign: 'left', padding: '0', marginTop: '-5px'}}>
                                            {
                                                propositionsExternes.map((gest) => {
                                                    return (
                                                        <li style={{padding: '20px 10px 20px 20px', borderBottom: '1px solid black', borderLeft: '1px solid black', borderRight: '1px solid black', cursor: 'pointer'}} onClick={(e) => {
                                                            e.preventDefault();
                                                            addGestionnaire(gestionnaireExterne, setGestionnaireExterne, gest);
                                                            let propositions = [];
                                                            let value = document.getElementById("gestexternes").value;
                                                            ListeGestionnaireExternes.forEach((gestionnaire) => {
                                                                let chaine = gestionnaire.Nom + " " + gestionnaire.Prenom + " " + gestionnaire.Mail + " " + gestionnaire.Entreprise + " " + gestionnaire.Metier;
                                                                if(chaine.toUpperCase().includes(value.toUpperCase())){
                                                                    if(propositions.length < 5){
                                                                        let obj = gestionnaireExterne.findIndex(element => element.id === gestionnaire.id)
                                                                        if(obj === -1){
                                                                            propositions.push(gestionnaire);
                                                                        }
                                                                    }
                                                                }
                                                            })
                                                            setPropositionsExternes(propositions);
                                                            setUpdate(update + 1)
                                                        }}>
                                                            <b>{gest.Nom} {gest.Prenom},</b> {gest.Metier} chez {gest.Entreprise}
                                                        </li>
                                                    )
                                                })
                                            }
                                            </ul>
                                            <br/>
                                            <ul style={{listStyleType: 'none', width: '100%', padding: '0', textAlign: 'left', display: 'flex', flexDirection: 'row', overflow: 'scroll', overflowY: 'hidden'}}>
                                                {
                                                    gestionnaireExterne.map((gest, index) => {
                                                        return (
                                                            <li key={`gestexternes-${index}`} style={{backgroundColor: '#00897B', textAlign: 'left' ,marginLeft: '10px', marginRight: '10px', fontSize: '17px'}}>
                                                                <ul style={{display: 'flex', flexDirection: 'row', listStyleType: 'none', width: '100%', padding: '0px'}}>
                                                                <li style={{textAlign: 'center', width: '80%', fontSize: '18px', padding: '10px 10px 10px 10px', color: 'white'}}>
                                                                        {gest.Nom} {gest.Prenom}
                                                                </li>
                                                                <li style={{width: '20%', padding: '10px 10px 10px 0px'}}>
                                                                        <button style={{backgroundColor: '#00897B', border: 'none', color: 'black', cursor: 'pointer'}} onClick={(e) => {
                                                                            e.preventDefault();
                                                                            removeGestionnaire(gestionnaireExterne, setGestionnaireExterne, gest);
                                                                        }}>
                                                                            X
                                                                        </button>
                                                                </li>
                                                                </ul>
                                                            </li>
                                                        )
                                                    })
                                                }
                                            </ul>
                                            </div>
                                            <br/><br/><br/>
                                            <h2>
                                                Gestionnaires Internes
                                            </h2>
                                            <div>
                                            <input type="text" id="gestIApau" style={{
                                                width: '100%',
                                                height: '60px',
                                                border: `3px solid #707776`,
                                                borderRadius: '15px',
                                                fontSize: '25px', 
                                                paddingLeft: '20px',
                                                boxSizing: 'border-box'
                                            }} onChange={(e) => {
                                                if(e.target.value === "" || (typeof ListeGestionnaireExternes === "undefined")){
                                                    setPropositionsInternes([]);
                                                    setUpdate(update+1);
                                                } else {
                                                    let propositions = [];
                                                    let value = e.target.value;
                                                    ListeGestionnairesInternes.forEach((gestionnaire) => {
                                                        let chaine = gestionnaire.Nom + " " + gestionnaire.Prenom + " " + gestionnaire.Mail + " " + gestionnaire.Entreprise + " " + gestionnaire.Metier;
                                                        if(chaine.toUpperCase().includes(value.toUpperCase())){
                                                            if(propositions.length < 5){
                                                                let obj = gestionnaireIA.findIndex(element => element.id === gestionnaire.id)
                                                                if(obj === -1){
                                                                    propositions.push(gestionnaire);
                                                                }
                                                            }
                                                        }
                                                    })
                                                    setPropositionsInternes(propositions);
                                                    setUpdate(update + 1)
                                                }
                                            }} />
                                            <ul style={{ listStyleType: 'none', textAlign: 'left', padding: '0', marginTop: '-5px'}}>
                                            {
                                                propositionsInternes.map((gest) => {
                                                    return (
                                                        <li style={{padding: '20px 10px 20px 20px', borderBottom: '1px solid black', borderLeft: '1px solid black', borderRight: '1px solid black', cursor: 'pointer'}} onClick={(e) => {
                                                            e.preventDefault();
                                                            addGestionnaire(gestionnaireIA, setGestionnaireIA, gest);
                                                            let propositions = [];
                                                            let value = document.getElementById("gestIApau").value;
                                                            ListeGestionnairesInternes.forEach((gestionnaire) => {
                                                                let chaine = gestionnaire.Nom + " " + gestionnaire.Prenom + " " + gestionnaire.Mail + " " + gestionnaire.Entreprise + " " + gestionnaire.Metier;
                                                                if(chaine.toUpperCase().includes(value.toUpperCase())){
                                                                    if(propositions.length < 5){
                                                                        let obj = gestionnaireIA.findIndex(element => element.id === gestionnaire.id)
                                                                        if(obj === -1){
                                                                            propositions.push(gestionnaire);
                                                                        }
                                                                    }
                                                                }
                                                            })
                                                            setPropositionsInternes(propositions);
                                                            setUpdate(update + 1)
                                                        }}>
                                                            <b>{gest.Nom} {gest.Prenom},</b> {gest.Metier} chez {gest.Entreprise}
                                                        </li>
                                                    )
                                                })
                                            }
                                            </ul>
                                            <br/>
                                            <ul style={{listStyleType: 'none', width: '100%', padding: '0', textAlign: 'left', display: 'flex', flexDirection: 'row', overflow: 'scroll', overflowY: 'hidden'}}>
                                                {
                                                    gestionnaireIA.map((gest, index) => {
                                                        return (
                                                            <li key={`gestinternes-${index}`} style={{backgroundColor: '#00897B', textAlign: 'left' ,marginLeft: '10px', marginRight: '10px', fontSize: '17px'}}>
                                                                <ul style={{display: 'flex', flexDirection: 'row', listStyleType: 'none', width: '100%', padding: '0px'}}>
                                                                <li style={{textAlign: 'center', width: '80%', fontSize: '18px', padding: '10px 10px 10px 10px', color: 'white'}}>
                                                                        {gest.Nom} {gest.Prenom}
                                                                </li>
                                                                <li style={{width: '20%', padding: '10px 10px 10px 0px'}}>
                                                                        <button style={{backgroundColor: '#00897B', border: 'none', color: 'black', cursor: 'pointer'}} onClick={(e) => {
                                                                            e.preventDefault();
                                                                            removeGestionnaire(gestionnaireIA, setGestionnaireIA, gest);
                                                                        }}>
                                                                            X
                                                                        </button>
                                                                </li>
                                                                </ul>
                                                            </li>
                                                        )
                                                    })
                                                }
                                            </ul>
                                            </div>
                                            </>
                                        )
                                    }
                                    <br/><br/><br/>
                                    <h2>
                                        Ressources
                                    </h2>
                                    <div>
                                        {
                                            ressources.length === 0 && (
                                                <p>
                                                    Il n'y a pas de ressources pour le moment.
                                                </p>
                                            )
                                        }
                                        {
                                            ressources.map((ressource, index) => {
                                                return (
                                                    <>
                                                        {
                                                            (!ressource.shown) && (
                                                                <div key={ressource-`${index}`} style={{textAlign: 'left', borderBottom: '1px solid black'}}>
                                                                    <h2 style={{cursor: 'pointer'}} onClick={(e) => {
                                                                        e.preventDefault();
                                                                        handleView(index);
                                                                    }}>
                                                                        {"> "} {(ressource.nom !== "") ? ressource.nom : "Nouvelle ressource"}
                                                                    </h2>
                                                                    <div style={{paddingLeft: '50px'}}>
                                                                        <button style={{position: 'relative', top: '-45px', right: '-80%', backgroundColor: 'white', borderRadius: '8px', fontSize: '20px', borderColor: 'red', color: 'red', cursor: 'pointer'}} onClick={(e) => {
                                                                            e.preventDefault();
                                                                            deleteRessource(index);
                                                                        }}>
                                                                            Supprimer
                                                                        </button>
                                                                    </div>
                                                                </div>
                                                            )
                                                        }
                                                        {
                                                            (ressource.shown) && (
                                                                <div style={{textAlign: 'left', paddingBottom: '20px', borderBottom: '1px solid black'}}>
                                                                    <h2 style={{cursor: 'pointer'}} onClick={(e) => {
                                                                        e.preventDefault();
                                                                        handleView(index);
                                                                    }}>
                                                                        {"⌄ "} {(ressource.nom !== "") ? ressource.nom : "Nouvelle ressource"}
                                                                    </h2>
                                                                    <div style={{paddingLeft: '50px'}}>
                                                                        <button style={{position: 'relative', top: '-45px', right: '-80%', backgroundColor: 'white', borderRadius: '8px', fontSize: '20px', borderColor: 'red', color: 'red', cursor: 'pointer'}} onClick={(e) => {
                                                                            e.preventDefault();
                                                                            deleteRessource(index);
                                                                        }}>
                                                                            Supprimer
                                                                        </button>
                                                                        <p style={{fontSize: '17px', verticalAlign: 'middle'}}>
                                                                            
                                                                            {
                                                                                ressource.consultation && (
                                                                                    <>
                                                                                        <input style={{width: '20px'}} type="checkbox" defaultChecked onClick={(e) => {
                                                                                            editRessource(index, "CONSULTATION", null);
                                                                                        }}/> Une fois cochée, il faut participer au projet pour la consulter
                                                                                    </>
                                                                                )
                                                                            }
                                                                            {
                                                                                !ressource.consultation && (
                                                                                    <>
                                                                                        <input style={{width: '20px'}} type="checkbox" onClick={(e) => {
                                                                                            editRessource(index, "CONSULTATION", null);
                                                                                        }}/> Une fois cochée, il faut participer au projet pour la consulter
                                                                                    </>
                                                                                )
                                                                            }
                                                                            
                                                                        </p>
                                                                        <p style={{fontSize: '20px'}}>
                                                                            Nom
                                                                        </p>
                                                                        <input style={{height: '30px', paddingLeft: '5px', fontSize: '20px', borderRadius: '5px', width: '500px'}} value={ressource.nom} onChange={(e) => {
                                                                            editRessource(index, "NOM", e.target.value);
                                                                        }}/>
                                                                        <p style={{fontSize: '20px'}}>
                                                                            Consultation
                                                                        </p>
                                                                        <input id="project-date" type="datetime-local" style={{height: '30px', fontSize: '17px', borderRadius: '5px'}} value={(new Date(ressource.publication)).getFullYear() + "-" + base10value((new Date(ressource.publication)).getMonth()+1) + "-" + base10value((new Date(ressource.publication)).getDate()) + "T" + base10value((new Date(ressource.publication)).getHours()) + ":" + base10value((new Date(ressource.publication)).getMinutes())} onChange={(e) => {
                                                                            
                                                                            let dateValue = new Date(e.target.value);
                                                                            let jour = dateValue.getDate();
                                                                            let mois = dateValue.getMonth()+1;
                                                                            let annee = dateValue.getFullYear();
                                                                            let minutes = dateValue.getMinutes();
                                                                            let heures = dateValue.getHours();

                                                                            let month = (mois < 10) ? "0" + mois : mois;
                                                                            let day = (jour < 10) ? "0" + jour : jour;
                                                                            let hour = (heures < 10) ? "0" + heures : heures;
                                                                            let min = (minutes < 10) ? "0" + minutes : minutes;
                                                                            let dateFormat = annee + "-" + month + "-" + day + "T" + hour + ":" + min + ":00.000Z";
                                                                                    
                                                                            let date = new Date(dateFormat);
                                                                            date.setHours(date.getHours() - 2);
                                                                            editRessource(index, "PUBLICATION", date);
                                                                        }}/>
                                                                        
                                                                        <p style={{fontSize: '20px'}}>
                                                                            Type
                                                                        </p>
                                                                        <select style={{height: '30px', fontSize: '17px', borderRadius: '5px', width: '150px'}} onChange={(e) => {
                                                                            editRessource(index, "TYPE", e.target.value);
                                                                        }}>
                                                                            {
                                                                                ressource.type === "lien" && (
                                                                                    <option value="lien">lien</option>
                                                                                )
                                                                            }
                                                                            {
                                                                                ressource.type === "drive" && (
                                                                                    <option value="drive">drive</option>
                                                                                )
                                                                            }
                                                                            {
                                                                                ressource.type === "vidéo" && (
                                                                                    <option value="vidéo">vidéo</option>
                                                                                )
                                                                            }
                                                                            {
                                                                                ressource.type === "téléchargement" && (
                                                                                    <option value="téléchargement">téléchargement</option>
                                                                                )
                                                                            }

                                                                            

                                                                            {
                                                                                ressource.type === "lien" && (
                                                                                    <>
                                                                                        <option value="drive">drive</option>
                                                                                        <option value="vidéo">vidéo</option>
                                                                                        <option value="téléchargement">téléchargement</option>
                                                                                    </>
                                                                                )
                                                                            }
                                                                            {
                                                                                ressource.type === "drive" && (
                                                                                    <>
                                                                                        <option value="lien">lien</option>
                                                                                        <option value="vidéo">vidéo</option>
                                                                                        <option value="téléchargement">téléchargement</option>
                                                                                    </>
                                                                                )
                                                                            }
                                                                            {
                                                                                ressource.type === "vidéo" && (
                                                                                    <>
                                                                                        <option value="lien">lien</option>
                                                                                        <option value="drive">drive</option>
                                                                                        <option value="téléchargement">téléchargement</option>
                                                                                    </>
                                                                                )
                                                                            }
                                                                            {
                                                                                ressource.type === "téléchargement" && (
                                                                                    <>
                                                                                        <option value="lien">lien</option>
                                                                                        <option value="drive">drive</option>
                                                                                        <option value="vidéo">vidéo</option>
                                                                                    </>
                                                                                )
                                                                            }
                                                                        </select>
                                                                        <p style={{fontSize: '20px'}}>
                                                                            Lien
                                                                        </p>
                                                                        <input style={{height: '30px', paddingLeft: '5px', fontSize: '20px', borderRadius: '5px', width: '500px'}} value={ressource.lien} onChange={(e) => {
                                                                            editRessource(index, "LIEN", e.target.value);
                                                                        }}/>
                                                                        <p style={{fontSize: '20px'}}>
                                                                            Description
                                                                        </p>
                                                                        {/*<input style={{height: '30px', paddingLeft: '5px', fontSize: '20px', borderRadius: '5px', width: '500px'}}  value={ressource.description} onChange={(e) => {
                                                                            editRessource(index, "DESCRIPTION", e.target.value);
                                                                        }}/>*/}

                                                                        <textarea style={{height: '200px', paddingLeft: '5px', fontSize: '20px', borderRadius: '5px', width: '500px', resize: 'none'}} value={ressource.description} onChange={(e) => {
                                                                            editRessource(index, "DESCRIPTION", e.target.value);
                                                                        }}/>

                                                                        <br/>
                                                                    </div>
                                                                    {ressource.shown}
                                                                </div>
                                                            )
                                                        }
                                                    </>
                                                )
                                            })
                                        }
                                        <button style={{ marginTop: '20px' ,padding: '10px 15px 10px 15px', backgroundColor: 'white', borderRadius: '25px', cursor: 'pointer'}} onClick={(e) => {
                                            e.preventDefault();
                                            addRessource();
                                        }}>
                                            +
                                        </button>
                                    </div>
                                    <div style={{height: '200px'}}>

                                    </div>
                                </div>
                                </li>
                                <li style={{width: '150px'}}>
                                    <div style={{width: '150px', position: 'fixed'}}>
                                        <ButtonGreen toRun={async (e) => {
                                            e.preventDefault();
                                            let datas = ressources;
                                            let Ressources = [];
                                            datas.forEach((ressource) => {
                                                let res = {
                                                    nom: ressource.nom,
                                                    type: ressource.type,
                                                    lien: ressource.lien,
                                                    description: ressource.description,
                                                    consultation: (ressource.consultation) ? 'privé' : 'public',
                                                    publication: new Date(ressource.publication),
                                                }
                                                Ressources.push(res);
                                            })

                                            let gestExt = [];
                                            gestionnaireExterne.forEach((gest) => {
                                                gestExt.push(gest.id);
                                            })

                                            let gestIA = [];
                                            gestionnaireIA.forEach((gest) => {
                                                gestIA.push(gest.id);
                                            });

                                            const body = JSON.stringify({
                                                nom,
                                                lienSujet,
                                                recompense,
                                                description,
                                                image,
                                                motClefs: keyword,
                                                Ressources,
                                                gestionnaireExterne: gestExt,
                                                gestionnaireIA: gestIA,
                                                template
                                            });
                                            try {
                                                const response = await fetch(getApiUrl() + "projet/creerProjets", {
                                                    method: "POST",
                                                    headers: {
                                                        Authorization: "Bearer " + user.token,
                                                        "Content-Type": "application/json; charset=utf-8",
                                                    },
                                                    body: body,
                                                });
                                                const json = await response.json();
                                                let error = "Erreur non indiquée par le serveur.";
                                                if(json.errors && Array.isArray(json.errors) && json.errors.length > 0) {
                                                    error = json.errors[0].msg;
                                                }
                                                if(!response.ok){
                                                    Swal.fire({
                                                        icon: 'error',
                                                        title: 'Impossible de créer le projet !',
                                                        text: error
                                                    })
                                                } else {
                                                    setRetour(true);
                                                }
                                            } catch(e) {
                                                throwErrorOnFetch(e);
                                            }
                                        }}>
                                            Enregistrer
                                        </ButtonGreen>
                                    </div>
                                </li>
                            </ul>
                            
                        </PageEspacePerso>
                    </>
                )
            }
        </>
    )
}