import getApiUrl from '../../Constantes/apiurl';
import { throwErrorOnFetch } from '../../Utils/Errors';
import accept from '../../assets/Accept.png';
import deny from '../../assets/Deny.png';

export default function TeamDemandeUser({user, capitaine, update, setUpdate, token, teamId}){

    const { id, pseudo, message} = user;
    return (
        <div style={{color: 'black', border: '1px solid black', borderRadius: '25px', width: '85%', padding: '20px 20px', marginBottom: '20px', textAlign: 'justify', marginLeft: window.innerWidth < 800 ? '8px' : '0px'}}>
            <ul style={{listStyleType: 'none', padding: '0', display: 'flex', flexDirection: 'row'}}>
                <li style={{width: `calc(100% - 70px)`}}>
                     <span href={`/users/${id}`} style={{color: 'blue', textDecoration: 'none', cursor: 'pointer', textAlign: 'justify'}}>
                        <b>{pseudo}</b>
                    </span>
                    <p style={{paddingRight: '40px', paddingLeft: '10px', fontSize: '17px'}}>
                        {message === "" ? "Aucune description" : message}
                    </p>
                </li>
                {
                    (capitaine === true) && (
                        <li style={{width: '70px'}}>
                            <ul style={{listStyleType: 'none', padding: '0', display: 'flex', flexDirection: 'column'}}>
                                <li style={{height: '50%', cursor: 'pointer'}}>
                                    <img src={accept} alt="AccepterImage" style={{width: '40px', height: '40px', paddingTop: '15px', paddingBottom: '15px'}} onClick={async (e) => {
                                        e.preventDefault();
                                        const bodyRequest = JSON.stringify({ id });
                                        try {
                                            const response = await fetch(getApiUrl() + 'teams/' + teamId + "/AccepterDemande", {
                                                method: 'POST',
                                                headers: {
                                                    Authorization: "Bearer " + token,
                                                    'Content-Type': 'application/json'
                                                },
                                                body: bodyRequest
                                            });
                                            if(response.ok){
                                                setUpdate(update+1);
                                            }
                                        } catch(e) {
                                            throwErrorOnFetch(e);
                                        }
                                    }} />
                                </li>
                                <li style={{height: '50%', cursor: 'pointer'}}>
                                    <img src={deny} alt="RefuserImage" style={{width: '40px', height: '40px', paddingTop: '15px', paddingBottom: '15px'}} onClick={async (e) => {
                                        e.preventDefault();
                                        const bodyRequest = JSON.stringify({ id });
                                        try {
                                            const response = await fetch(getApiUrl() + 'teams/' + teamId + "/declinerDemande", {
                                                method: 'POST',
                                                headers: {
                                                    Authorization: "Bearer " + token,
                                                    'Content-Type': 'application/json'
                                                },
                                                body: bodyRequest
                                            });
                                            if(response.ok){
                                                setUpdate(update+1);
                                            }
                                        } catch(e) {
                                            throwErrorOnFetch(e);
                                        }
                                    }} />
                                </li>
                            </ul> 
                        </li>
                    )
                }
                {
                    (capitaine === false) && (
                        <li style={{width: '70px'}}>
                            <ul style={{listStyleType: 'none', padding: '0', display: 'flex', flexDirection: 'column'}}>
                                <li style={{height: '50%', cursor: 'pointer'}}>
                                    <img src={accept} alt="AccepterImage" style={{width: '40px', height: '40px', paddingTop: '15px', paddingBottom: '15px', filter: 'grayscale(100%)'}} onClick={(e) => {
                                        e.preventDefault();
                                    }} />
                                </li>
                                <li style={{height: '50%', cursor: 'pointer'}}>
                                    <img src={deny} alt="AccepterImage" style={{width: '40px', height: '40px', paddingTop: '15px', paddingBottom: '15px', filter: 'grayscale(100%)'}} onClick={(e) => {
                                        e.preventDefault();
                                    }} />
                                </li>
                            </ul> 
                        </li>
                    )
                }
            </ul>
        </div>
    )
}