import "./SearchBar.css";

import '../../style/App.css';
import '../../fonts/Inter-Light.ttf'

import loupe from "../../assets/loupe.png";

export default function SearchBar({changeFiltre, message}){

    const handleChange = (e) => {
        changeFiltre(e.target.value);
    }

    let valueMessage = (typeof message !== "undefined") ? message : "Rechercher un utilisateur";

    return (
        <div style={{height: '50px', borderRadius: '15px', border: '2px solid black', textAlign: 'center', fontSize: '20px'}}>
            <ul style={{display: 'flex', flexDirection: 'row', listStyleType: 'none', padding: '0'}}>
                <li style={{width: '85%'}}>
                    <input id="searchbar" type="text" name="search" placeholder={valueMessage} className="SearchBar" style={{fontFamily: 'Light'}} onChange={handleChange}/>
                </li>
                <li style={{width: '15%'}}>
                    <img src={loupe} alt="" style={{height: '30px', marginTop: '10px'}} />
                </li>
            </ul>
        </div>
    )
}