
import { useEffect, useState } from "react";
import bannerPau from "../../../assets/pyrenees.jpg";
import ListeTeams from "../../../components/ListeTeams";

import PageEspacePerso from "../../PageEspacePersoType";
import getApiUrl from "../../../Constantes/apiurl";
import { useAuthContext } from "../../../Contexts/AuthContext";
import { visitUrl } from "../../../Contexts/ReturnManager";
import { throwErrorOnFetch } from "../../../Utils/Errors";

export default function PageListeEquipe(){

    let {user} = useAuthContext();
    let userStorage = localStorage.getItem('user');
    if(userStorage){ 
        user = JSON.parse(userStorage); 
    }
    const [equipesPassees, setEquipesPassees] = useState([]);
    const [equipesCourrantes, setEquipeCourrantes] = useState([]);

    visitUrl(window.location.pathname);
    
    const [update, setUpdate] = useState(0);

    // eslint-disable-next-line
    const [width, setWidth] = useState(window.innerWidth);

    useEffect(() => {
        const changeWidth = () => {
            setWidth(window.innerWidth);
        }

        window.addEventListener('resize', changeWidth);

        return () => {
            window.removeEventListener('resize', changeWidth);
        }
    }, [])

    const [loading, setLoading] = useState(true);

    useEffect(() => {
        async function fetchDatas(){
            try {
                const response = await fetch(getApiUrl() + 'teams/mesEquipes', {
                    method: 'GET',
                    headers: {
                        Authorization: "Bearer " + user.token,
                        'Content-Type': 'application/json'
                    }
                })
                const json = await response.json();
                setLoading(false);
                if(response.ok){
                    let newTeams = [];
                    let oldTeams = [];
                    json.equipe.forEach(team => {
                        if(team.fini === true){
                            oldTeams.push(team);
                        } else {
                            newTeams.push(team);
                        }
                    });
                    setEquipeCourrantes(newTeams);
                    setEquipesPassees(oldTeams);
                }
            } catch(e) {
                throwErrorOnFetch(e);
            }
        }

        fetchDatas();
    // eslint-disable-next-line
    }, [update])

    return (
        <PageEspacePerso loading={loading}>
            
            {
                window.innerWidth > 800 && (
                    <>
                        <img src={bannerPau} className="BannerTeamVision" alt=""/>
                        <h1 style={{fontFamily: 'Light', color: 'white', position: 'absolute',top: '80px', fontSize: '55px', textAlign: 'center', width: '96%', textShadow: '0 1px 0 black, 0 -1px 0 black'}}>
                            Vos équipes
                        </h1>
                    </>
                )
            }
            <div style={{marginLeft: '10%', width: '80%', marginTop: '100px', fontFamily: 'Light', overflowX: 'hidden'}}>
                <h3 style={{fontSize: '27px', color: '#00897B'}}>
                    {"équipes actuelles".toUpperCase()}
                </h3>
                <ListeTeams datas={equipesCourrantes} size={3} passed={false} update={update} setUpdate={setUpdate}/>
                <br/><br/>
                <h3 style={{fontSize: '27px', color: '#00897B'}}>
                    {"anciennes équipes".toUpperCase()}
                </h3>
                <ListeTeams datas={equipesPassees} size={7} passed={true} update={update} setUpdate={setUpdate}/>
            </div>
        </PageEspacePerso>
    )
}