import React, {useState, useContext, useEffect} from "react";
import getThisServerUrl from "../Constantes/thisurl";
const AuthContext = React.createContext()

const minConnexion = 60;

export function getMinConnexion(){
    return minConnexion;
}

export function useAuthContext(){
    return useContext(AuthContext)
}

export function AuthProvider(props){

    const [user, setUser] = useState(null)
    const [isLoggedIn, setIsLoggedIn] = useState(false)
    const [timeFin, setTimeFin] = useState(-1);

    useEffect(() => {
        const storedUser = localStorage.getItem("user");
        const now = (new Date(Date.now())).getTime();
        if (storedUser) {
            if(JSON.parse(storedUser).end < now){
                logout();
            } else {
                setUser(JSON.parse(storedUser));
                setIsLoggedIn(true);
            }
        }
      // eslint-disable-next-line
      }, [setTimeFin]);
    
    const logout = () => {
        window.location.replace(getThisServerUrl());
        setUser(null);
        setIsLoggedIn(false);
        const storedUser = localStorage.getItem("user");
        if (storedUser) {
            localStorage.removeItem("user");
        }
    }

    const updateTimeConnexion = () => {
        const storedUser = localStorage.getItem("user");
        const now = (new Date(Date.now())).getTime();
        if (storedUser) {
            let user = JSON.parse(storedUser);
            if(user.end < now){
                logout();
                window.location.replace(getThisServerUrl());
            } else {
                let end = now + 60000*minConnexion;
                if(user.rememberMe === true){
                    end = now + 13281408000000;
                }
                user.end = end;
                localStorage.setItem("user", JSON.stringify(user));
            }
        }
    }

    const value = {
        user, setUser, isLoggedIn, setIsLoggedIn, logout, timeFin, updateTimeConnexion
    }

    return (
        <AuthContext.Provider value = {value}>
            {props.children}
        </AuthContext.Provider>
    )

}