import { renderToString } from "react-dom/server";
import Swal from "sweetalert2";
import dateToString from "./DateUtils";
import getApiUrl from "../Constantes/apiurl";
import { throwErrorOnFetch } from "./Errors";

export default async function observation(idEquipe, user){
    Swal.fire({
    title: 'Chargement en cours...',
    html: 'Nous recherchons vos données...',
    didOpen: () => {
        Swal.showLoading()
    }})
    try {
        const response = await fetch(getApiUrl() + "teams/" + idEquipe + "/getAnnotation", {
            method: "GET",
            headers: {
                Authorization: "Bearer " + user.token,
                "Content-Type": "application/json; charset=utf-8",
            },
        });
        const json = await response.json();
        if(response.ok){
            let datas = json.annotations;
            datas.sort((a, b) => {
                let dateA = new Date(a.date);
                let dateB = new Date(b.date);
                if(dateA.getTime() > dateB.getTime()){
                    return -1;
                } else {
                    return 1;
                }
            })
            Swal.fire({
                title: 'Dernières observations:',
                html: renderToString(
                    <ul style={{width: '90%', marginLeft: '5%', fontSize: '22px', fontFamily: 'Light', listStyleType: 'none', padding: '0', display: 'flex', flexDirection: 'column'}}>
                        <li>
                            <ul style={{width: '100%', height: '50px', listStyleType: 'none', padding: '0', display: 'flex', flexDirection: 'row', border: '3px solid black', paddingTop: '20px', fontSize: '25px'}}>
                                <li style={{width: '20%', textAlign: 'center'}}>
                                    <b>Observateur</b>
                                </li>
                                <li style={{width: '30%', textAlign: 'center'}}>
                                    <b>Date d'observation</b>
                                </li>
                                <li style={{width: '50%', textAlign: 'center'}}>
                                    <b>Commentaire</b>
                                </li>
                            </ul>
                        </li>
                        {
                            Array.isArray(datas) && datas.map((comm) => {
                                let {auteur, date, contenu } = comm;
                                return (
                                    <ul style={{width: '100%', height: 'auto', listStyleType: 'none', padding: '0', display: 'flex', flexDirection: 'row', paddingTop: '20px', borderLeft: '1px solid black', borderBottom: '1px solid black', borderRight: '1px solid black', paddingBottom: '20px'}}>
                                        <li style={{width: '20%', textAlign: 'center'}}>
                                            {auteur}
                                        </li>
                                        <li style={{width: '30%', textAlign: 'center'}}>
                                            {dateToString(date)}
                                        </li>
                                        <li style={{width: `calc(50% - 15px)`, textAlign: 'justify'}}>
                                            {contenu}
                                        </li>
                                    </ul>
                                )
                            })
                        }
                        {
                            Array.isArray(datas) && datas.length === 0 && (
                                <p style={{marginTop: '70px', marginBottom: '70px', color: 'red'}}>
                                    Il n'y a pas d'observation disponibles pour cette équipe pour le moment.
                                </p>
                            )
                        }
                    </ul>
                ),
                width: '2000px',
                didOpen: () => {
                    Swal.hideLoading()
                }
            })
        } else{
            Swal.fire({
                title: 'Erreur de chargement',
                text: 'Une erreur est survenue pendant le chargement de vos donnés.',
                icon: 'error',
                didOpen: () => {
                    Swal.hideLoading()
                }
            })
        }
    } catch(e) {
        throwErrorOnFetch(e);
    }
}

export async function ajouterCommantaire(IdEquipe, user){
    Swal.fire({
        title: 'Veuillez saisir un message à ajouter en tant que commentaire:',
        html: renderToString(
            <div>
                <textarea id="annotationValue" type="text" style={{
                    border: '1px solid #cecfcf',
                    outline: 'none',
                    height: '200px',
                    width: '900px',
                    fontSize: '22px',
                    fontFamily: 'Light',
                    resize: 'none'
                }}/>
            </div>
        ),
        width: '1000px',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Envoyer',
        cancelButtonText: 'Annuler',
        preConfirm: () => {
            let value = document.getElementById("annotationValue").value;
            if(value === ""){
                Swal.showValidationMessage("Le commentaire ne doit pas être vide.")
            }
        }
      }).then(async (result) => {
        if (result.isConfirmed) {
          let value = document.getElementById("annotationValue").value;
          if(value === ""){
            Swal.showValidationMessage("Le message ne doit pas être vide.")
          } else {
            try {
                const response = await fetch(getApiUrl() + 'teams/' + IdEquipe + "/annoter", {
                method: 'POST',
                headers: {
                    Authorization: "Bearer " + user.token,
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({contenu: value})
                })
                if(response.ok){
                Swal.fire(
                    'Commentaire enregistré !',
                    'Votre commentaire à bien été enregistré.',
                    'success'
                )} else {
                    Swal.showValidationMessage("Le commentaire n'a pas été sauvegardé à cause d'une erreur interne.")
                }
            } catch(e) {
                throwErrorOnFetch(e);
            }
          }
        }
      })
}