import { Navigate, useLocation } from "react-router-dom";
import { useAuthContext } from "../../Contexts/AuthContext"
import HeaderDashboard from "../../components/HeaderDashboard"

import '../../style/App.css';
import '../../fonts/Inter-Light.ttf'

import { useState, useEffect } from "react";
import Header from "../../components/Header";
import Loading from "../../components/LoadingComponent";

export default function PageEspacePerso({children, loading}){

    let {user} = useAuthContext();
    let userStorage = localStorage.getItem('user');
    if(userStorage){ 
        user = JSON.parse(userStorage); 
    }
    if(!loading){
        loading = false;
    }

    // eslint-disable-next-line
    const [size, setSize] = useState(window.innerWidth);

    useEffect(() => {
        const changeWidth = () => {
            setSize(window.innerWidth);
        }

        window.addEventListener('resize', changeWidth);

        return () => {
            window.removeEventListener('resize', changeWidth);
        }
    // eslint-disable-next-line
    }, [])

    let bypass = false;

    let widthCalc = (window.innerWidth > 800) ? window.innerWidth - 74 : window.innerWidth;
    let widthProp = widthCalc + 'px';
    let paddingLeftProp = (window.innerWidth > 800) ? '74px' : '0px';

    let loc = useLocation();
    let etudiantRedirection = false;
    if(loc.pathname === "/dashboard"){
        etudiantRedirection = true;
    }

    return (
        <div style={{width: `calc(100% + 16px)`, marginLeft: '-8px', marginTop: '-10px', overflowX: 'hidden'}}>
            {
                user && (user.role === "etudiant") && etudiantRedirection && (
                    <Navigate to="/myteams" replace={true} />
                )
            }
            {
                !user && bypass === false && (
                    <Navigate to="/connexion" replace={true} />
                )
            }
            {
                (user || bypass === true) && (
                    <>
                        {
                            user && (
                                <HeaderDashboard bypassConnexion={true}/>
                            )
                        }
                        {
                            !user && (
                                <Header/>
                            )
                        }
                        {
                            loading === true && (
                                <>
                                    <div style={{height: window.innerHeight + 'px'}}><Loading/></div>
                                </>
                            )
                        }
                        {
                            loading === false && (
                                <>
                                    {
                                        <div style={{width: widthProp, paddingLeft: paddingLeftProp, paddingTop: '0px', position: 'relative', top: '-8px', overflowX: 'hidden'}}>
                                            {children}
                                        </div>
                                    }
                                </>
                            )
                        }
                    </>
                )
            }
        </div>
    )
}