
import { Navigate, useParams } from "react-router-dom";
import bannerPau from "../../../assets/photoFont1.jpg";
import { useAuthContext } from "../../../Contexts/AuthContext";
import PageEspacePerso from "../../PageEspacePersoType";
import { useEffect, useState } from "react";
import WhiteNavButton from "../../../components/WhiteNavButton";
import TextBox from "../../../components/Champs/TextBox";
import ButtonGreen from "../../../components/ButtonGreen";
import getApiUrl from "../../../Constantes/apiurl";
import Swal from "sweetalert2";
import { getFirstError, throwErrorOnFetch } from "../../../Utils/Errors";
import Loading from "../../../components/LoadingComponent";

export default function EventModificationPage(){

    const {id} = useParams();
    let {user} = useAuthContext();
    let userStorage = localStorage.getItem('user');
    if(userStorage){ 
        user = JSON.parse(userStorage); 
    }
    let doesNotHavePermission = !user || user.role !== "administrateur";
    let hasPermission = !doesNotHavePermission;

    const [retour, setRetour] = useState(false);
    const [projets, setProjets] = useState({});

    function handleRetour(){
        setRetour(!retour);
    }


    const [filtreProjets, setFiltreProjets] = useState("");
    const [ListeProjets, setListeProjets] = useState([]);
    const [update, setUpdate] = useState(0);
    const [image, setImage] = useState("");
    const [Nom, setNom] = useState("");
    const [Description, setDescription] = useState("");
    const [Regles, setRegles] = useState([]);
    const [nombreMinEquipe, setNombreMinEquipe] = useState(3);
    const [nombreMaxEquipe, setNombreMaxEquipe] = useState(8);
    const [dateInscriptions, setDateInscriptions] = useState(new Date(Date.now()));
    const today = new Date()
    const tomorrow = new Date(today)
    tomorrow.setDate(tomorrow.getDate() + 1)
    const [dateDebut, setDateDebut] = useState(tomorrow);
    const tomorrow2 = new Date(today)
    tomorrow2.setDate(tomorrow2.getDate() + 2)
    const [dateFin, setDateFin] = useState(tomorrow2);
    const [typeEvent, setTypeEvent] = useState("challenge");
    const [loading, setLoading] = useState(true);


    useEffect(() => {
        async function fetchDatas() {
            try {
                const response = await fetch(getApiUrl() + "projet/", {
                    method: "GET",
                    headers: {
                        Authorization: "Bearer " + (user && typeof user.token !== "undefined" ? user.token : "null"),
                        "Content-Type": "application/json; charset=utf-8",
                    }
                });

                const json = await response.json();
                if(response.ok){
                    setProjets(json);
                }
                const response2 = await fetch(getApiUrl() + "events/" + id + "/infos", {
                    method: "GET",
                    headers: {
                        Authorization: "Bearer " + user.token,
                        "Content-Type": "application/json; charset=utf-8",
                    }
                });

                const json2 = await response2.json();
                setLoading(false);
                if(response2.ok){
                    setNom(json2.title);
                    setDateInscriptions(new Date(json2.date_creation));
                    setDateDebut(new Date(json2.date_debut));
                    setDateFin(new Date(json2.date_fin));
                    setListeProjets([]);
                    json2.projet.forEach((projet) => {
                        let newProjet = {
                            idProjet: projet.idprojet,
                            nom: projet.titre,
                            description: projet.description,
                            sujet: projet.sujet
                        };
                        addProject(newProjet);
                    })
                    setImage(json2.image);
                    setTypeEvent(json2.type_challenge);
                    setDescription(json2.description);
                    setNombreMinEquipe(json2.nbMinParEquipe);
                    setNombreMaxEquipe(json2.nbMaxParEquipe);
                    setRegles(json2.regles);
                } 
            } catch(e) {
                throwErrorOnFetch(e);
            }
        }
        fetchDatas();
        
    //eslint-disable-next-line
    }, [])
    function getStringValueOfDate(date){

        return date.getFullYear() + "-" + base10value(date.getMonth()+1) + "-" + base10value(date.getDate()) + "T" + base10value(date.getHours()) + ":" + base10value(date.getMinutes());

    }

    function base10value(val){
        return val < 10 ? "0" + val : val;
    }

    function addRegle(){
        let rules = Regles;
        rules.push({
            titre: '',
            contenu: ''
        })
        setRegles(rules);
    }

    function editRegle(i, title, content){
        let rule = {
            titre: title,
            contenu: content
        };
        let rules = Regles;
        rules[i] = rule;
        setRegles(rules);
    }

    function removeRegle(i){
        let rules = [];
        for(let j = 0; j < Regles.length; j++){
            if(i !== j){
                rules.push(Regles[j]);
            }
        }
        setRegles(rules);
    }

    function addProject(projet){
        let In = false;
        ListeProjets.forEach((p) => {
            if(p.idProjet === projet.idProjet){
                In = true;
            }
        })
        if(In === false){
            let projects = ListeProjets;
            projects.push(projet);
            setListeProjets(projects);
        }
    }

    function removeProject(projet){
        let liste = [];
        ListeProjets.forEach((p) => {
            if(p.idProjet !== projet.idProjet){
                liste.push(p);
            }
        })
        setListeProjets(liste);
    }

    function estDans(projet){
        let retour = false;
        ListeProjets.forEach((p) => {
            if(p.idProjet === projet.idProjet){
                retour = true;
            }
        })
        return retour;
    }

    let propositionsProjet = [];
    if(projets && projets.projets){
        projets.projets.forEach((projet) => {
            if(projet.nom.toUpperCase().includes(filtreProjets.toUpperCase()) && filtreProjets !== "" && !(estDans(projet)) && propositionsProjet.length < 5){
                propositionsProjet.push(projet);
            }
        })
    }
    return (
        <>
            {
                doesNotHavePermission && (
                <Navigate to="/" replace/>
                )
            }
            {
                hasPermission && retour && (
                    <Navigate to="/events" replace/>
                )
            }

            {
                loading === true && <Loading/>
            }
            {
                hasPermission && !retour && (
                    <>
                        <PageEspacePerso>
                            <img src={bannerPau} className="BannerAdminUser" alt=""/>
                            <h1 style={{fontFamily: 'Light', color: 'white', position: 'absolute',top: '80px', fontSize: '55px', textAlign: 'center', width: '96%', textShadow: '0 1px 0 black, 0 -1px 0 black'}}>
                                Modification d'un événement
                            </h1>
                            <ul style={{listStyleType: 'none', padding: '0', display: 'flex', flexDirection: 'row', marginLeft: '100px', marginRight: '100px'}}>
                                <li style={{width: '150px'}}>
                                    <div style={{width: '150px', position: 'fixed'}}>
                                        <WhiteNavButton onClick={(e) => {
                                            e.preventDefault();
                                            handleRetour();
                                        }}>
                                            Retour
                                        </WhiteNavButton>
                                    </div>
                                </li>
                                <li style={{width: `calc(100% - 300px)`}}>
                                {
                                    loading === false && (

                                    <div style={{fontFamily: 'Light', marginLeft: '15%', marginTop: '40px', textAlign: 'center', width: '70%'}}>
                                        <h2>
                                            Nom de l'événement
                                        </h2>
                                        <TextBox setText={setNom} id="event-create-nom" value={Nom}/>
                                        
                                        <br/><br/><br/>
                                        <h2>
                                            Description
                                        </h2>
                                        <textarea style={{width: '100%', fontFamily: 'Light', height: '350px', resize: 'none', border: `3px solid #707776`,
                                            borderRadius: '15px',
                                            fontSize: '19px', }} 
                                        onChange={(e) => {
                                            e.preventDefault();
                                            setDescription(e.target.value);
                                        }} value={Description}>
                                        </textarea>
                                        <br/><br/><br/>
                                        <h2>
                                            Image
                                        </h2>
                                        
                                        <TextBox setText={setImage} id="event-create-picture" value={image}/>
                                        <br/><br/><br/>
                                        <h2>
                                            Fin de l'événement
                                        </h2>
                                        <input id="project-date" type="datetime-local" style={{height: '40px', fontSize: '20px', borderRadius: '5px'}}  value={getStringValueOfDate(dateFin)} onChange={(e) => {
                                                                           
                                            let dateValue = new Date(e.target.value);
                                            let jour = dateValue.getDate();
                                            let mois = dateValue.getMonth()+1;
                                            let annee = dateValue.getFullYear();
                                            let minutes = dateValue.getMinutes();
                                            let heures = dateValue.getHours();

                                            let month = (mois < 10) ? "0" + mois : mois;
                                            let day = (jour < 10) ? "0" + jour : jour;
                                            let hour = (heures < 10) ? "0" + heures : heures;
                                            let min = (minutes < 10) ? "0" + minutes : minutes;
                                            let dateFormat = annee + "-" + month + "-" + day + "T" + hour + ":" + min + ":00.000Z";
                                                               
                                            let date = new Date(dateFormat);
                                            date.setHours(date.getHours() - 2);

                                            if(date.getTime() <= dateDebut.getTime()){
                                                Swal.fire({
                                                    icon: 'error',
                                                    title: 'Erreur dans le format de la date',
                                                    text: 'La date de fin doit-être après la date de début de l\'événement.'
                                                  })
                                            } else {
                                                setDateFin(date);
                                            }
                                        }}/>
                                        <br/><br/><br/>
                                        <h2>
                                            Début de l'événement
                                        </h2>
                                        <input id="project-date" type="datetime-local" style={{height: '40px', fontSize: '20px', borderRadius: '5px'}} value={getStringValueOfDate(dateDebut)}  onChange={(e) => {
                                                                           
                                            let dateValue = new Date(e.target.value);
                                            let jour = dateValue.getDate();
                                            let mois = dateValue.getMonth()+1;
                                            let annee = dateValue.getFullYear();
                                            let minutes = dateValue.getMinutes();
                                            let heures = dateValue.getHours();

                                            let month = (mois < 10) ? "0" + mois : mois;
                                            let day = (jour < 10) ? "0" + jour : jour;
                                            let hour = (heures < 10) ? "0" + heures : heures;
                                            let min = (minutes < 10) ? "0" + minutes : minutes;
                                            let dateFormat = annee + "-" + month + "-" + day + "T" + hour + ":" + min + ":00.000Z";
                                                               
                                            let date = new Date(dateFormat);
                                            date.setHours(date.getHours() - 2);

                                            if(date.getTime() <= dateInscriptions.getTime()){
                                                Swal.fire({
                                                    icon: 'error',
                                                    title: 'Erreur dans le format de la date',
                                                    text: 'La date de début doit-être après la date de début des inscriptions.'
                                                  })
                                            }

                                            else if(date.getTime() >= dateFin.getTime()){
                                                Swal.fire({
                                                    icon: 'error',
                                                    title: 'Erreur dans le format de la date',
                                                    text: 'La date de début doit-être avant la date de fin de l\'événement.'
                                                  })
                                            } else {
                                                setDateDebut(date);
                                            }
                                        }}/>
                                        <br/><br/><br/>
                                        <h2>
                                            Début des inscriptions
                                        </h2>
                                        <input id="project-date" type="datetime-local" style={{height: '40px', fontSize: '20px', borderRadius: '5px'}} value={getStringValueOfDate(dateInscriptions)} onChange={(e) => {
                                                                           
                                            let dateValue = new Date(e.target.value);
                                            let jour = dateValue.getDate();
                                            let mois = dateValue.getMonth()+1;
                                            let annee = dateValue.getFullYear();
                                            let minutes = dateValue.getMinutes();
                                            let heures = dateValue.getHours();

                                            let month = (mois < 10) ? "0" + mois : mois;
                                            let day = (jour < 10) ? "0" + jour : jour;
                                            let hour = (heures < 10) ? "0" + heures : heures;
                                            let min = (minutes < 10) ? "0" + minutes : minutes;
                                            let dateFormat = annee + "-" + month + "-" + day + "T" + hour + ":" + min + ":00.000Z";
                                                               
                                            let date = new Date(dateFormat);
                                            date.setHours(date.getHours() - 2);
                                            if(date.getTime() >= dateDebut.getTime()){
                                                Swal.fire({
                                                    icon: 'error',
                                                    title: 'Erreur dans le format de la date',
                                                    text: 'La date saisie est après la date de début de l\'événement'
                                                  })
                                            } else {
                                                setDateInscriptions(date);
                                            }
                                        }}/>
                                        <br/><br/><br/>
                                        <h2>
                                            Projets
                                        </h2>
                                        <input type="text" id="filtreProjets" style={{
                                                width: '100%',
                                                height: '60px',
                                                border: `3px solid #707776`,
                                                borderRadius: '15px',
                                                fontSize: '25px', 
                                                paddingLeft: '20px',
                                                boxSizing: 'border-box'
                                        }} onChange={(e) => {
                                            e.preventDefault();
                                            setFiltreProjets(e.target.value);
                                        }} value={filtreProjets}/>
                                        {
                                            typeEvent && ((typeEvent === "battle" && ListeProjets.length < 1) || (typeEvent === "challenge")) && (
                                                
                                                <ul style={{ listStyleType: 'none', textAlign: 'left', padding: '0', marginTop: '-5px'}}>
                                                    {
                                                        propositionsProjet.map((projet) => {
                                                            return (
                                                                <li style={{padding: '20px 10px 20px 20px', borderBottom: '1px solid black', borderLeft: '1px solid black', borderRight: '1px solid black', cursor: 'pointer'}} onClick={(e) => {
                                                                    e.preventDefault();
                                                                    addProject(projet);
                                                                    setFiltreProjets("");
                                                                    setUpdate(update+1)
                                                                }}>
                                                                    <b>{projet.nom}</b>
                                                                </li>
                                                            )
                                                        })
                                                    }
                                                    {
                                                        propositionsProjet && propositionsProjet.length < 1 && filtreProjets !== "" && (
                                                            <li style={{padding: '20px 10px 20px 20px', borderBottom: '1px solid black', borderLeft: '1px solid black', borderRight: '1px solid black', cursor: 'pointer'}}>
                                                                Aucun résultat pour votre recherche: {filtreProjets}
                                                            </li>
                                                        )
                                                    }
                                                </ul>
                                            )
                                        }
                                        {
                                            typeEvent && typeEvent === "battle" && ListeProjets.length >= 1 && (
                                                <h3 style={{color: 'red'}}>
                                                    Attention: une Data Battle ne peut pas contenir plus d'un projet !
                                                </h3>
                                            )
                                        }
                                        <br/>
                                        <ul style={{listStyleType: 'none', width: '100%', padding: '0', textAlign: 'left', display: 'flex', flexDirection: 'row', overflow: 'scroll', overflowY: 'hidden'}}>
                                                {
                                                    ListeProjets.map((projet, index) => {
                                                        return (
                                                            <li key={`PROJECT-${index}`} style={{backgroundColor: '#00897B', textAlign: 'left' ,marginLeft: '10px', marginRight: '10px', fontSize: '17px'}}>
                                                                <ul style={{display: 'flex', flexDirection: 'row', listStyleType: 'none', width: '100%', padding: '0px'}}>
                                                                    <li style={{textAlign: 'center', width: '80%', fontSize: '18px', padding: '10px 10px 10px 10px', color: 'white'}}>
                                                                            {projet.nom}
                                                                    </li>
                                                                    <li style={{width: '20%', padding: '10px 10px 10px 0px'}}>
                                                                            <button style={{backgroundColor: '#00897B', border: 'none', color: 'black', cursor: 'pointer'}} onClick={(e) => {
                                                                                e.preventDefault();
                                                                                removeProject(projet);
                                                                                setUpdate(update+1);
                                                                            }}>
                                                                                X
                                                                            </button>
                                                                    </li>
                                                                </ul>
                                                            </li>
                                                        )
                                                    })
                                                }
                                        </ul>
                                        <br/><br/><br/>
                                        <h2>
                                            Règles
                                        </h2>
                                                    <div style={{marginTop: '25px', border: '1px solid black'}}>
                                                        <p>
                                                            Règle n°1
                                                        </p>
                                                        <h3> Titre</h3>
                                                        <input type="text" style={{backgroundColor: 'rgb(209, 209, 209)', width: '70%', height: '30px', fontSize: '20px'}} value="Nombre de participants par équipe" onChange={(e) => {
                                                            e.preventDefault()
                                                        }}/>
                                                        <br/>
                                                        <br/>
                                                        <p>
                                                            De 
                                                            <input type="number" style={{width: '50px', height: '30px', marginLeft: '5px', marginRight: '5px', textAlign: 'center'}} value={nombreMinEquipe} onChange={(e) => {
                                                                let value = parseInt(e.target.value);
                                                                if(e.target.value === ""){
                                                                    value = 0;
                                                                }
                                                                let newValue = (value < 1) ? 1 : ((value > nombreMaxEquipe) ? nombreMaxEquipe : value);
                                                                setNombreMinEquipe(newValue);
                                                                setUpdate(update+1)
                                                            }}/>
                                                            à
                                                            <input type="number" style={{width: '50px', height: '30px', marginLeft: '5px', marginRight: '5px', textAlign: 'center'}} value={nombreMaxEquipe} onChange={(e) => {
                                                                let value = parseInt(e.target.value);
                                                                if(e.target.value === ""){
                                                                    value = 0;
                                                                }
                                                                let newValue = (value < nombreMinEquipe) ? nombreMinEquipe : value;
                                                                setNombreMaxEquipe(newValue);
                                                                setUpdate(update+1)
                                                            }}/>
                                                            étudiants
                                                        </p>
                                                        <br/>
                                                        <br/>
                                                    </div>
                                        {
                                            Regles.map((regle, index) => {
                                                return (
                                                    <div style={{marginTop: '25px', border: '1px solid black'}}>
                                                        <p>
                                                            Règle n°{(index+2)}
                                                        </p>
                                                        <h3> Titre</h3>
                                                        <input type="text" style={{width: '70%', height: '30px', fontSize: '20px'}} value={regle.titre} onChange={(e) => {
                                                            e.preventDefault()
                                                            editRegle(index, e.target.value, regle.contenu)
                                                            setUpdate(update+1);
                                                        }}/>
                                                        <h3> Contenu</h3>
                                                        <textarea style={{resize: 'none', width: '70%', height: '200px', fontSize: '15px'}} onChange={(e) => {
                                                            e.preventDefault();
                                                            editRegle(index, regle.titre, e.target.value);
                                                            setUpdate(update+1);
                                                        }}>
                                                            {regle.contenu}
                                                        </textarea>
                                                        <br/>
                                                        <br/>
                                                        <button onClick={(e) => {
                                                            e.preventDefault();
                                                            removeRegle(index);
                                                            setUpdate(update+1);
                                                        }} style={{width: '100px', color: 'red', borderColor: 'red', backgroundColor: 'white', height: '30px', borderRadius: '5px', cursor: 'pointer'}}>
                                                            SUPPRIMER
                                                        </button>
                                                        <br/>
                                                        <br/>
                                                        <br/>
                                                    </div>
                                                )
                                            })
                                        }
                                        <button style={{ marginTop: '20px' ,padding: '10px 15px 10px 15px', backgroundColor: 'white', borderRadius: '25px', cursor: 'pointer'}} onClick={(e) => {
                                            e.preventDefault();
                                            addRegle();
                                            setUpdate(update+1)
                                        }}>
                                            +
                                        </button>
                                        
                                    </div>)
                                }
                                </li>
                                <li style={{width: '150px'}}>
                                    <div style={{width: '150px', position: 'fixed'}}>
                                        <ButtonGreen toRun={async (e) => {
                                            e.preventDefault();
                                            let body = JSON.stringify({
                                                typeEvent: typeEvent,
                                                nom: Nom,
                                                inscription: dateInscriptions,
                                                debut: dateDebut,
                                                fin: dateFin,
                                                description: Description,
                                                image: image,
                                                nombreMinEquipe: nombreMinEquipe,
                                                nombreMaxEquipe: nombreMaxEquipe,
                                                messageFin: '',
                                                regles: Regles,
                                                projets: ListeProjets
                                            });
                                            try {
                                                const response = await fetch(getApiUrl() + "events/edit/" + id, {
                                                    method: "PATCH",
                                                    headers: {
                                                        Authorization: "Bearer " + user.token,
                                                        "Content-Type": "application/json; charset=utf-8",
                                                    },
                                                    body: body,
                                                });
                                                const json = await response.json();
                                                let error = getFirstError(json);
                                                if(!response.ok){
                                                    Swal.fire({
                                                        icon: 'error',
                                                        title: 'Impossible de modifier l\'évènement !',
                                                        text: error
                                                    })
                                                } else {
                                                    setRetour(true);
                                                }
                                            } catch(e) {
                                                throwErrorOnFetch(e);
                                            }
                                        }}>
                                            Enregistrer
                                        </ButtonGreen>
                                    </div>
                                </li>
                            </ul>
                            
                        </PageEspacePerso>
                    </>
                )
            }
        </>
    )
}