
import { Navigate, useParams } from "react-router-dom";
import bannerPau from "../../../assets/photoFont1.jpg";
import { useAuthContext } from "../../../Contexts/AuthContext";
import PageEspacePerso from "../../PageEspacePersoType";
import { useEffect, useState } from "react";
import WhiteNavButton from "../../../components/WhiteNavButton";
import getApiUrl from "../../../Constantes/apiurl";
import ListeTeamsSuperUser from "../../../components/ListeTeamsSuperUser";
import sendMessage from "../../../Utils/messages";
import { visitUrl } from "../../../Contexts/ReturnManager";
import { throwErrorOnFetch } from "../../../Utils/Errors";
import SplitButton from "../../../components/SplitButton";
import Swal from "sweetalert2";

export default function PageListeEquipesEvents(){

    const {id} = useParams();
    let {user} = useAuthContext();
    let userStorage = localStorage.getItem('user');
    if(userStorage){ 
        user = JSON.parse(userStorage); 
    }
    let doesNotHavePermission = !user || user.role !== "administrateur";
    let hasPermission = !doesNotHavePermission;
    visitUrl(window.location.pathname);
    const [retour, setRetour] = useState(false);

    function handleRetour(){
        setRetour(!retour);
    }

    const [update, setUpdate] = useState(0);

    const [Teams, setTeams] = useState([]);

    const [loading, setLoading] = useState(true);

    useEffect(() => {
        const fetchDatas = async () => {
            try {
                const response = await fetch(getApiUrl() + "events/" + id + "/teams", {
                    method: "GET",
                    headers: {
                        Authorization: "Bearer " + user.token,
                        "Content-Type": "application/json; charset=utf-8",
                    }
                });
                setLoading(false);
                const json = await response.json();
                if(response.ok && Array.isArray(json.equipes)){
                    setTeams(json.equipes);
                }
            } catch(e) {
                throwErrorOnFetch(e);
            }
        }
        fetchDatas();
    // eslint-disable-next-line
    }, [update]);

    return (
        <>
            {
                doesNotHavePermission && (
                <Navigate to="/" replace/>
                )
            }
            {
                hasPermission && retour && (
                    <Navigate to="/events" replace/>
                )
            }
            {
                hasPermission && !retour && (
                    <>
                        <PageEspacePerso loading={loading}>
                            <img src={bannerPau} className="BannerAdminUser" alt=""/>
                            <h1 style={{fontFamily: 'Light', color: 'white', position: 'absolute',top: '80px', fontSize: '55px', textAlign: 'center', width: '96%', textShadow: '0 1px 0 black, 0 -1px 0 black'}}>
                                Liste des équipes de l'évènement
                            </h1>
                            <ul style={{listStyleType: 'none', padding: '0px', display: 'flex', flexDirection: 'row', marginLeft: '100px', marginRight: '100px'}}>
                                <li style={{width: '150px'}}>
                                    <div style={{width: '150px'}}>
                                        <WhiteNavButton onClick={(e) => {
                                            e.preventDefault();
                                            handleRetour();
                                        }}>
                                            Retour
                                        </WhiteNavButton>
                                    </div>
                                </li>
                                <li style={{width: `calc(100% - 300px)`, marginTop: '150px'}}>
                                <div style={{marginLeft: '30px', marginBottom: '25px', fontSize: '22px', fontFamily: 'Light'}}>
                                        <b>Actions possibles:</b> <SplitButton options={[
                                            {
                                                name: 'Envoyer un message à toutes les équipes',
                                                fonction: () => {
                                                    sendMessage(user, "EVENTS", id);
                                                }
                                            },{
                                                name: 'Récupérer les données GITLAB',
                                                fonction: () => {
                                                    Swal.fire('Erreur', 'Fonctionnalité non implémentée...', 'error');
                                                }
                                            }
                                        ]} />
                                    </div>
                                    <ListeTeamsSuperUser datas={Teams} passed={true} update={update} setUpdate={setUpdate}/>
                                    
                                </li>
                            </ul>
                            
                        </PageEspacePerso>
                    </>
                )
            }
        </>
    )
}