
import { renderToString } from 'react-dom/server';
import Swal from "sweetalert2";
import getApiUrl from '../Constantes/apiurl';
import { throwErrorOnFetch } from './Errors';

export default async function sendMessage(user, type, id){
    Swal.fire({
        title: 'Choisir le message à envoyer',
        html: renderToString(
            <div>
                <textarea id="inputMessageGlobalSent" type="text" style={{
                    border: '1px solid #cecfcf',
                    outline: 'none',
                    height: '200px',
                    width: '900px',
                    fontSize: '22px',
                    fontFamily: 'Light',
                    resize: 'none'
                }}/>
            </div>
        ),
        width: '1000px',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Envoyer',
        cancelButtonText: 'Annuler',
        preConfirm: () => {
            let value = document.getElementById("inputMessageGlobalSent").value;
            if(value === ""){
                Swal.showValidationMessage("Le message ne doit pas être vide.")
            }
        }
      }).then(async (result) => {
        if (result.isConfirmed) {
          let value = document.getElementById("inputMessageGlobalSent").value;
          if(value === ""){
            Swal.showValidationMessage("Le message ne doit pas être vide.")
          } else {
            if(type === "EVENTS"){
              try {
                const response = await fetch(getApiUrl() + 'message/envoyerMessageEvenement/' + id, {
                  method: 'POST',
                  headers: {
                      Authorization: "Bearer " + user.token,
                      'Content-Type': 'application/json'
                  },
                  body: JSON.stringify({contenu: value})
                })
                if(response.ok){
                  Swal.fire(
                    'Message envoyé !',
                    'Votre message a été envoyé à toutes les équipes.',
                    'success'
                  )
                } else {
                  Swal.showValidationMessage("Le message n'a pas été envoyé à cause d'une erreur.")
                }
              } catch(e) {
                throwErrorOnFetch(e);
                Swal.showValidationMessage("Le message n'a pas été envoyé à cause d'une erreur.");
              }
            } else if (type === "PROJETS"){
              try {
                const response = await fetch(getApiUrl() + 'message/envoyerMessageProjet/' + id, {
                  method: 'POST',
                  headers: {
                      Authorization: "Bearer " + user.token,
                      'Content-Type': 'application/json'
                  },
                  body: JSON.stringify({contenu: value})
                })
                if(response.ok){
                  Swal.fire(
                    'Message envoyé !',
                    'Votre message a été envoyé à toutes les équipes.',
                    'success'
                  )
                } else {
                  Swal.showValidationMessage("Le message n'a pas été envoyé à cause d'une erreur.")
                }
              } catch(e){
                throwErrorOnFetch(e);
                Swal.showValidationMessage("Le message n'a pas été envoyé à cause d'une erreur.")
              }
            } else {
              Swal.showValidationMessage("Ce type de message :" + type + " n'existe pas !");
            }
          }
        }
      })
}