import Header from "../../components/Header";
//import Typewriter from "typewriter-effect";

import '../../style/App.css';
import '../../fonts/Inter-Light.ttf'
import ParticleDrawing from "../../components/Font404";

import "./404.css"

/**
 * @name Erreur404
 * @description This page will never appear in the website except if the page the user is looking for doesn't exists (anymore).
 * @returns The Error 404 page component
 */
function Erreur404(){
    let margintop = (window.innerWidth > 1000) ? '20%' : '300px';
    
    return (
        <div>
            <Header/>
            <div className="Font404">
                <ParticleDrawing/>
                <div style={{marginLeft: '5%' ,width: '90%', textAlign: 'center', marginTop: margintop, fontFamily: 'Light'}}>
                    <h1 style={{color: 'red', fontSize: '40px'}}>ERREUR 404</h1>
                    <div style={{
                        fontSize: '30px',
                        zIndex: '1',
                        color: 'white'
                    }}>
                        La page recherchée n'a pas été trouvée sur le serveur.
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Erreur404;