/**
 * @author Arthur Rimaudière
 * @function TextBox
 * @description Zone de texte par défaut du site.
 * @returns Component
 * @version 1.0.0
 * @example <Textbox setText={setValue} value={value} placeholder="Choix valeur" id="textbox" color="#707776" />
 * @param {Function} setText - State changer de la valeur value
 * @param {String} value - Variable d'état du composant parent
 * @param {String} placeholder - Texte affiché dans la zone de texte lorsqu'elle est vide.
 * @param {String} color - La couleur du composant
 * @param {String} id - L'id de la balise
 */
function TextBox({setText, id, placeholder, color, value}){

    let finalColor = "#707776";

    if(color){
        finalColor = color;
    }

    return ( 
        <input type="text" id={id} placeholder={placeholder} style={{
            width: '100%',
            height: '60px',
            border: `3px solid ${finalColor}`,
            borderRadius: '15px',
            fontSize: '18px', 
            paddingLeft: '20px',
            boxSizing: 'border-box'
        }} onChange={(e) => {
            if(typeof setText === "undefined"){ return; }
            setText(document.getElementById(id).value);
        }}
        value={value}/>
    )
}

export default TextBox;