import Header from "../../components/Header";
import LoginMessagePart from "../../components/LoginMessagePart";

import '../../style/App.css';
import '../../fonts/Inter-Light.ttf'
import MyNavButton from "../../components/MyNavButtonClair";
import TextBox from "../../components/Champs/TextBox";
import MdpBox from "../../components/Champs/MdpBox";

import WhiteNavButton from "../../components/WhiteNavButton";

import './Connexion.css';
import { getMinConnexion, useAuthContext } from "../../Contexts/AuthContext";
import { Navigate} from "react-router-dom";
import getApiUrl from "../../Constantes/apiurl";
import { useState } from "react";
import { visitUrl } from "../../Contexts/ReturnManager";
import { throwErrorOnFetch } from "../../Utils/Errors";


/**
 * @name ConnectionPage
 * @description Used to print the connection page
 * @returns The Page ConnectionPage component
 */
function ConnectionPage(){

    visitUrl(window.location.pathname);

    const [mail, setMail] = useState("");
    const [mdp, setMdp] = useState("");
    const [erreur, setErreur] = useState(false);

    const [mdpFrozen, setMdpFrozen] = useState(false);

    const {user, setUser, setIsLoggedIn} = useAuthContext();

    const connect = async () => {
        let password = mdp;
        let identifiant = mail;

        const checkbox = document.getElementById("keepConnected");

        setMdpFrozen(true);
        try {
            const response = await fetch(getApiUrl() + 'connexion', {
                method: 'POST',
                body: JSON.stringify({identifiant, password}),
                headers: {
                    'Content-Type': 'application/json'
                }
            })

            const json = await response.json();
            if(response.ok) {
                setErreur(false);
                const now = new Date(Date.now());
                let end = now.getTime() + 60000*getMinConnexion();
                setIsLoggedIn(true);
                let rememberMe = false;
                if(checkbox.checked){
                    end = now.getTime() + 13281408000000;
                    rememberMe = true;
                }
                const userLogged = {token: json.token, role: json.role, prenom: json.prenom, nom: json.nom, pseudo: json.pseudo, end: end, rememberMe: rememberMe}
                setUser(userLogged);
                localStorage.setItem("user", JSON.stringify(userLogged));

            } else {
                setMdp("");
                setMdpFrozen(false);
                setErreur(true);
            }
        } catch(e) {
            throwErrorOnFetch(e);
            setMdp("");
            setMdpFrozen(false);
            setErreur(true);
        }
    }

    return (
        <div style={{overflowX: 'hidden', width: `calc(100% + 16px)`, height: '100%', marginLeft: '-8px'}}>
            <div className="font">
                {user && (
                    <Navigate to="/dashboard" replace={true} />
                )}
                <Header/>
                <ul className="listeConnexion">
                    <li className="ConnexionC1">
                        <div className="ConnexionC1Container">
                            <LoginMessagePart>
                                <img alt="logo-Asso-Ia-Pau" src={require("../../assets/iapau_logo.png")} className="ConnexionC1Picture"/>
                                <h1> <b>Bon retour parmis nous !</b> </h1>
                                <p style={{ fontSize: '20px' }}>
                                    Vous n'avez pas encore de compte ?
                                </p>
                                <MyNavButton to="/inscription">
                                    Créer un compte
                                </MyNavButton>
                            </LoginMessagePart>
                        </div>
                    </li>
                    <li className="ConnexionC2">
                        <div style={{
                            backgroundColor: 'white'
                        }}>
                            <img alt="banner" src={require("../../assets/photoFont1.jpg")} className="ConnexionC2Image"/>
                            <div style={{
                                paddingBottom: '28%',
                                textAlign: 'center',
                                width: '100%'
                            }}>
                               <h1 style={{
                                    color: '#00897B',
                                    fontSize: '45px',
                                    textAlign: 'center',
                                    paddingTop: '10px'
                               }}>
                                    Connexion
                               </h1>
                               <form style={{width: '100%', height: '100%'}}>
                                <ul className="ConnexionListeChamps" >
                                    
                                    <li>
                                        <TextBox placeholder="E-mail ou Nom d'utilisateur" setText={setMail} value={mail} id='mailbox'/>
                                    </li>
                                    <li style={{ paddingTop: '20px'}}>
                                        <MdpBox setMdp={setMdp} id='passwordbox' value={mdp} frozen={mdpFrozen}/>
                                    </li>
                                    <li style={{ paddingTop: '20px'}}>
                                        <input type="checkbox" id="keepConnected" name="keepConnected" value="keepConnected"/>
                                        Rester connecté(e) ?
                                        <br/>
                                    </li>
                                    
                                    <WhiteNavButton onClick={(e) => {
                                        e.preventDefault();
                                        connect();
                                    }}>
                                        Se connecter
                                    </WhiteNavButton>
                                    {erreur && (
                                        <p style={{color: 'red'}}>
                                            Erreur : identifiant ou mot de passe incorrect
                                        </p>
                                    )}
                                </ul>
                               </form>
                            </div>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    )
}

export default ConnectionPage;