import { useState } from "react";
import { Navigate } from "react-router-dom";
import Swal from "sweetalert2";
import getApiUrl from "../../Constantes/apiurl";
import { useAuthContext } from "../../Contexts/AuthContext";

import "./CollonesTeams.css";
import { throwErrorOnFetch } from "../../Utils/Errors";

export default function ListeTeams({datas, passed, update, setUpdate, size}){

    const [redirection, setRedirection] = useState("");

    let teams = datas;
    for(let i = datas.length; i < 6; i++){
        teams.push({
            id: -1,
            nom: '',
            nombreMembre: -1,
            idProjet: -1,
            nomProjet: '',
            idCapitaine: -1,
            nomCapitaine: '',
            nombre_max_equipe: -1,
            nomEvent: '',
            fini: false
        })
    }

    let componentsDatas = {
        height: '600px',
        height_back: '550px'
    };

    if(size === 3){
        componentsDatas = {
            height: '340px',
            height_back: '286px'
        };
    }

    let {user} = useAuthContext();
    let userStorage = localStorage.getItem('user');
    if(userStorage){ 
        user = JSON.parse(userStorage); 
    }

    return redirection === "" ? (
        <div style={{width: '100%', height: componentsDatas.height, backgroundColor: '#00897B', borderRadius: '30px', border: 'none'}}>
            <ul style={{listStyleType: 'none', marginLeft: '25px', width: '100%', padding: '0px', display: 'flex', flexDirection: 'column', overflowX: 'hidden'}}>
                <li style={{height: '25px'}}/>
                <li style={{width: `calc(100% - 50px)`, height: componentsDatas.height_back, borderRadius: '30px', backgroundColor: 'white'}}>
                    <ul style={{listStyleType: 'none', padding: '0', paddingTop: '10px', display: 'flex', flexDirection: 'column'}}>
                        <li>
                            <ul style={{fontSize: '25px', paddingTop: '10px', borderBottom: '3px solid #00897B', height: '45px', textAlign: 'center', listStyleType: 'none', display: 'flex', flexDirection: 'row', padding: '0'}}>
                                <li className="c1">
                                    <b>Nom</b>
                                </li>
                                <li className="c2">
                                    <b>Membre(s)</b>
                                </li>
                                <li className="c3">
                                    <b>Projet choisi</b>
                                </li>
                                <li className="c4">
                                    <b>Evènement</b>
                                </li>
                                <li className="c5">
                                    <b>Actions</b>
                                </li>
                            </ul>
                        </li>
                        <li>
                            <ul style={{maxHeight: '492px', listStyleType: 'none', padding: '0', display: 'flex', flexDirection: 'column', overflow: 'scroll', overflowX: 'hidden'}} >
                                {
                                    datas.map((team, index) => {
                                        if(index >= size){
                                            return null;
                                        }
                                        return (
                                            <li>
                                                <ul style={{fontSize: '20px', marginTop: '25px', borderBottom: '1px solid #00897B', height: '50px', textAlign: 'center', listStyleType: 'none', display: 'flex', flexDirection: 'row', padding: '0'}}>
                                                    <li className="c1">
                                                        {team.nom}
                                                    </li>
                                                    <li className="c2">
                                                        {team.id === -1 ? "" : team.nombreMembre + "/" + team.nombre_max_equipe}
                                                    </li>
                                                    <li className="c3">
                                                          {team.nomProjet}
                                                    </li>
                                                    <li className="c4">
                                                       {team.nomEvent}
                                                    </li>
                                                    {
                                                        team.id !== -1 && (
                                                            <li className="c5">
                                                                <ul style={{listStyleType: 'none', padding: '0', display: 'flex', flexDirection: 'row'}}>
                                                                    <li style={{width: '40%', marginLeft: '5%'}}>
                                                                        <button style={{backgroundColor: '#75aa8c', color: 'white', cursor: 'pointer', border: 'none', borderRadius: '8px', padding: '10px 25px 10px 25px'}} onClick={(e) => {
                                                                            e.preventDefault();
                                                                            setRedirection("/teams/" + team.id);
                                                                        }}>
                                                                            VOIR
                                                                        </button>
                                                                    </li>
                                                                    {
                                                                        window.innerWidth > 800 && team.estCapitaine && (team.estCapitaine === true) && (
                                                                            <li style={{width: '40%'}}>
                                                                                <button style={{backgroundColor: 'red', color: 'white', cursor: 'pointer', border: 'none', borderRadius: '8px', padding: '10px 25px 10px 25px'}} onClick={(e) => {
                                                                                    e.preventDefault();
                                                                                    Swal.fire({
                                                                                        title: "ê".toUpperCase() + "tes vous sûrs ?",
                                                                                        text: "Une fois l'équipe supprimée, il sera trop tard !",
                                                                                        icon: 'warning',
                                                                                        showCancelButton: true,
                                                                                        confirmButtonText: 'Supprimer l\'équipe',
                                                                                        cancelButtonText: 'Retour',
                                                                                        reverseButtons: true
                                                                                      }).then( async (result) => {
                                                                                        if (result.isConfirmed) {
                                                                                            try {
                                                                                                const response = await fetch(getApiUrl() + "teams/delete/" + team.id, {
                                                                                                    method: 'DELETE',
                                                                                                    headers: {
                                                                                                        Authorization: "Bearer " + user.token,
                                                                                                        'Content-Type': 'application/json'
                                                                                                    }
                                                                                                });
                                                                                                // eslint-disable-next-line
                                                                                                const json = await response.json();
                                                                                                if(response.ok){
                                                                                                    setUpdate(update+1);
                                                                                                } else {
                                                                                                    Swal.showValidationMessage("Il y a eu une erreur lors de la suppression de l'équipe.")
                                                                                                }
                                                                                            } catch(e) {
                                                                                                throwErrorOnFetch(e);
                                                                                                Swal.showValidationMessage("Il y a eu une erreur lors de la suppression de l'équipe de l'équipe.");
                                                                                            }
                                                                                        } else if (
                                                                                          result.dismiss === Swal.DismissReason.cancel
                                                                                        ) {
                                                                                            Swal.close();
                                                                                        }
                                                                                      })
                                                                                }}>
                                                                                    SUPPRIMER
                                                                                </button>
                                                                            </li>
                                                                        )
                                                                    }
                                                                    {
                                                                        window.innerWidth > 800 && !team.estCapitaine && (
                                                                            <li style={{width: '40%'}}>
                                                                                <button style={{backgroundColor: 'red', color: 'white', cursor: 'pointer', border: 'none', borderRadius: '8px', padding: '10px 25px 10px 25px'}} onClick={(e) => {
                                                                                    e.preventDefault();
                                                                                    Swal.fire({
                                                                                        title: "ê".toUpperCase() + "tes vous sûrs ?",
                                                                                        text: "Une fois l'équipe quittée, vous devrez repostuler pour la rejoindre !",
                                                                                        icon: 'warning',
                                                                                        showCancelButton: true,
                                                                                        confirmButtonText: 'Quitter l\'équipe',
                                                                                        cancelButtonText: 'Retour',
                                                                                        reverseButtons: true
                                                                                      }).then( async (result) => {
                                                                                        if (result.isConfirmed) {
                                                                                            try {
                                                                                                const response = await fetch(getApiUrl() + "teams/" + team.id + "/quitterEquipe", {
                                                                                                    method: 'DELETE',
                                                                                                    headers: {
                                                                                                        Authorization: "Bearer " + user.token,
                                                                                                        'Content-Type': 'application/json'
                                                                                                    }
                                                                                                });
                                                                                                // eslint-disable-next-line
                                                                                                const json = await response.json();
                                                                                                if(response.ok){
                                                                                                    setUpdate(update+1);
                                                                                                } else {
                                                                                                    Swal.showValidationMessage("Il y a eu une erreur lorsque vous avez quitté l'équipe.")
                                                                                                }
                                                                                            } catch(e) {
                                                                                                throwErrorOnFetch(e);
                                                                                                Swal.showValidationMessage("Il y a eu une erreur lorsque vous avez quitté l'équipe.");
                                                                                            }
                                                                                        } else if (
                                                                                          result.dismiss === Swal.DismissReason.cancel
                                                                                        ) {
                                                                                            Swal.close();
                                                                                        }
                                                                                      })
                                                                                }}>
                                                                                    QUITTER
                                                                                </button>
                                                                            </li>
                                                                        )
                                                                    }
                                                                </ul>
                                                            </li>
                                                        ) 
                                                    }
                                                </ul>
                                            </li>
                                        )
                                    })
                                }
                            </ul>
                        </li>
                    </ul>
                </li>
                <li style={{height: '25px'}}/>
            </ul>
        </div>
    ) : (
        <Navigate to={redirection} replace={true} />
    )
}