import { NavLink } from "react-router-dom";

import '../../style/App.css';
import '../../fonts/Inter-Light.ttf'

/**
 * @author Arthur Rimaudière
 * @function BlackButton
 * @description Un boutton blanc avec des contours noirs et du texte noir.
 * @returns Component
 * @version 1.0.0
 * @example <BlackButton to="https://www.google.fr" onClick={(e) => {alert("Vous avez cliqué !")}}>{Children}</BlackButton>
 * @param {Component} children - Ce sont les balises htmls et le texte contenenu entre la balise d'entrée et de sortie de ce composant (son contenu).
 * @param {URL} to - Une url vers laquelle redirige le bouton (peut être nulle.)
 * @param {Function} onClick - Fonction a executer lorsqu'un utiliser clique sur le bouton. (peut être nulle).
 */
function BlackButton({ children, to , onClick}) {

    return (
        <div>
            <button style={{
                backgroundColor: 'white',
                minHeight: '50px',
                border: '2px solid black',
                borderRadius: '25px',
            }} onClick={onClick}>
                <NavLink style={{
                    textDecoration: "none",
                    fontSize: '20px',
                    color: "black",
                    paddingLeft: '10px', 
                    paddingRight: '10px'
                }} to={to} >
                    {children}
                </NavLink>
            </button>
        </div>
    );
}

export default BlackButton;
