import React, { useEffect, useRef } from 'react';

const ParticleDrawing = () => {
  const canvasRef = useRef(null);
  const particlesRef = useRef([]);

  // Constantes pour contrôler le nombre de particules, leur vitesse et la distance pour les lignes
  const numberOfParticles = 250 * (window.innerWidth/2000);
  const particleSpeed = 6;
  const lineDistanceThreshold = 130;
  const distanceMouseChangeDir = 120;

  useEffect(() => {
    const canvas = canvasRef.current;
    const ctx = canvas.getContext('2d');
    let particles = particlesRef.current;

    const handleResize = () => {
      canvas.width = window.innerWidth;
      canvas.height = window.innerHeight;
    };

    const drawLine = (x1, y1, x2, y2) => {
      ctx.beginPath();
      ctx.moveTo(x1, y1);
      ctx.lineTo(x2, y2);
      ctx.strokeStyle = 'white';
      let dx = x1-x2;
      let dy = y1-y2;
      let distance = Math.sqrt(dx*dx + dy*dy)
      let reducer = distance/lineDistanceThreshold;
      ctx.lineWidth = 1-reducer;
      ctx.stroke();
    };

    const updateParticle = (particle) => {
      particle.x += particle.vx + particle.boostVitesse.x;
      particle.y += particle.vy + particle.boostVitesse.y;
      
      if(Math.abs(particle.boostVitesse.x) > 0.2){
        particle.boostVitesse.x /= 2;
      } else {
        particle.boostVitesse.x = 0;
      }

      if(Math.abs(particle.boostVitesse.y) > 0.2){
        particle.boostVitesse.y /= 2;
      } else {
        particle.boostVitesse.y = 0;
      }

      if(particle.x < 0){
        particle.x = canvas.width;
      }
      if(particle.x > canvas.width){
        particle.x = 0;
      }
      if(particle.y < 0){
        particle.y = canvas.height;
      }
      if(particle.y > canvas.height){
        particle.y = 0;
      }
      
      let dx = particle.posMouse.x - particle.x;
      let dy = particle.posMouse.y - particle.y;
      let distance = Math.sqrt(dx*dx + dy*dy);
      if(particle.inRadiusMouse === false){
        if(distance < distanceMouseChangeDir){
            particle.vx = -particle.vx;
            particle.vy = -particle.vy;
            particle.inRadiusMouse = true;  
        } else {
            particle.inRadiusMouse = false;
        }
    } else {
        particle.inRadiusMouse = distance < distanceMouseChangeDir;
    }
    };

    const drawParticles = (mouseX, mouseY, cause) => {
      ctx.clearRect(0, 0, canvas.width, canvas.height);
      
      particles.forEach(particle => {
        ctx.beginPath();
        ctx.arc(particle.x, particle.y, 2, 0, 2 * Math.PI);
        ctx.fillStyle = 'white';
        ctx.fill();
        updateParticle(particle);
      });

      for (let i = 0; i < particles.length - 1; i++) {
        for (let j = i + 1; j < particles.length; j++) {
          const distance = Math.sqrt(
            (particles[i].x - particles[j].x) ** 2 +
            (particles[i].y - particles[j].y) ** 2
          );

          if (distance < lineDistanceThreshold) {
            drawLine(particles[i].x, particles[i].y, particles[j].x, particles[j].y);
          }
        }
      }

      requestAnimationFrame(drawParticles);
    };

    const createParticles = () => {
      particles = [];
      for (let i = 0; i < numberOfParticles; i++) {
        particles.push({
          x: Math.random() * window.innerWidth,
          y: Math.random() * window.innerHeight,
          vx: (Math.random() - 0.5) * particleSpeed,
          vy: (Math.random() - 0.5) * particleSpeed,
          inRadiusMouse: false,
          posMouse: {x: (window.innerWidth < 1000) ? 1500 : 0, y: (window.innerWidth < 1000) ? 1500 : 0},
          boostVitesse: {x: 0, y: 0}
        });
      }
      particlesRef.current = particles;
    };

    const handleMouseMove = (event) => {
        if(window.innerWidth < 1000){ return; }
        const mouseX = event.clientX;
        const mouseY = event.clientY - 90;
        particles.forEach((particle) => {
            let dx = mouseX - particle.x;
            let dy = mouseY - particle.y;
            particle.posMouse.x = mouseX;
            particle.posMouse.y = mouseY;
            let distance = Math.sqrt(dx*dx+dy*dy);
            if(distance < distanceMouseChangeDir){

                    let arrayDirectionnal = [];

                    arrayDirectionnal.push({
                        varX: -particle.vx,
                        varY: -particle.vy,
                        boostmultX: -1,
                        boostmultY: -1,
                    });
                    arrayDirectionnal.push({
                        varX: particle.vx,
                        varY: -particle.vy,
                        boostmultX: 1,
                        boostmultY: -1,
                    });
                    arrayDirectionnal.push({
                        varX: -particle.vx,
                        varY: particle.vy,
                        boostmultX: -1,
                        boostmultY: 1,
                    });
                    arrayDirectionnal.push({
                        varX: particle.vx,
                        varY: particle.vy,
                        boostmultX: 1,
                        boostmultY: 1,
                    });

                    let directionnal = arrayDirectionnal[0];
                    let dx = (mouseX - (particle.x + arrayDirectionnal[0].varX));
                    let dy = (mouseY - (particle.y + arrayDirectionnal[0].varY));
                    let distance = Math.sqrt(dx*dx+dy*dy);
                    for(let i = 1; i < arrayDirectionnal.length; i++){
                        let dx = (mouseX - (particle.x + arrayDirectionnal[i].varX + 10 * arrayDirectionnal[i].boostmultX));
                        let dy = (mouseY - (particle.y + arrayDirectionnal[i].varY + 10 * arrayDirectionnal[i].boostmultY));
                        let newCalcDistance = Math.sqrt(dx*dx+dy*dy);
                        if(newCalcDistance > distance){
                            directionnal = arrayDirectionnal[i];
                        }
                    }

                    particle.vx = directionnal.varX;
                    particle.vy = directionnal.varY;

                    particle.inRadiusMouse = true;
                    particle.boostVitesse.x = 10 * directionnal.boostmultX;  
                    particle.boostVitesse.y = 10 * directionnal.boostmultY;
                } else {
                    particle.inRadiusMouse = false;
                }
        })
    };

    createParticles();
    handleResize();
    window.addEventListener('resize', handleResize);
    window.addEventListener('mousemove', handleMouseMove);

    requestAnimationFrame(drawParticles);

    return () => {
      window.removeEventListener('resize', handleResize);
      window.removeEventListener('mousemove', handleMouseMove);
    };
  // eslint-disable-next-line
  }, []);

  return <canvas ref={canvasRef} style={{marginTop: '90px', width: '100%', height: '100%', position: 'absolute' }} />;
};

export default ParticleDrawing;
