import { NavLink } from "react-router-dom";
import styled from "styled-components";

import '../../style/App.css';
import '../../fonts/Inter-Light.ttf'

const StyledNavLink = styled.div`
    & a {
        text-decoration: none;
        color: black;
        margin-right: 10px
    }

    & .active {
        text-decoration: underline;
    }
`;
/**
 * @name MyNavLink
 * @param {children} object What we need to put inside, it could be string or tags
 * @param {to} string The destination the link need to move on.
 * @returns Component a button, which will have underlined text if in route.
 */
function MyNavLink({ children, to, color}) {

    let finalColor = (color) ? color : "black";

    return (to === window.location.pathname) ?

    (
        <StyledNavLink>
            <NavLink style={{
                textDecoration: "underline",
                color: finalColor,
                fontSize: "22px"
            }} to={to} >
                {children}
            </NavLink>
        </StyledNavLink>
    ) : (
        <StyledNavLink>
            <NavLink style={{
                textDecoration: "none",
                color: finalColor,
                fontSize: "18px"
            }} to={to} >
                {children}
            </NavLink>
        </StyledNavLink>
    );
}

export default MyNavLink;
