import ProjectBox from "../ProjectBox";

export default function ListProjectBox({datas}){

    let projects = datas.projet;

    return (
        <ul style={{listStyleType: 'none', padding: '0'}}>
            {
                (typeof projects !== "undefined") && projects.map((projet, index) => {
                    return (
                        <li key={`projectbox-${index}`} style={{width: '90%'}}>
                            <ProjectBox projet={projet} index={index} />
                        </li>
                    )
                })
            }
        </ul>
    )
}