import { useState } from "react";

import mdpShown from "../../../assets/MDPShown.png";
import mdpHidden from "../../../assets/MDPhidden.png";

import "./MotDePasse.css";

/**
 * @author Arthur Rimaudière
 * @function MdpBox
 * @description Le composant pour saisir son mot de passe (lors de la connexion).
 * @returns Component
 * @version 1.0.0
 * @example <MdpBox setMdp={setStateMDP} id="id-of-field" color="#707776" value={StateMDP} />
 * @param {Function} setMdp - State permettant de modifier le champs value
 * @param {String} id - id de la balise
 * @param {String} color - Couleur de la balise.
 * @param {Boolean} frozen - Le champ est figé (ex: lors de l'attente d'une réponse serveur)
 */
function MdpBox({setMdp, id, color, value, frozen}){
    let finalColor = "#707776";

    if(color){
        finalColor = color;
    }

    let isFrozen = false;
    if(frozen){
        isFrozen = frozen;
    }

    const [type, setType] = useState("password");
    // eslint-disable-next-line
    const [inButtonToggle, setInButtonToggle] = useState(false);

    const handleClick = () => {
        if(isFrozen){ return; }
        if(type === "text"){
            setType("password");
            setInButtonToggle(false);
        } else {
            setType("text");
            setInButtonToggle(true);
        }
    }

    let image = (type === "password") ? mdpHidden : mdpShown;

    let colorFont = 'white';
    if(isFrozen){
        colorFont = 'rgb(189, 188, 188)';
    }
    let borderStyle = '3px solid ' + colorFont;

    return (
        <div style={{
            width: '100%',
            height: '60px',
            border: `3px solid ${finalColor}`,
            borderRadius: '15px',
            fontSize: '18px', 
            paddingLeft: '20px',
            boxSizing: 'border-box',
            backgroundColor: colorFont,
            padding: '0'}}>
            <ul style={{listStyleType: 'none', padding: '0px', width: '100%', display: 'flex', flexDirection: 'row', backgroundColor: colorFont, borderRadius: '12px'}}>
                <li style={{width: `calc(100% - 70px)`, height: '30px', marginTop: '10px', border: borderStyle, borderRadius: '15px', backgroundColor: colorFont}}>
                    <input type={type} id={id} placeholder="Mot de passe" className="MdpBoxModifier" style={{backgroundColor: colorFont, borderColor: colorFont}} onChange={() => {
                        if(isFrozen){
                            document.getElementById(id).value = value;
                        } else {
                            setMdp(document.getElementById(id).value);
                        }
                    }} value={value}/>
                </li>
                <li style={{width: '70px', backgroundColor: colorFont, borderRadius: '25px'}}>
                    <img src={image} alt="ImageToggleVisionMdp" style={{width: '30px', height: '30px', marginTop: '15px', cursor: 'pointer', backgroundColor: colorFont}} onClick={handleClick} onMouseLeave={(e) => {
                        if(isFrozen){ return; }
                        e.stopPropagation();
                        setType("password");
                        setInButtonToggle(false);
                    }} />
                </li>
            </ul>
        </div>
    )
}

export default MdpBox;