export function getFirstError(json){
    let error = "Erreur non précisée par le serveur.";
    if(json.erreur){
        error = json.erreur;
    } else if(json.error){
        error = json.error;
    }  else if(json.errors){
        if(Array.isArray(json.errors) && json.errors.length > 0){
            error = json.errors[0].msg;
        }
    }
    return error;
}

export function throwErrorOnFetch(msg){
    if(msg !== ""){
        console.error("[ERREUR] Il y a eu une erreur lors d'une requete asynchrone : " + msg);
    } else {
        throwErrorOnFetch(" Aucune erreur spécifiée");
    }
}

export function throwFetchError(){
    throwErrorOnFetch("");
}