import { NavLink } from "react-router-dom";

import '../../style/App.css';
import '../../fonts/Inter-Light.ttf'

/**
 * @name MyNavButton
 * @param {children} child : The children of the component
 * @param {to} link : the link it leads to 
 * @returns the button component nav link (exemple: used to be clicked for connection)
 */
function WhiteNavButton({ children, to , onClick}) {

    return (
        <div>
            <button style={{
                backgroundColor: 'white',
                height: '50px',
                width: '100%',
                border: '3px solid #00897B',
                borderRadius: '15px',
                marginTop: '20px',
                cursor: 'pointer'
            }} onClick={onClick}>
                <NavLink style={{
                    textDecoration: "none",
                    fontSize: '22px',
                    color: "#00897B",
                    paddingLeft: '20px', 
                    paddingRight: '20px'
                }} to={to} >
                    {children}
                </NavLink>
            </button>
        </div>
    );
}

export default WhiteNavButton;
