

import '../../style/App.css';
import '../../fonts/Inter-Light.ttf'

/**
 * @author Arthur Rimaudière
 * @function ButtonGreen
 * @description Un boutton vert
 * @returns Component
 * @version 1.0.0
 * @example <ButtonGreen toRun={(e) => {alert("Vous avez cliqué !")}}>{Children}</ButtonGreen>
 * @param {Component} children - Ce sont les balises htmls et le texte contenenu entre la balise d'entrée et de sortie de ce composant (son contenu).
 * @param {Function} toRun - Fonction a executer lorsqu'un utiliser clique sur le bouton. (peut être nulle).
 */
function ButtonGreen({ children, toRun}) {

    return (
        <div>
            <button style={{
                backgroundColor: '#00897B',
                height: '50px',
                width: '200px',
                border: '1px solid #00897B',
                borderRadius: '26px',
                color: 'white',
                fontSize: '22px',
                fontFamily: 'Light',
                cursor: 'pointer'
            }} onClick={toRun}>
                {children}
            </button>
        </div>
    );
}

export default ButtonGreen;
