import { useState, useEffect } from "react";
import PageEspacePerso from "../PageEspacePersoType";
import { Navigate } from "react-router-dom";
import bannerPau from "../../assets/photoFont1.jpg";
import SearchBar from "../../components/SearchBar";
import ButtonGreen from "../../components/ButtonGreen";
import getApiUrl from "../../Constantes/apiurl";
import AdminProjectBox from "../../components/AdminProjectBox";
import { useAuthContext } from "../../Contexts/AuthContext";
import { visitUrl } from "../../Contexts/ReturnManager";
import { throwErrorOnFetch } from "../../Utils/Errors";

export default function ProjectListe(){
    visitUrl(window.location.pathname);
    let {user} = useAuthContext();
    let userStorage = localStorage.getItem('user');
    if(userStorage){ 
        user = JSON.parse(userStorage); 
    }
    let hasPermission = false;
    if(user && user.role){
        if(user.role !== "etudiant"){
            hasPermission = true;
        }
    }
    let doesNotHavePermission = !hasPermission;

    const [filtre, changeFiltre] = useState("");
    const [redirection, setRedirection] = useState("");
    const [datas, setDatas] = useState({});
    const [maxViews, setMaxViews] = useState(4);
    const [update, setUpdate] = useState(0);
    const [loading, setLoading] = useState(true);

    const updateMaxViews = () => {
        setMaxViews(maxViews+3);
    }

    useEffect(() => {
        async function fetchDatas(){
            try {
                const response = await fetch(getApiUrl() + "projet/", {
                    method: "GET",
                    headers: {
                        Authorization: "Bearer " + (user && typeof user.token !== "undefined" ? user.token : "null"),
                        "Content-Type": "application/json; charset=utf-8",
                    }
                });
                const json = await response.json();
                setLoading(false);
                if(response.ok){
                    setDatas(json);
                }
            } catch(e) {
                throwErrorOnFetch(e);
            }
        }
        fetchDatas();
    // eslint-disable-next-line
    }, [update]);
    let filteredDatas = [];
    let hasMoreResults = false;
    if(typeof datas.projets !== "undefined"){
        let liste = [];
        datas.projets.forEach(element => {
            liste.push(element);
        });
        liste.sort((a,b) => {
            if(a.derniereModif > b.derniereModif){
                return -1;
            } else {
                return 1;
            }
        })
        for(let i = 0; i < liste.length; i++){
            if(liste[i].nom.toUpperCase().includes(filtre.toUpperCase())){
                if(filteredDatas.length < maxViews){
                    filteredDatas.push(liste[i]);
                } else {
                    hasMoreResults = true;
                }
            }
        }
    }

    filteredDatas.sort((a, b) => {
        if(a.derniereModif > b.derniereModif){
            return -1;
        } else {
            return 1;
        }
    })

    return (
        <>
            {
                doesNotHavePermission && (
                <Navigate to="/" replace/>
                )
            }
            {
                hasPermission && (redirection !== "") && (
                    <Navigate to={redirection} replace/>
                )
            }
            {
                hasPermission && (redirection === "") && (
                    <>
                        <PageEspacePerso loading={loading}>
                            <img src={bannerPau} style={{width: '100%', marginTop: '-250px'}} alt=""/>
                            <h1 style={{fontFamily: 'Light', color: 'white', position: 'absolute',top: '80px', fontSize: '55px', textAlign: 'center', width: '96%', textShadow: '0 1px 0 black, 0 -1px 0 black'}}>Gestion des projets</h1>
                            <div style={{backgroundColor: 'white', width: '90%', marginLeft: '5%', marginRight: '5px', marginTop: '50px'}}>
                                <ul style={{display: 'flex', flexDirection: 'row', listStyleType: 'none'}}>
                                    <li>
                                        <SearchBar changeFiltre={changeFiltre} message="Rechercher un projet"/>
                                    </li>
                                    {
                                        user && user.role === "administrateur" && (
                                            <li style={{marginLeft: '20px'}}>
                                                <ButtonGreen toRun={(e) => {
                                                    e.preventDefault();
                                                    setRedirection("/projects/create");
                                                }}>
                                                    Créer un projet
                                                </ButtonGreen>
                                            </li>
                                        )
                                    }
                                </ul>
                                <div key="listofprojects" style={{marginTop: '30px', width: '100%'}}>
                                    {
                                        (typeof filteredDatas !== "undefined") && filteredDatas.map((projet, index) => {
                                            return (
                                                <div key={`adminprojectbox--${index}`}>
                                                    <AdminProjectBox datas={projet} update={update} setUpdate={setUpdate}/>
                                                </div>
                                            )
                                        })
                                    }
                                    <br/><br/><br/>
                                    {
                                        (hasMoreResults) ? (
                                            <div style={{display: 'flex', justifyContent: 'center'}}>
                                                <button onClick={() => {
                                                    updateMaxViews();
                                                }} style={{
                                                    backgroundColor: '#00897B',
                                                    height: '50px',
                                                    width: '300px',
                                                    border: '1px solid #00897B',
                                                    borderRadius: '26px',
                                                    boxShadow: '2px 1px 1px black',
                                                    cursor: 'pointer',
                                                    color: 'white',
                                                    fontSize: '20px',
                                                }}>
                                                    <span>Voir plus de résultats</span>
                                                </button>
                                                <br/><br/><br/><br/><br/>
                                            </div>
                                        ) : ( 
                                            <div style={{display: 'flex', justifyContent: 'center', fontSize: '20px'}}>
                                                Aucun résultat supplémentaire
                                                <br/><br/>
                                            </div>
                                        )
                                    }
                                </div>
                            </div>
                        </PageEspacePerso>
                    </>
                )
            }
        </>
    )
}