import { useState } from "react"
import { Navigate } from "react-router-dom";
import Swal from "sweetalert2";
import getApiUrl from "../../Constantes/apiurl";
import { useAuthContext } from "../../Contexts/AuthContext";
import { throwErrorOnFetch } from "../../Utils/Errors";
import SplitButton from "../SplitButton";
import { getMainImage } from "../../Utils/Image";

/**
 * @author Arthur Rimaudière
 * @function AdminProjectBox
 * @description Ce composant est présent uniquement dans les pages d'Administration.
 * @returns Component
 * @version 1.0.0
 * @example <AdminProjectBox datas={fetched.datas} update={updateComponent} setUpdate={setUpdateComponent}/>
 * @param {object} datas - Les données recues du serveur contenant ce qu'il faut afficher dans le composant: nom, description, ...
 * @param {Number} update - State du composant parent (peut être nul).
 * @param {Function} setUpdate - Updater du state du composant parent (peut être nul).
 */
export default function AdminProjectBox({datas, update, setUpdate}){

    const [redirection, setRedirection] = useState("");
    const {user} = useAuthContext();

    const maxSize = 1040;
    const N = (datas.description.length > maxSize) ? maxSize : datas.description.length;
    const description = datas.description.slice(0, N) + ((datas.description.length > maxSize) ? "..." : "");


    let spaceDescription = [];
    for(let i = datas.description.length; i < maxSize; i+= 130){
        spaceDescription.push(<br/>);
    }

    const now = new Date(Date.now()).getTime();
    const date = new Date(datas.derniereModif).getTime();
    const diffTime = now - date;
    let lastModifiedOn = "";

    const diffInMinutes = Math.floor(diffTime / (1000 * 60));
    const diffInHours = Math.floor(diffTime / (1000 * 60 * 60));
    const diffInDays = Math.floor(diffTime / (1000 * 60 * 60 * 24));

    if(diffInDays >= 1){
        lastModifiedOn = "Il y a " + diffInDays + " jour" + (((diffInDays > 1) ? "s" : ""));
        if (diffInDays === 1){
            lastModifiedOn = "Hier";
        }
    } else if (diffInHours >= 1){
        lastModifiedOn = "Il y a " + diffInHours + " heure" + (((diffInHours > 1) ? "s" : ""));
    } else if (diffInMinutes >= 1){
        lastModifiedOn = "Il y a " + diffInMinutes + " minute" + (((diffInMinutes > 1) ? "s" : ""));
    } else {
        lastModifiedOn = "Il y a quelques secondes";
    }
    
    return (
        <>
            {
                redirection && redirection !== "" && (
                    <Navigate to={redirection} replace />
                )
            }
            <div key={`projectbox-div-${datas.idProjet}-1`} style={{backgroundColor: '#dfddddf2', width: '100%', fontFamily: 'Light', border: '1px solid #dfddddf2', borderRadius: '5px', marginTop: '40px'}}>
                <ul style={{listStyleType: 'none', width: '100%', display: 'flex', flexDirection: 'row', padding: '0', paddingLeft: '12px'}}>
                    <li key={`projectbox-1-${datas.idProjet}`} style={{width: '30%', height: '100%'}}>
                        <div style={{width: '100%', height: '350px', overflow: 'hidden'}}>
                            <img src={datas.image} onError={({ currentTarget }) => {
                            currentTarget.onerror = null;
                            currentTarget.src=getMainImage();
                        }} alt="pictureAdminProjectBox" style={{maxWidth: '100%', height: '100%'}}/>
                            
                        </div>
                    </li>

                    <li key={`projectbox-2-${datas.idProjet}`} style={{width: '70%', height: '100%', paddingLeft: '15px'}}>

                        <ul style={{listStyleType: 'none', height: '100%', width: '100%', display: 'flex', flexDirection: 'column', padding: '0'}}>
                            <li key={`projectbox-2-${datas.idProjet}-1`} style={{height: '90%'}}>
                                <h2>
                                    {datas.nom}
                                </h2>
                                <p>
                                    {description}
                                    {
                                        spaceDescription && spaceDescription.map((value, index) => {
                                            return (
                                                <>
                                                    {value}
                                                </>
                                            )
                                        })
                                    }
                                </p>
                                <br/>
                            </li>
                            <li key={`projectbox-2-${datas.idProjet}-2`} style={{position: 'static', marginTop: 'auto'}}>
                                <ul style={{display: 'flex', flexDirection: 'row', padding: '0', listStyleType: 'none'}}>
                                    {
                                        user.role && user.role !== "administrateur" && (
                                            <>
                                                <li style={{marginLeft: '70px'}}>
                                                    <button style={{backgroundColor: '#dfddddf2', padding: '10px 10px 10px 10px', fontSize: '17px', borderRadius: '5px', border: '1px solid black', cursor: 'pointer'}} onClick={(e) => {
                                                        e.preventDefault()
                                                        setRedirection("/projects/" + datas.idProjet + "/teams")
                                                    }}>
                                                        Gérer les équipes
                                                    </button>
                                                </li>
                                            </>
                                        )
                                    }
                                    {
                                        user.role && user.role === "administrateur" && (
                                            <>
                                                <li style={{marginLeft: '70px'}}>
                                                    <SplitButton options={[
                                                        {
                                                            name: "Gérer les équipes",
                                                            fonction: () => {
                                                                setRedirection("/projects/" + datas.idProjet + "/teams");
                                                            }
                                                        },{
                                                            name: "Modifier le projet",
                                                            fonction: () => {
                                                                setRedirection("/projects/" + datas.idProjet + "/edit");
                                                            }
                                                        },{
                                                            name: "Supprimer le projet",
                                                            fonction: () => {
                                                                Swal.fire({
                                                                    title: 'Êtes-vous sûr ?',
                                                                    text: "Cette action est irréversible !",
                                                                    icon: 'warning',
                                                                    showCancelButton: true,
                                                                    confirmButtonColor: '#3085d6',
                                                                    cancelButtonColor: '#d33',
                                                                    confirmButtonText: 'Confirmer',
                                                                    cancelButtonText: 'Annuler'
                                                                }).then((result) => {
                                                                    if (result.isConfirmed) {
                                                                    async function deleteData(){
                                                                        try {
                                                                            const response = await fetch(getApiUrl() + "projet/delete/" + datas.idProjet, {
                                                                                method: "DELETE",
                                                                                headers: {
                                                                                    Authorization: "Bearer " + user.token,
                                                                                    "Content-Type": "application/json; charset=utf-8",
                                                                                },
                                                                            });
                                                                            if(!response.ok){
                                                                                Swal.fire({
                                                                                    icon: 'error',
                                                                                    title: 'Oops...',
                                                                                    text: 'La suppression du projet n\'a pas fonctionné',
                                                                                })
                                                                            } else {
                                                                                setUpdate(update+100); 
                                                                            }
                                                                        } catch(e) {
                                                                            throwErrorOnFetch(e);
                                                                        }
                                                                    }
                                                                    deleteData();
                                                                    setUpdate(update+10);
                                                                    }
                                                                })
                                                                setUpdate(update+1);
                                                            }
                                                        }
                                                    ]} />
                                                </li>
                                            </>
                                        )
                                    }
                                    

                                </ul>
                            </li>
                        </ul>
                    </li>

                    <li key={`projectbox-3-${datas.idProjet}`} style={{width: '30%', textAlign: 'center', fontSize: '20px'}}>
                        <p>
                            <b>Thèmes:</b> {datas.themes.length}
                        </p>
                        <p>
                            <b>Ressources:</b> {datas.ressources.length}
                        </p>
                        {/*<p>
                            <b>Récompense:</b> {datas.recompense}€
                                </p>*/}
                        <span style={{textAlign: 'center', cursor: 'pointer'}} onClick={(e) => {
                            e.preventDefault();
                            window.open(datas.sujet);
                        }}>
                            <b><u>Lien du sujet</u></b>
                        </span>
                        <br/>
                        <p>
                            <b>Id du projet:</b> {datas.idProjet}
                        </p>
                        <br/>
                        <p style={{paddingLeft: '25px', paddingRight: '25px'}}>
                            <b>Dernière modification:</b> {lastModifiedOn}
                        </p>
                    </li>
                </ul>
            </div>
        </>
    )
}