import { Navigate, useParams } from "react-router-dom";
import { useEffect, useState } from "react";
import Header from "../../components/Header";

import "./DataChallenge.css";
import Description from "../../components/EventsPage/Description";
import Regles from "../../components/EventsPage/Regles";
import Projets from "../../components/EventsPage/Projets";

import '../../style/App.css';
import '../../fonts/Inter-Light.ttf'

import BlackButton from "../../components/BlackButton";

import MyNavButton from "../../components/MyNavButton";
import { useAuthContext } from "../../Contexts/AuthContext";
import getApiUrl from "../../Constantes/apiurl";
import { openCreateTeamMenu, printListeOfTeams } from "./Functions";

import "./Functions.js";
import { visitUrl } from "../../Contexts/ReturnManager";
import { throwErrorOnFetch } from "../../Utils/Errors";
import Loading from "../../components/LoadingComponent";

export default function DataChallengePage(){

    const {id} = useParams();
    const [page, setPage] = useState("Description");
    // eslint-disable-next-line
    const [width, setWidth] = useState(window.innerWidth);

    visitUrl(window.location.pathname);

    useEffect(() => {
        const changeWidth = () => {
            setWidth(window.innerWidth);
        }

        window.addEventListener('resize', changeWidth);

        return () => {
            window.removeEventListener('resize', changeWidth);
        }
    }, [])

    const [datas, setDatas] = useState({
        title: '',
        themes: [],
        date_creation: '27 May, 2023 00:00:00 GMT+2',
        date_debut: '27 Jul, 2023 00:00:00 GMT+2',
        date_fin: '27 Aug, 2023 00:00:00 GMT+2',
        description: '',
        type_challenge: 'Data Battle', // sensible à la case: Data Battle / Data Challenge
        projet: [], // le lien du sujet est vide si l'utilisateur n'est pas connecté
        regles: [], 
        classement: null, // null si pas dispo
        finalistes: null, //null si pas dispo
        messageFin: '', // null si pas disponible
        userIsInterested: false, //defaut: false, si user le changer en fonction
        equipe: -1, // -1 si pas d'équipe l'équipe sinon
        nbMinParEquipe: 3,
        nbMaxParEquipe: 8
    });

    const [Teams, setTeams] = useState([]);

    let {user} = useAuthContext();
    let userStorage = localStorage.getItem('user');
    if(userStorage){ 
        user = JSON.parse(userStorage); 
    }

    const [updateData, setUpdateData] = useState(0);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        async function fetchData(){
            let token = "";
            if(user){
                token = user.token;
            }
            let response;
            try {
                response = await fetch(getApiUrl() + "voir_event/" + id, {
                    method: "GET",
                    headers: {
                        Authorization: "Bearer " + token,
                        "Content-Type": "application/json; charset=utf-8",
                }
                })
                const json = await response.json();
                if(response.ok) {
                    setDatas(json);
                } else {
                    setRedirection("/error404");
                }
            } catch(e) {
                throwErrorOnFetch(e);
            }
            if(user){
                try {
                    const response2 = await fetch(getApiUrl() + "teams/" + id + "/ouvertes", {
                        method: "GET",
                        headers: {
                            Authorization: "Bearer " + user.token,
                            "Content-Type": "application/json; charset=utf-8",
                    }
                    })
                    const json2 = await response2.json();
                    if(response.ok) {
                        setTeams(json2.equipes);
                    }
                } catch(e) {
                    throwErrorOnFetch(e);
                }
            }
            setLoading(false);
        }
        fetchData();
    //eslint-disable-next-line
    }, [updateData])

    const [redirection, setRedirection] = useState("");
    let selectedNavStyle = {borderTop: '1px solid rgb(196, 195, 195)', borderRight: '1px solid rgb(196, 195, 195)', borderLeft: '1px solid rgb(196, 195, 195)', borderTopLeftRadius: '5px', borderTopRightRadius: '5px',  padding: '10px 10px 10px 10px', cursor: 'pointer', fontSize: '18px'};
    let notSelectedNavStyle = {borderBottom: '1px solid rgb(196, 195, 195)', padding: '10px 10px 10px 10px', cursor: 'pointer', fontSize: '18px'};

    return (
        <>
            {
                redirection !== "" && (
                    <Navigate to={redirection} replace={true} />
                )
            }
            <Header/>
            {
                    window.innerWidth > 1120 && (
                        <img style={{marginTop: '0px', width: `calc(100% + 16px)`, marginLeft: '-8px'}} alt="ImageHautDePage" src={require("../../assets/pyrenees.jpg")} className="img"/>
                    )
            }
            {
                window.innerWidth < 1120 && (
                    <div style={{height: '90px'}}>
                    </div>
                )
            }
            {
                loading === true && (
                 <Loading/>
                )
            }
            {
                loading === false && (<>
            <div className="EventTitleAndThemes" style={{fontFamily: 'Light', overflowX: 'hidden'}}>
                {
                    window.innerWidth > 1120 && (
                        <h1>{datas.title}</h1>
                    )
                }
                {
                    window.innerWidth > 1330 && (
                        <ul className="EventThemes">
                            {
                                (typeof datas.themes !== "undefined") && datas.themes.map((theme, index) => {
                                    return (
                                        <li key={`theme-${index}`} style={{marginLeft: '10px', marginRight: '10px'}}>
                                            <div style={{backgroundColor: '#EAE7E7', padding: '10px 10px 10px 10px', fontSize: '17px', filter: 'opacity(0.6)'}}>
                                                {theme}
                                            </div>
                                        </li>
                                    )
                                })
                            }
                        </ul>
                    )
                }
            </div>
            <div className="ButtonEvents">
                {
                    !user && (
                        <MyNavButton to="/connexion">
                            S'inscrire
                        </MyNavButton>
                    )
                }
                {
                    user && (user.role === "etudiant") && (
                        <>
                            <ul style={{display: 'flex', flexDirection: 'row', padding: '0', listStyleType: 'none'}}>
                            {
                                (datas.teamId === -1) ? (
                                    <>
                                        <li onClick={(e) => {
                                            e.preventDefault()
                                            printListeOfTeams(Teams, id, user, setRedirection, updateData, setUpdateData)
                                        }}>
                                            <BlackButton to="">
                                                Rejoindre une équipe
                                            </BlackButton>
                                        </li>
                                        <li style={{paddingLeft: '10px', paddingRight: '10px'}}/>
                                        <li onClick={(e) => {
                                            e.preventDefault();
                                            openCreateTeamMenu(user, id, datas.projet, setRedirection);
                                        }}>
                                            <MyNavButton to="">
                                                Créer une équipe
                                            </MyNavButton>
                                        </li>
                                    </>
                                ) : (
                                    <li>
                                        <BlackButton to={"/teams/" + datas.teamId}>
                                            Voir mon équipe
                                        </BlackButton>
                                    </li>
                                )
                            }
                            </ul>
                        </>
                    )
                }
            </div>
            <ul className="SeparationPageEvents" style={{fontFamily: 'Light'}}>
                <li className="NavBarPageEvents">
                    <ul className="ChoixCatPageEvent">
                        {
                            window.innerWidth > 800 && (
                                <>
                                    <li>
                                        <button onClick={(e) => {
                                            e.preventDefault();
                                            setPage("Description");
                                        }} className={(page === "Description") ? "btnNavBarEventsBold" : "btnNavBarEvents"} style={{fontFamily: 'Light'}}> {(page === "Description") ? "|" : null} Description </button>
                                    </li>
                                    <li>
                                        <button onClick={(e) => {
                                            e.preventDefault();
                                            setPage("Projets");
                                        }} className={(page === "Projets") ? "btnNavBarEventsBold" : "btnNavBarEvents"} style={{fontFamily: 'Light'}}> {(page === "Projets") ? "|" : null} Projet{((typeof datas.projet !== "undefined") && datas.projet.length > 1) ? "s" : null} </button>
                                    </li>
                                    <li>
                                        <button onClick={(e) => {
                                            e.preventDefault();
                                            setPage("Règles");
                                        }} className={(page === "Règles") ? "btnNavBarEventsBold" : "btnNavBarEvents"} style={{fontFamily: 'Light'}}> {(page === "Règles") ? "|" : null} Règles </button>
                                    </li>
                                </>
                            )
                        }
                        {
                            window.innerWidth < 800 && (
                                <li>
                                    <ul style={{display: 'flex', flexDirection: 'row', listStyleType: 'none', padding: '0'}}>
                                        <li style={(page === "Description") ? selectedNavStyle : notSelectedNavStyle} onClick={(e) => {
                                            e.preventDefault();
                                            setPage("Description");
                                        }}>
                                            <b>Informations</b>
                                        </li>
                                        <li style={(page === "Projets") ? selectedNavStyle : notSelectedNavStyle} onClick={(e) => {
                                            e.preventDefault();
                                            setPage("Projets");
                                        }}>
                                            <b>Projets</b>
                                        </li>
                                        <li style={(page === "Règles") ? selectedNavStyle : notSelectedNavStyle} onClick={(e) => {
                                            e.preventDefault();
                                            setPage("Règles");
                                        }}>
                                            <b>Règles</b>
                                        </li>
                                    </ul>
                                </li>
                            )
                        }
                    </ul>
                </li>
                <li className="ContainerPageEvents">
                    { page === "Description" &&  <Description datas={datas}/> }
                    { page === "Règles" &&  <Regles datas={datas}/> }
                    { page === "Projets" &&  <Projets datas={datas}/> }
                </li>
            </ul></>)}
        </>
    )

}