export default function Regles({datas}){
    return (
        <>
            <h1 style={{fontFamily: 'Light', color: '#00897B', marginBottom: '50px'}}>Règles du data {datas.type_challenge}</h1>
            <div style={{width: '100%'}}>
                <h2 style={{textAlign: 'justify', width: '90%'}}> Article n° 1 : Nombre de participants par équipe </h2>
                <p style={{textAlign: 'justify', width: '90%'}}>
                    Pour participer à l'événement, les participants devront créer des équipes contenant entre <b>{datas.nbMinParEquipe}</b> et <b>{datas.nbMaxParEquipe}</b> personnes. Toute équipe ne satisfaisant pas cette condition à la fin des inscriptions sera mise en attente jusqu'à la validation de l'équipe par un gestionnaire de l'événement.
                </p>
                {datas.regles.map((rule, index) => {
                    return (
                        <div>
                            <h2 style={{textAlign: 'justify', width: '90%'}}>Article n° {(index+2)} : {rule.titre}</h2>
                            <p style={{textAlign: 'justify', width: '90%'}}>
                                {rule.contenu}
                            </p>
                        </div>
                    )
                })}
            </div>
        </>
    )
}