import { useEffect, useState } from "react";
import { useAuthContext } from "../../Contexts/AuthContext";
import sendImage from "../../assets/sendMessage.png";
import Message from "../Message";
import { useParams } from "react-router-dom";
import getApiUrl from "../../Constantes/apiurl";
import { getFirstError, throwErrorOnFetch } from "../../Utils/Errors";
import Swal from "sweetalert2";

export default function Messagerie(){

    const { id } = useParams();

    let {user} = useAuthContext();
    let userStorage = localStorage.getItem('user');
    if(userStorage){ 
        user = JSON.parse(userStorage); 
    }

    const [message, setMessage] = useState("");

    async function sendMessageServeur(user, msg){
        if(msg === ""){
            document.getElementById("chatEntryMessagerieMessage").style.borderColor = 'red';
            document.getElementById("chatEntryMessagerieMessage").style.border = '2px solid red';
            document.getElementById("chatEntryMessagerieMessage").style.outline = 'none';
        } else {
            document.getElementById("chatEntryMessagerieMessage").style.borderColor = 'black';
            document.getElementById("chatEntryMessagerieMessage").style.border = '1px solid black';
            document.getElementById("chatEntryMessagerieMessage").style.outline = 'none';
            let bodyRequest = JSON.stringify({
                contenu: msg,
                typeMessage: 'team',
            });
            try {
                const response = await fetch(getApiUrl() + "message/teams/" + id + "/envoyerMessage", {
                    method: 'POST',
                    headers: {
                        Authorization: "Bearer " + user.token,
                        'Content-Type': 'application/json'
                    },
                    body: bodyRequest
                })
                const json = await response.json();
                if(response.ok){
                    setMessage("");
                    setUpdate(update+1)
                } else {
                    let erreur = getFirstError(json);
                    Swal.fire({
                        position: 'top-end',
                        icon: 'error',
                        title: erreur,
                        showConfirmButton: false,
                        timer: 1000
                    })
                }
            } catch(e){
                throwErrorOnFetch(e);
            }
        }
    }  

    const sendMessage = async (user) => {
        let value = document.getElementById("chatEntryMessagerieMessage").value;
        sendMessageServeur(user, value);
    }
    
    function enterPressEventMethod(e){
        if(e.key === "Enter" ){
            sendMessage(user);
        } 
    }

    const [update, setUpdate] = useState(0);

    useEffect(() => {
        async function fetchDatas(){
            try {
                const response = await fetch(getApiUrl() + "message/teams/" + id, {
                    method: 'GET',
                    headers: {
                        Authorization: "Bearer " + user.token,
                        'Content-Type': 'application/json'
                    }
                })

                const json = await response.json();
                if(response.ok){
                    setMessages(json.message)
                    setTimeout(() => {
                        let chat = document.getElementById("listeofmessages");
                        chat.scrollTop = chat.scrollHeight;
                    }, 10);
                }
            } catch(e) {
                throwErrorOnFetch(e);
            }
        }
        fetchDatas();
    // eslint-disable-next-line
    }, [update])

    useEffect(() => {

        const inputElement = document.getElementById("chatEntryMessagerieMessage");
        if(typeof inputElement !== "undefined"){
            inputElement.addEventListener('keypress', enterPressEventMethod);
        }

        return () => {
            const inputElement = document.getElementById("chatEntryMessagerieMessage");
            try {
                inputElement.removeEventListener('keypress', enterPressEventMethod);
            } catch(error){
                console.log(error);
            }
        }
    // eslint-disable-next-line
    }, [])

    const [messages, setMessages] = useState([]);

    if(Array.isArray(messages)){
        messages.sort((a, b) => {
            let dateA = new Date(a.dateMessage);
            let dateB = new Date(b.dateMessage);
            if(dateA.getTime() < dateB.getTime()){
                return 1;
            } else{
                return 1;
            }
        })
    }

    const messagesSorted = messages;
    messagesSorted.sort((a,b) => {
        let dateA = new Date(a.dateMessage);
        let dateB = new Date(b.dateMessage);
        if(dateA.getTime() < dateB.getTime()){
            return -1;
        } else {
            return 1;
        }
    })

   return window.innerWidth > 800 ? (
    <div style={{width: '70%', height: '1200px', marginLeft: '15%', marginTop: '50px', borderRadius: '25px', backgroundColor: '#00897B'}}>
        <div style={{width: `calc(100% - 50px)`, height: '1150px', marginLeft: '25px', marginTop: '50px', borderRadius: '25px', backgroundColor: 'white', position: 'relative', top: '25px'}}>
            <ul id="listeofmessages" style={{width: `calc(100% - 50px)`, height: '1040px', marginLeft: '25px', marginTop: '50px', position: 'relative', top: '25px', listStyleType: 'none', padding: '0', display: 'flex', flexDirection: 'column', overflowY: 'scroll'}}>
                <li style={{textAlign: 'center', fontSize: '22px', width: '100%', height: '30px'}}>
                    Début de la conversation
                </li>
                {
                    Array.isArray(messagesSorted) && messagesSorted.map((msg) => {
                        return (
                            <li style={{marginTop: '20px', width: `calc(100% - 40px)`, paddingLeft: '10px', paddingRight: '30px'}}>
                                <Message msg={msg} update={update} setUpdate={setUpdate} />
                            </li>
                        )
                    })
                }          
            </ul>
            <ul style={{listStyleType: 'none', padding: '0', display: 'flex', flexDirection: 'row', marginLeft: '25px', paddingTop: '10px', width: `calc(100% - 50px)`, height: '70px'}}>
                <li style={{width: `calc(100% - 150px)`}}>
                    <input type="text" placeholder="Envoyer un message" id="chatEntryMessagerieMessage" style={{
                        width: '100%',
                        height: '50px',
                        fontSize: '23px',
                        marginTop: '10px',
                        fontFamily: 'Light',
                        padding: '5px 10px 5px 10px',
                        borderRadius: '15px'
                        
                    }} onChange={(e) => {
                        setMessage(e.target.value);
                    }} value={message}/>
                </li>
                <li style={{width: '150px'}}>
                    <img src={sendImage} alt="Envoie d'un message" style={{
                        width: '40px',
                        marginLeft: '60px',
                        marginTop: '10px',
                        cursor: 'pointer',
                        border: '1px solid black',
                        borderRadius: '25px',
                        padding: '10px 10px 10px 10px'
                    }} onClick={async (e) => {
                        e.preventDefault();
                        sendMessage(user, message);
                    }}/>
                </li>
            </ul>
        </div>
    </div>
   ) : (
    <div style={{width: '100%', height: 'auto', marginTop: '50px'}}>
        <ul id="listeofmessages" style={{width: `100%`, height: '1040px', marginLeft: '25px', marginTop: '50px', position: 'relative', top: '25px', listStyleType: 'none', padding: '0', display: 'flex', flexDirection: 'column', overflowY: 'scroll'}}>
                <li style={{textAlign: 'center', fontSize: '15px', width: '100%', height: '30px'}}>
                    Début de la conversation
                </li>
                {
                    Array.isArray(messages) && messages.map((msg) => {
                        return (
                            <li style={{marginTop: '20px', width: `calc(100% - 40px)`, paddingLeft: '10px', paddingRight: '30px'}}>
                                <Message msg={msg} update={update} setUpdate={setUpdate} />
                            </li>
                        )
                    })
                }          
            </ul>
            <ul style={{listStyleType: 'none', padding: '0', display: 'flex', flexDirection: 'row', marginLeft: '25px', paddingTop: '10px', width: `calc(100% - 50px)`, height: '70px'}}>
                <li style={{width: `100%`}}>
                    <input type="text" placeholder="Envoyer un message" id="chatEntryMessagerieMessage" style={{
                        width: '100%',
                        height: '30px',
                        fontSize: '16px',
                        marginTop: '10px',
                        fontFamily: 'Light',
                        padding: '5px 10px 5px 10px',
                        borderRadius: '15px'
                        
                    }} onChange={(e) => {
                        setMessage(e.target.value);
                    }} value={message}/>
                </li>
                <li style={{width: 'auto'}}>
                    <img src={sendImage} alt="Envoie d'un message" style={{
                        width: '25px',
                        marginLeft: '30px',
                        marginTop: '8px',
                        cursor: 'pointer',
                        border: '1px solid black',
                        borderRadius: '25px',
                        padding: '10px 10px 10px 10px'
                    }} onClick={async (e) => {
                        e.preventDefault();
                        sendMessage(user, message);
                    }}/>
                </li>
        </ul>
    </div>
   )
}