import "./ProjectBox.css";

import '../../style/App.css';
import '../../fonts/Inter-Light.ttf'
import WhiteNavButton from "../WhiteNavButton";

import { NavLink } from "react-router-dom";

import { useState } from "react";
import { getMainImage } from "../../Utils/Image";

export default function ProjectBox({projet, index}){

    const [menuRessourcesOpen, setMenuRessourcesOpen] =useState(false);

    function handleRessources(){
        setMenuRessourcesOpen(!menuRessourcesOpen);
    }

    function extractVideoCodeFromUrl(url){
        return url.replace('https://www.youtube.com/watch?v=', '');
    }

    const maxSize = 895;
    const N = (projet.description.length > maxSize) ? maxSize : projet.description.length;
    const description = projet.description.slice(0, N) + ((projet.description.length > maxSize) ? "..." : "");
    let spaceDescription = [];
    for(let i = projet.description.length; i < maxSize; i+= 130){
        spaceDescription.push(<br/>);
    }
    return (
        <div className="ContainerProjectBox" style={{fontFamily: 'Light'}}>
            <div>
                <ul className="ListeProjectBox">
                    <li className="Collone1ProjectBox">
                        <img src={projet.img} id="imageValue" onError={({ currentTarget }) => {
                            currentTarget.onerror = null;
                            currentTarget.src=getMainImage();
                        }} alt="imagedelaprojectbox" className="ImageProjectBox" />
                    </li>
                    <li className="Collone2ProjectBox">
                        <div style={{width: '90%'}}>
                            <h2 style={{textAlign: 'center'}}>{projet.titre}</h2>
                            <p style={{textAlign: 'justify', width: '100%'}}>
                                {description}
                                {
                                    spaceDescription && spaceDescription.map((child) => {
                                        return (
                                            <>
                                                {child}
                                            </>
                                        )
                                    })
                                }
                            </p>
                            <ul style={{listStyleType: 'none', padding: '0', display: 'flex', flexDirection: 'row'}}>
                                {
                                    projet.thematique.map((mot, index2) => {
                                        return (
                                            <li style={{backgroundColor: '#8A6F6F', marginRight: '10px', marginLeft: '10px', paddingLeft: '10px', paddingRight: '10px', padding: '10px 10px 10px 10px', fontSize: '17px', filter: 'opacity(0.6)'}}>
                                                {mot}
                                            </li>
                                        )
                                    })
                                }
                            </ul>
                            <WhiteNavButton onClick={(e) => {
                                e.preventDefault();
                                window.open(projet.sujet);
                            }}>
                                Voir le sujet
                            </WhiteNavButton>
                            </div>
                    </li>
                </ul>
            </div>
            <div style={{width: '100%', textAlign: 'left', cursor: 'pointer', fontSize: '25px', backgroundColor: 'white', borderColor: 'white', outline: 'none', marginTop: '20px'}} onClick={handleRessources}>
                    <b>{(menuRessourcesOpen) ? "⌄" : ">"}</b> Ressources du projet
            </div>
            <div style={{width: '90%', textAlign: 'left', fontSize: '18px', marginTop: '20px'}}>
                { projet.ressources.map((ressource, index) => 
                        {
                            return (menuRessourcesOpen) ? (
                                <>
                                    <div>
                                        <h3>{(index+1)}. {ressource.titre}</h3>
                                        {ressource.type === "video" && ressource.lien.startsWith("https://www.youtube.com/watch?v=") && (
                                            <>
                                            <br/>
                                            <iframe 
                                                src={"https://www.youtube.com/embed/" + extractVideoCodeFromUrl(ressource.lien)} 
                                                title="YouTube video player" 
                                                frameborder="0" 
                                                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share; fullscreen" 
                                                allowfullscreen="true"
                                            className="VideoYoutubeProject">
                                            </iframe>
                                            </>
                                            
                                        )}
                                        {ressource.type === "video" && !ressource.lien.startsWith("https://www.youtube.com/watch?v=") && (
                                            <div style={{color: 'red'}}>
                                                <b>Erreur :</b> Une erreur est survenue lors du chargement de la vidéo. Merci de contacter un gestionnaire ou un administrateur afin de résoudre ce problème.
                                            </div>
                                        )}
                                        <p style={{textAlign: 'justify'}}>{ressource.description}</p>
                                        {ressource.type === "drive" && (
                                            <button style={{width: '200px', fontSize: '22px', marginLeft: 'calc(50% - 100px)', border: '2px solid #00897B', backgroundColor: 'white', borderRadius: '25px', cursor: 'pointer'}}>
                                                <NavLink style={{
                                                    textDecoration: "none",
                                                    color: '#00897B'
                                                }} onClick={(e) => {
                                                    e.preventDefault();
                                                    window.open(ressource.lien);
                                                }}>
                                                    Voir le drive
                                                </NavLink>
                                            </button>
                                        )}
                                        {ressource.type === "téléchargement" && (
                                            <button style={{width: '300px', fontSize: '22px', marginLeft: 'calc(50% - 150px)', border: '2px solid #00897B', backgroundColor: 'white', borderRadius: '25px', cursor: 'pointer'}}>
                                                <NavLink style={{
                                                    textDecoration: "none",
                                                    color: '#00897B'
                                                }} onClick={(e) => {
                                                    e.preventDefault();
                                                    window.open(ressource.lien);
                                                }} >
                                                    Télécharger le fichier
                                                </NavLink>
                                            </button>
                                        )}
                                        {ressource.type === "lien" && (
                                            <button style={{width: '200px', fontSize: '22px', marginLeft: 'calc(50% - 100px)', border: '2px solid #00897B', backgroundColor: 'white', borderRadius: '25px', cursor: 'pointer'}}>
                                                <NavLink style={{
                                                    textDecoration: "none",
                                                    color: '#00897B'
                                                }} onClick={(e) => {
                                                    e.preventDefault();
                                                    window.open(ressource.lien);
                                                }} >
                                                    Suivre le lien
                                                </NavLink>
                                            </button>
                                        )}
                                    </div>
                                    <br/>
                                </>
                            ) : null;
                        }
                )}
            </div>
        </div>
    )
}