import '../../../style/App.css';
import '../../../fonts/Inter-Light.ttf'

/**
 * @author Arthur Rimaudière
 * @function MdpBox
 * @description Composant de mot clef pour un evenement
 * @returns Component
 * @version 1.0.0
 * @example < EventBoxKeyWord mot="Mot clef" />
 * @param {String} mot - Mot clef à afficher
 */
function EventBoxKeyWord({mot}){
    return (
        <button style={{
            backgroundColor: '#BCB3B3',
            height: '50px',
            fontSize: window.innerWidth < 800 ? '15px' : '20px',
            marginLeft: '10px',
            border: '0px solid black',
            fontFamily: 'Light'

        }}>
            <span style={{marginLeft: '5px', marginRight: '5px'}}>
                {mot}
            </span>
        </button>
    )
}

export default EventBoxKeyWord;