import * as React from 'react';
import TextField from '@mui/material/TextField';
import Autocomplete from '@mui/material/Autocomplete';

/**
 * @author Arthur Rimaudière
 * @function LevelSelector
 * @description Le selecteur de niveau lors de l'inscription.
 * @returns Component
 * @version 1.0.0
 * @example <LevelSelector color="#707776"/>
 * @param {String} children - Couleur du composant (peut être null)
 */
export default function LevelSelector({color}) {
  let finalColor = "#707776";

  if(color){
    finalColor = color;
  }

  return (
    <Autocomplete
      disablePortal
      id="combo-box-level"
      options={levels}
      
      sx={{
        width: `100%`,
        height: '60px',
        boxSizing: 'border-box',
        "& .MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline": {
          border: `3px solid ${finalColor}`,
          borderRadius: '15px'
        },
        "&:hover .MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline": {
          border: `3px solid ${finalColor}`,
          borderRadius: '15px'
        },
        "&.Mui-focused .MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline": {
          border: `3px solid black`,
          borderRadius: '15px'
        },
      }}
      renderInput={(params) => <TextField {...params} 
        label="Niveau scolaire" />}
      noOptionsText="Aucun résultat"
    />
  );
}

export const levels = [
{ label: 'L1'},
{ label: 'L2'},
{ label: 'L3'},
{ label: 'M1'},
{ label: 'M2'},
{ label: 'Doctorat'}]