
import Logo from "../../assets/iapau_logo.png";
import BurgerNotShown from "../../assets/burger_notShown.png";
import BurgerShown from "../../assets/burger_Shown.png";

import { useState, useEffect } from "react";

import MyNavLink from "../MyNavLink";
import { Link, Navigate, useLocation } from "react-router-dom";
import "./header.css";

import '../../style/App.css';
import '../../fonts/Inter-Light.ttf'
import { useAuthContext } from "../../Contexts/AuthContext";
import MyNavButton from "../MyNavButton";

import disconnectPicture from "../../assets/disconnect.png"
import profilPicture from "../../assets/profil.jpg"

/**
 * @name Header
 * @description The header is what you see at the top of your screen when you're in the website
 * @returns The header component
 */
function Header() {

    // to add an animation when we scroll on the page
    const [shown, setShown] = useState((window.innerWidth > 750));
    const [redirection, setRedirection] = useState("");

    let {user, logout, updateTimeConnexion} = useAuthContext();
    if(user){
        updateTimeConnexion();
    }
    let userStorage = localStorage.getItem('user');
    if(userStorage){ 
        user = JSON.parse(userStorage); 
    }

    function toggleShownMenu(){
        setShown(!shown);
    }

    useEffect(() => {
        const changeWidth = () => {
            if(window.innerWidth > 800){
                setShown(true);
            } else {
                setShown(false);
            }
        }

        window.addEventListener('resize', changeWidth);

        return () => {
            window.removeEventListener('resize', changeWidth);
        }
    }, [])

    const location = useLocation();

    return (
        <>
            {   
                redirection && redirection !== "" && location.pathname !== redirection && (
                    <Navigate to={redirection} replace/>
                )
            }
            <nav className="nav">
                { shown && (
                <ul style={{ fontFamily: 'Light', fontSize: '18px'}} className="liste">
                    <Link to="https://iapau.org/">
                        <img src={Logo} alt="Logo d'IA Pau"  className="logo"/>
                    </Link>
                    <li className="items">
                        <MyNavLink to="/"><span style={{fontSize: '18px'}}>Accueil</span></MyNavLink>
                    </li>

                    <li className="items">
                        <MyNavLink to="https://iapau.org/contact/">Contact</MyNavLink>
                    </li>

                    
                    {
                        !user && (
                            <>
                                <li className="itemsConnexion">
                                    <MyNavLink to="/connexion"><span style={{fontFamily: 'Light'}}>Connexion</span></MyNavLink>
                                </li><li className="itemsConnexion">
                                    <MyNavLink to="/inscription"><span style={{fontFamily: 'Light'}}>Inscription</span></MyNavLink>
                                </li>
                                <ul className="rightOfScreenHeaderListe">
                                    <li className="items">
                                        <MyNavLink to="/connexion"><span style={{fontFamily: 'Light', fontSize: '18px'}}>Connexion</span></MyNavLink>
                                    </li>
                                    <li className="items">
                                        <MyNavButton to="/inscription">
                                            <span style={{fontFamily: 'Light', fontSize: '18px'}}>Inscription</span>
                                        </MyNavButton>
                                    </li>
                                </ul>
                            </>
                        )
                    }
                    {
                        user && (
                            <>
                                {/*<li className="itemsConnexion">
                                    <MyNavLink to="/notifications"><span style={{fontFamily: 'Light'}}>Mes notifications</span></MyNavLink>
                                </li>*/}
                                <li className="itemsConnexion">
                                    <MyNavLink to="/dashboard"><span style={{fontFamily: 'Light'}}>Mon espace</span></MyNavLink>
                                </li>
                                <li className="itemsConnexion" onClick={(e) => {
                                        e.preventDefault();
                                        logout();
                                    }}>
                                    <MyNavLink to=""><span style={{fontFamily: 'Light'}}>Déconnexion</span></MyNavLink>
                                </li>
                                <ul className="rightOfScreenHeaderListe">
                                    {/*<li className="items">
                                        <MyNavLink to="/notifications">
                                            <img src={notificationsPicture} alt="Notifications" style={{width: '40px', height: '40px'}} />
                                        </MyNavLink>
                                </li>*/}
                                    <li className="items">
                                    <MyNavLink to="/dashboard">
                                            <img src={profilPicture} alt="Profil" style={{width: '40px', height: '40px'}} />
                                        </MyNavLink>
                                    </li>
                                    <li className="items">
                                        <img onClick={() => {
                                            logout();
                                            setRedirection("/")
                                        }}src={disconnectPicture} alt="Disconnect" style={{width: '40px', height: '40px'}} />
                                    </li>
                                </ul>
                            </>
                        )
                    }
                    
                    <li className="items">
                        <img src = {BurgerShown} id="btnToSwap" alt="togler" className="btn" onClick={(e) => {
                            e.preventDefault();
                            toggleShownMenu();
                        }}/>
                    </li>
                </ul>)}
                { !shown && (
                <div>
                    <Link to="">
                        <img src={Logo} alt="Logo d'IA Pau" className="logoNotShown"/>
                    </Link>
                    
                    <img src = {BurgerNotShown} id="btnToSwap" alt="togler" className="btn" onClick={(e) => {
                            e.preventDefault();
                            toggleShownMenu();
                        }}/>
                    
                </div>
                )}
            </nav>
        </>
    );
}

export default Header;
