import "./DashboardBox.css"

import '../../style/App.css';
import '../../fonts/Inter-Light.ttf'
/**
 * @author Arthur Rimaudière
 * @function DashboardBox
 * @description Le composant des cartes de la page https://<adresse>/dashboard
 * @returns Component
 * @version 1.0.0
 * @example < DashboardBox img={require('../img.png')} alt="altImage" title="titreCarte" lore="Message bas de la carte" />
 * @param {File} img - Image à afficher
 * @param {String} alt - alt prop de l'image à ajouter
 * @param {String} titre - Titre de la carte
 * @param {String} lore - Sous titre de la carte
 */
export default function DashboardBox({img, alt, title, lore}){

    return (
        <>

            <div className="DashboardBoxContainer" style={{fontFamily: 'Light', color: 'white', cursor: 'pointer'}}>
                <ul className="DashboardBoxListe">
                    <li className="DashboardBoxImageContaniner">
                        <img src={img} alt={alt} className="DashboardBoxImage"/>
                    </li>
                    <li>
                        <h2 style={{width: '90%', marginLeft: '5%', fontSize: '25px', textAlign: 'center'}}>{title}</h2>
                        <p style={{width: '80%', marginLeft: '10%', fontSize: '17px'}}>{lore}</p>
                    </li>
                </ul>
            </div>
        </>
    )
}