
import MyNavLink from "../MyNavLink";
import EventBoxKeyWord from "./EventBoxKeyWord";

import "./Eventbox.css"
import { useState } from "react";
import { Navigate } from "react-router-dom";
import getApiUrl from "../../Constantes/apiurl";
import { useAuthContext } from "../../Contexts/AuthContext";
import Swal from "sweetalert2";
import { getMainImage } from "../../Utils/Image";
import dateToString from "../../Utils/DateUtils";
import { throwErrorOnFetch } from "../../Utils/Errors";
import SplitButton from "../SplitButton";

/**
 * @author Arthur Rimaudière
 * @function EventBox
 * @description Le composant d'affichage d'un événement
 * @returns Component
 * @version 1.0.0
 * @example <EventBox Event={Object} index={1} adminMode={false} update={update} setUpdate={setUpdate} />
 * @param {Object} Event - Les données de l'événement à afficher cf ListEventBox/index.js pour voir le format
 * @param {Number} index - Pour voir l'index de l'événement dans la liste d'événement.
 * @param {Boolean} adminMode - Pour afficher le composant de la page d'administration (default: false).
 * @param {Number} update - Variable d'état du composant parent permettant un réaffichage lors d'une modification
 * @param {Function} setUpdate - State changer du composant parent permettant un réaffichage lors d'une modification
 */
function EventBox({Event, index, adminMode, update, setUpdate}){

    
    let {user} = useAuthContext();
    let userStorage = localStorage.getItem('user');
    if(userStorage){ 
        user = JSON.parse(userStorage); 
    }
    const [redirection, setRedirection] = useState("");

    let builtForAdmins = false;
    if(adminMode){
        builtForAdmins = adminMode;
    }

    return builtForAdmins === true ? (
        <>
            {
                redirection && (redirection !== "") && (
                    <Navigate to={redirection} replace />
                )
            }
            <div className="containerEventBox">
                <ul style={{fontFamily: 'Light'}} className="listeEventBox">
                    
                <li key={`picture-${index}`} className="collone1">
                    <div style={{height: '100%', position: 'relative', }}>
                        <img src={Event.image} onError={({ currentTarget }) => {
                            currentTarget.onerror = null;
                            currentTarget.src=getMainImage();
                        }} alt="ImageOfTheEvent" className="EventPicture"/>
                    </div>
                </li>

                    
                    <li key={`center-${index}`} className="collone2">
                        <br/>
                        <h1 style={{fontSize: '22px', marginLeft: '20px'}}>
                            <b>{ "Data " + (Event.type.slice(0)[0]).toUpperCase() + Event.type.slice(1) + " : " + Event.titre}</b>
                        </h1>
                        <br/>
                        <br/>
                        <br/>
                        <br/>
                        <br/>
                        <br/>
                        <br/>
                        <ul style={{display: 'flex', flexDirection: 'row', padding: '0px', justifyContent: 'left', listStyleType: 'none'}}>
                            {
                                (typeof Event.mot !== 'undefined') && (
                                    <>
                                    {
                                        Event.mot.map((value, index2) => {
                                            return (
                                                <li key={`keyword-${index}-${index2}`}style={{listStyleType: 'none', paddingRight: '20px', paddingLeft: '20px'}}>
                                                    <p>{value} </p>
                                                </li>
                                            )
                                        })
                                    }
                                    </>
                                )
                            }
                        </ul>
                        <br/>
                        <SplitButton options={[
                            {
                                name: 'Voir les équipes',
                                fonction: () => {
                                    setRedirection("/events/" + Event.id + "/teams");
                                }
                            },{
                                name: 'Voir l\'évènement',
                                fonction: () => {
                                    setRedirection("/events/" + Event.id);
                                }
                            },{
                                name: 'Modifier l\'évènement',
                                fonction: () => {
                                    setRedirection("/events/" + Event.id + "/edit");
                                }
                            },{
                                name: 'Supprimer l\'évènement',
                                fonction: () => {
                                    Swal.fire({
                                        title: 'Êtes-vous sûr ?',
                                        text: "Cette action est irréversible !",
                                        icon: 'warning',
                                        showCancelButton: true,
                                        confirmButtonColor: '#3085d6',
                                        cancelButtonColor: '#d33',
                                        confirmButtonText: 'Confirmer',
                                        cancelButtonText: 'Annuler'
                                      }).then((result) => {
                                        if (result.isConfirmed) {
                                          async function deleteData(){
                                            try {
                                                const response = await fetch(getApiUrl() + "events/delete/" + Event.id, {
                                                    method: "DELETE",
                                                    headers: {
                                                        Authorization: "Bearer " + user.token,
                                                        "Content-Type": "application/json; charset=utf-8",
                                                    },
                                                });
                                                if(!response.ok){
                                                    Swal.fire({
                                                        icon: 'error',
                                                        title: 'Oops...',
                                                        text: 'La suppression du projet n\'a pas fonctionné',
                                                    })
                                                } else {
                                                    setUpdate(update+100); 
                                                }
                                            } catch(e) {
                                                throwErrorOnFetch(e);
                                            }
                                          }
                                          deleteData();
                                        }
                                      })
                                }
                            }
                        ]} />
                    </li>
                    <li key={`right-${index}`} className="collone3">
                        <p className="texteInfos"> {"Début: " + dateToString(Event.debut)} </p>
                        <p className="texteInfos"> {"Fin: " + dateToString(Event.fin)} </p>
                        <p className="texteInfos"> {"Phase: " + Event.phase} </p>
                        <p className="texteInfos"> <b>{Event.Gains}</b> </p>
                    </li>
                </ul>
            </div>
        </>
    ) : (
        <div className="containerEventBox">
            <ul style={{fontFamily: 'Light'}} className="listeEventBox">
                
                <li key={`picture-${index}`} className="collone1">
                    <div style={{height: '100%', position: 'relative', }}>
                        <img src={Event.image} onError={({ currentTarget }) => {
                            currentTarget.onerror = null;
                            currentTarget.src=getMainImage();
                        }} alt="ImageOfTheEvent" className="EventPicture"/>
                    </div>
                </li>

                
                <li key={`center-${index}`} className="collone2">
                    <br/><br/><br/>
                    <h1 style={{fontSize: '22px', marginLeft: '20px'}}>
                        <b>{ "Data " + (Event.type.slice(0)[0]).toUpperCase() + Event.type.slice(1) + " : " + Event.titre}</b>
                    </h1>
                    <br/>
                    <MyNavLink to={"events/" + Event.id}>
                        <u style={{fontSize: (window.innerWidth < 800) ? '18px' :'22px', marginLeft: '20px'}}>{user ? "Voir l'événement en détails" : "S'inscrire"}</u>
                    </MyNavLink>
                    <br/>
                    <br/>
                    <ul style={{display: 'flex', flexDirection: 'row', justifyContent: 'center'}}>
                        {
                            (typeof Event.mot !== 'undefined') && (
                                <>
                                {
                                    Event.mot.map((value, index2) => {
                                        return (
                                            <li key={`keyword-${index}-${index2}`} style={{listStyleType: 'none', paddingRight: '20px', paddingLeft: '20px'}}>
                                                <EventBoxKeyWord mot={value} />
                                            </li>
                                        )
                                    })
                                }
                                </>
                            )
                        }
                    </ul>
                </li>
                <li key={`right-${index}`} className="collone3">
                    <p className="texteInfos"> {"Début: " + dateToString(Event.debut)} </p>
                    <p className="texteInfos"> {"Fin: " + dateToString(Event.fin)} </p>
                    <p className="texteInfos"> {"Phase: " + Event.phase} </p>
                    <p className="texteInfos"> <b>{Event.Gains}</b> </p>
                </li>
            </ul>
        </div>
    )
}

export default EventBox;