import "./UserList.css"

import '../../style/App.css';
import '../../fonts/Inter-Light.ttf'
import { useState, useEffect } from "react";
import { deleteConfirm, AccountInterraction } from "../../pages/AdminUserConfig/AccountFunctions";
import { useAuthContext } from "../../Contexts/AuthContext";


function reduce(text, size){
    let returnText = text;
    if(text.length >= size){
        returnText = text.substring(0, size);
    }
    return returnText;
}

function reducePageAdmin(text){
    return reduce(text, 25);
}

export default function UserList({datas, change, setChange, ecoles}){

    const [width, setWidth] = useState(window.innerWidth);

    const [typeFiltre, setTypeFiltre] = useState("Nom");
    let filteredDatas = datas;

    function compareDatas(user1, user2){
        let date1 = Date.parse(user1.dateCreation);
        let date2 = Date.parse(user2.dateCreation);
        let defaultReturnValue = 0;
        if(user1.nom === ""){
            return 1;
        }
        if(user2.nom === ""){
            return -1;
        }
        if((date1 - date2) >= 0){
            defaultReturnValue = 1;
        } else {
            defaultReturnValue = -1;
        }
        if(typeFiltre === "Nom"){
            let val1 = user1.nom.toUpperCase();
            let val2 = user2.nom.toUpperCase();
            if(val1 < val2){
                return -1;
            } else if (val1 === val2){
                return defaultReturnValue;
            } else {
                return 1;
            }
        } else if(typeFiltre === "Prenom"){
            let val1 = user1.prenom.toUpperCase();
            let val2 = user2.prenom.toUpperCase();
            if(val1 < val2){
                return -1;
            } else if (val1 === val2){
                return defaultReturnValue;
            } else {
                return 1;
            }
        } else if(typeFiltre === "Pseudo"){
            let val1 = user1.pseudo.toUpperCase();
            let val2 = user2.pseudo.toUpperCase();
            if(val1 < val2){
                return -1;
            } else if (val1 === val2){
                return defaultReturnValue;
            } else {
                return 1;
            }
        } else if(typeFiltre === "Mail"){
            let val1 = user1.mail.toUpperCase();
            let val2 = user2.mail.toUpperCase();
            if(val1 < val2){
                return -1;
            } else if (val1 === val2){
                return defaultReturnValue;
            } else {
                return 1;
            }
        } else if(typeFiltre === "Role"){
            let val1 = user1.role.toUpperCase();
            let val2 = user2.role.toUpperCase();
            if(val1 < val2){
                return -1;
            } else if (val1 === val2){
                return defaultReturnValue;
            } else {
                return 1;
            }
        }
        return 0;
    }

    useEffect(() => {
        const changeWidth = () => {
            setWidth(window.innerWidth);
        }

        window.addEventListener('resize', changeWidth);

        return () => {
            window.removeEventListener('resize', changeWidth);
        }
    }, [])

    filteredDatas.sort(compareDatas);

    const {user} = useAuthContext();

    let widthAction = (width > 1400) ? "13%" : "50%";

    return (
        <div className="GreenUserListContainer" style={{fontFamily: 'Light'}}>
            <div className="WhiteUserListContainer">
                <ul className="ListeUserList">
                    <li className="borderBelowUserListBold">
                        <ul style={{display: 'flex', flexDirection: 'row', listStyleType: 'none', padding: '0'}}>
                            {
                                width > 1400 ? (
                                    <>
                                    <li style={{width: '14%', height: '50px', display: 'flex', alignItems: 'center', justifyContent: 'center', cursor: 'pointer'}} onClick={(e) => {
                                        e.preventDefault();
                                        setTypeFiltre("Nom");
                                    }}>
                                        <b>Nom</b>
                                    </li>
                                    <li style={{width: '14%', height: '50px', display: 'flex', alignItems: 'center', justifyContent: 'center', cursor: 'pointer'}} onClick={(e) => {
                                        e.preventDefault();
                                        setTypeFiltre("Prenom");
                                    }}>
                                        <b>Prénom</b>
                                    </li>
                                    </>
                                ) : ( null)
                            }
                            
                            <li className="CollonePseudoAdmin" onClick={(e) => {
                                e.preventDefault();
                                setTypeFiltre("Pseudo");
                            }}>
                                <b>Pseudo</b>
                            </li>
                            {
                                width > 1400 ? (
                                    <>
                                        <li style={{width: '30%', height: '50px', display: 'flex', alignItems: 'center', justifyContent: 'center', cursor: 'pointer'}} onClick={(e) => {
                                            e.preventDefault();
                                            setTypeFiltre("Mail");
                                        }}>
                                            <b>E-mail</b>
                                        </li>
                                        <li style={{width: '15%', height: '50px', display: 'flex', alignItems: 'center', justifyContent: 'center', cursor: 'pointer'}} onClick={(e) => {
                                            e.preventDefault();
                                            setTypeFiltre("Role");
                                        }}>
                                            <b>Rôle</b>
                                        </li>
                                    </>
                                ) : ( null )
                            }
                            
                            <li style={{width: `${widthAction}`, height: '50px', display: 'flex', alignItems: 'center', justifyContent: 'center'}}>
                                <b>Actions</b>
                            </li>
                        </ul>
                    </li>
                    {
                        filteredDatas.map((user2, index) => {
                            return (
                                <li key={`user-${index}`} className="borderBelowUserList">
                                    <ul style={{display: 'flex', flexDirection: 'row', listStyleType: 'none', padding: '0'}}>
                                        {
                                            width > 1400 ? (
                                                <>
                                                    <li style={{width: '14%', height: '50px', display: 'flex', alignItems: 'center', justifyContent: 'center'}}>
                                                        {reducePageAdmin(user2.nom)}
                                                    </li>
                                                    <li style={{width: '14%', height: '50px', display: 'flex', alignItems: 'center', justifyContent: 'center'}}>
                                                        {reducePageAdmin(user2.prenom)}
                                                    </li>
                                                </>
                                            ) : null
                                        }
                                        
                                        <li className="CollonePseudoAdmin" style={{height: '50px', display: 'flex', alignItems: 'center', justifyContent: 'center'}}>
                                            {reducePageAdmin(user2.pseudo)}
                                        </li>
                                        {
                                            width > 1400 ? (
                                                <>
                                                    <li style={{width: '30%', height: '50px', display: 'flex', alignItems: 'center', justifyContent: 'center'}}>
                                                        {user2.mail}
                                                    </li>
                                                    <li style={{width: '15%', height: '50px', display: 'flex', alignItems: 'center', justifyContent: 'center'}}>
                                                        {user2.role === "etudiant" && (
                                                            <>
                                                                Etudiant
                                                            </>
                                                        )}
                                                        {user2.role === "administrateur" && (
                                                            <>
                                                                Administrateur
                                                            </>
                                                        )}
                                                        {user2.role === "gestionnaireExterne" && (
                                                            <>
                                                                Gestionnaire Externe
                                                            </>
                                                        )}
                                                        {user2.role === "gestionnaireIA" && (
                                                            <>
                                                                Gestionnaire IA-Pau
                                                            </>
                                                        )}
                                                    </li>
                                                </>
                                            ) : null
                                        }
                                        
                                        <li style={{width: `${widthAction}`, height: '50px', display: 'flex', alignItems: 'center', justifyContent: 'center', flexDirection: 'row'}}>
                                            {
                                                (user2.nom !== "") ? (
                                                    <>
                                                        <button style={{marginRight: '8px', backgroundColor: '#85ebd2', fontSize: '20px', border: '1px solid #85ebd2', borderRadius: '5px', cursor: 'pointer', padding: '5px 10px 5px 10px'}} onClick={(e) => {
                                                            e.preventDefault();
                                                            AccountInterraction(user2.role, user.token, user2, change, setChange, ecoles);
                                                        }}>
                                                            Modifier
                                                        </button>
                                                        <button style={{marginLeft: '8px', backgroundColor: '#ea4d45', fontSize: '20px', border: '1px solid #ea4d45', borderRadius: '5px', cursor: 'pointer'}} onClick={(e) => {
                                                            e.preventDefault();
                                                            deleteConfirm(user2, user.token, change, setChange);
                                                        }}>
                                                            X
                                                        </button>
                                                    </>
                                                ) : (
                                                    null
                                                )
                                            }
                                        </li>
                                    </ul>
                                </li>
                            )
                        })
                    }
                </ul>
            </div>
        </div>
    )
}