
import { useState, useEffect } from "react";
import EventBox from "../EventBox";

import '../../style/App.css';
import '../../fonts/Inter-Light.ttf'
import getApiUrl from "../../Constantes/apiurl";
import { throwErrorOnFetch } from "../../Utils/Errors";
import Loading from "../LoadingComponent";

/**
 * @name ListEventBox
 * @description Create the box that contains the list of all events
 * @returns The list event box component : All the events you see in the main page
 */
function ListEventBox({filtre, pageAdmin, update, setUpdate}){

    let adminMode = false;
    if(pageAdmin){
        adminMode = pageAdmin;
    }

    let filtreToApply = "";
    if(filtre){
        filtreToApply = filtre;
    }

    const [oldEvents, setOldEvents] = useState([]);
    const [actualEvents, setActualEvents] = useState([]);
    const [ListeUpdate, setListeUpdate] = useState(0);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        async function fetchData(){
            try {
                const response = await fetch(getApiUrl()+ "voir_tous_events", {
                    method: "GET",
                    headers: {
                        "Content-Type": "application/json; charset=utf-8",
                    }
                })
                const json = await response.json();
                setLoading(false);
                if(response.ok) {
                    setOldEvents((json.oldEvents !== null) ? json.oldEvents : []);
                    setActualEvents((json.actualEvent !== null) ? json.actualEvent : []);
                }
            } catch(e) {
                throwErrorOnFetch(e);
            }
        }

        fetchData();
        
    }, [ListeUpdate]);

    let printed = 1;
    const [maxPrinted, setMaxPrinted] = useState(3);

    let oldEventsFiltered = [];
    if(typeof oldEvents !== "undefined"){
        oldEvents.forEach((event) => {
            if(event.titre.toUpperCase().includes(filtreToApply.toUpperCase())){
                oldEventsFiltered.push(event);
            }
        })
    }
    oldEventsFiltered.sort((a, b) => {
        if(a.debut > b.debut){
            return -1;
        } else {
            return 1;
        }
    })

    let newEventsFiltered = [];
    if(typeof actualEvents !== "undefined"){
        actualEvents.forEach((event) => {
            if(event.titre.toUpperCase().includes(filtreToApply.toUpperCase())){
                newEventsFiltered.push(event);
            }
        })
    }

    newEventsFiltered.sort((a, b) => {
        if(a.debut > b.debut){
            return -1;
        } else {
            return 1;
        }
    })

    return loading === true ? <div style={{marginTop: '40%'}}><Loading/></div> : (
        
        <div style={{
            width: '80%',
            marginLeft: 'auto',
            marginRight: 'auto',
            fontFamily: 'Light',
            marginTop: (window.innerWidth  < 800) ? '25px' : '0px'
        }}>
            <br/><br/><br/>
            
            <h2 style={{fontFamily: 'Light'}}>Évènements en cours ({newEventsFiltered.length})</h2>
            {(newEventsFiltered.length > 0) && newEventsFiltered.map((Event, index) => {
                return (
                    <div key={`actualbox-${index}`}>
                        <EventBox Event={Event} adminMode={adminMode} update={ListeUpdate} setUpdate={setListeUpdate}/>
                    </div>
                )
            })}
            {(newEventsFiltered.length === 0) && (
                <>
                    Il n'y a pas d'événement pour le moment.
            <br/><br/>
                </>
            )}
            <br/><br/>
            <h2>Anciens évènements ({oldEventsFiltered.length})</h2>
            <br/><br/>
            {(oldEventsFiltered.length === 0) && (
                <>
                    Il n'y a pas d'ancien événement pour le moment.
            <br/><br/> 
                </>
            )}
            {oldEventsFiltered.map((Event, index) => {
                if(index < maxPrinted){
                    printed++;
                }
                return ( (index < maxPrinted) ? 
                    (
                        <div key={`box-${index}`}>
                            <EventBox Event={Event} index={index} adminMode={adminMode} update={ListeUpdate} setUpdate={setListeUpdate}/>
                        </div>
                    ) : ( null )
                )
            })}
            <br/><br/><br/>
            {
                (printed <= oldEventsFiltered.length) ? (
                    <div style={{display: 'flex', justifyContent: 'center'}}>
                        <button onClick={() => {
                            setMaxPrinted(maxPrinted+3)
                        }} style={{
                            backgroundColor: '#00897B',
                            height: '50px',
                            width: '300px',
                            border: '1px solid #00897B',
                            borderRadius: '26px',
                            boxShadow: '2px 1px 1px black',
                            cursor: 'pointer',
                            color: 'white',
                            fontSize: '20px',
                        }}>
                            <span>Voir plus de résultats</span>
                        </button>
                        <br/><br/><br/><br/><br/>
                    </div>
                ) : ( 
                    <>
                        {
                            (oldEventsFiltered.length > 0) ? (
                                <div style={{display: 'flex', justifyContent: 'center', fontSize: '20px'}}>
                                    Aucun résultat supplémentaire
                                    <br/><br/>
                                </div>
                            )  :  null
                        }
                    </>
                )
            }
        </div>
    )
}

export default ListEventBox;