import { Navigate } from "react-router-dom";
import { useAuthContext } from "../../Contexts/AuthContext"
import PageEspacePerso from "../PageEspacePersoType";
import bannerPau from "../../assets/photoFont1.jpg";

import { useEffect, useState } from "react";
import SearchBar from "../../components/SearchBar";
import UserList from "../../components/UserList";

import { CreateAccount } from "./AccountFunctions";
import "./Affichage.css";
import getApiUrl from "../../Constantes/apiurl";
import { visitUrl } from "../../Contexts/ReturnManager";
import { throwErrorOnFetch } from "../../Utils/Errors";
import SplitButton from "../../components/SplitButton";
import Loading from "../../components/LoadingComponent";

export default function AdminUserConfig(){
    let {user} = useAuthContext();
    let userStorage = localStorage.getItem('user');
    if(userStorage){ 
        user = JSON.parse(userStorage); 
    }

    visitUrl(window.location.pathname);

    let doesNotHavePermission = !user || user.role !== "administrateur";
    let hasPermission = !doesNotHavePermission;

    const [filter, setFilter] = useState("");
    
    const [datas, setDatas] = useState([]);

    const [change, setChange] = useState(0);

    const [ecoles, setEcoles] = useState([]);

    const [loading, setLoading] = useState(true);

    async function retrieveAllUsers(user){
        if(!user) { return; }
        try {
            const response = await fetch(getApiUrl() + "users/", {
                method: "GET",
                headers: {
                    Authorization: "Bearer " + user.token,
                    "Content-Type": "application/json; charset=utf-8",
                }
            });
            setLoading(false);
            const json = await response.json();
            if(response.ok){
                setDatas(json.utilisateurs);
            }
        } catch(e) {
            throwErrorOnFetch(e);
        }
    }

    useEffect(() => {
        retrieveAllUsers(user);
        async function fetchDatasEcoles(){
            try {
                const response = await fetch("https://data.enseignementsup-recherche.gouv.fr/api/records/1.0/search/?dataset=fr-esr-principaux-etablissements-enseignement-superieur&q=&rows=250&sort=uo_lib")
                const json = await response.json();
                if(response.ok){
                let newDatas = [];
                json.records.map((ecoleBDD) => {
                    let nom = "" + ecoleBDD.fields.uo_lib;
                    nom = nom.toString();
                    if(typeof nom !== "undefined" && nom !== "undefined"){
                    newDatas.push({label: nom});
                    if(nom.startsWith("CY")){
                        newDatas.push({label: 'CY-Tech'});
                    }
                    }
                    return 1;
                })
                setEcoles(newDatas);
                }
            } catch(e) {
                throwErrorOnFetch(e);
            }
          }
        fetchDatasEcoles();
    // eslint-disable-next-line
    }, [change])

    let filteredDatas = [];
    for(let i = 0; i < datas.length; i++){
        let matchable = datas[i].nom + " " + datas[i].prenom + " " + datas[i].pseudo + " " + datas[i].mail;
        matchable = matchable.toUpperCase();
        if(matchable.match(filter.toUpperCase())){
            filteredDatas.push(datas[i]);
        }
    }

    while(filteredDatas.length < 10){
        let voidUser = {
            nom: "",
            prenom: "",
            pseudo: "",
            role: "",
            mail: "",
            dateCreation: ""
        };
        filteredDatas.push(voidUser);
    }

    return (
        <>
            {
                doesNotHavePermission && (
                    <Navigate to="/" replace/>
                )
            }
            {
                hasPermission && (
                    <PageEspacePerso>
                        <img src={bannerPau} className="BannerAdminUser" alt=""/>
                        <h1 style={{fontFamily: 'Light', color: 'white', position: 'absolute',top: '80px', fontSize: '55px', textAlign: 'center', width: '96%', textShadow: '0 1px 0 black, 0 -1px 0 black'}}>
                            Gestion des utilisateurs
                        </h1>
                        <SplitButton/>
                        <ul className="ListeDeBouttonAdminUser">
                            <li>
                                <SearchBar changeFiltre={setFilter}/>
                            </li>
                            <li style={{marginLeft: '25px', marginTop: '10px'}}>
                                <SplitButton options={[
                                    {
                                        name: 'Création d\'un compte étudiant',
                                        fonction: () => {
                                            CreateAccount("etudiant", user.token, change, setChange, ecoles);
                                        }
                                    },{
                                        name: 'Création d\' un gestionnaire externe',
                                        fonction: () => {
                                            CreateAccount("gestExt", user.token, change, setChange);
                                        }
                                    },{
                                        name: 'Création d\'un gestionnaire IA-PAU',
                                        fonction: () => {
                                            CreateAccount("gestIA", user.token, change, setChange);
                                        }
                                    },{
                                        name: 'Création d\'un administrateur',
                                        fonction: () => {
                                            CreateAccount("admin", user.token, change, setChange);
                                        }
                                    }
                                ]} />
                            </li>
                        </ul>
                        {
                            loading === true && (
                                <Loading/>
                            )
                        }
                        <UserList datas={filteredDatas} change={change} setChange={setChange} ecoles={ecoles}/>
                    </PageEspacePerso>
                )
            }
        </>
    )
}