import * as React from 'react';
import TextField from '@mui/material/TextField';
import Autocomplete from '@mui/material/Autocomplete';
import { useState } from 'react';
import { useEffect } from 'react';
import { throwErrorOnFetch } from '../../../Utils/Errors';

/**
 * @author Association IA-Pau 
 * @function SchoolSelector
 * @description Un boutton pour choisir son école
 * @returns Component
 * @version 1.0.0
 * @example <SchoolSelector color="#707776" />
 * @param {String} color - couleur de la balise
 */
export default function SchoolSelector({color}) {
  let finalColor = "#707776";

  if(color){
    finalColor = color;
  }

  const [ecolesPresentes, setEcolesPresentes] = useState(ecoles);

  useEffect(() => {
    async function fetchDatasEcoles(){
      try {
        const response = await fetch("https://data.enseignementsup-recherche.gouv.fr/api/records/1.0/search/?dataset=fr-esr-principaux-etablissements-enseignement-superieur&q=&rows=250&sort=uo_lib")
        const json = await response.json();
        if(response.ok){
          let newDatas = [];
          json.records.map((ecoleBDD) => {
            let nom = "" + ecoleBDD.fields.uo_lib;
            nom = nom.toString();
            if(typeof nom !== "undefined" && nom !== "undefined"){
              newDatas.push({label: nom});
              if(nom.startsWith("CY")){
                newDatas.push({label: 'CY-Tech'});
              }
            }
            return 1;
          })
          newDatas.push({label: 'Autre'});
          setEcolesPresentes(newDatas);
        }
      } catch(e) {
        throwErrorOnFetch(e);
      }
    }
    fetchDatasEcoles();
  }, [])

  return (
    <Autocomplete
      id="combo-box-school"
      options={ecolesPresentes}
      sx={{
        width: `100%`,
        height: '60px',
        boxSizing: 'border-box',
        "& .MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline": {
          border: `3px solid ${finalColor}`,
          borderRadius: '15px'
        },
        "&:hover .MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline": {
          border: `3px solid ${finalColor}`,
          borderRadius: '15px'
        },
        "&.Mui-focused .MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline": {
          border: `3px solid black`,
          borderRadius: '15px'
        },
      }}
      renderInput={(params) => <TextField {...params} label="Ecole d'origine" />}
      disablePortal
      noOptionsText="Aucun résultat"
    />
  );
}

export const ecoles = []